import { isNumber } from "./helper"
import { bisection } from "./Math"

/**
 * 一次以上多项式类
 */
export default class Polynomial {
    //
    constructor() {
        if (arguments.length == 1) {
            const a = arguments[0]
            if (Array.isArray(a)) {
                this.coeffs = a
            }
            else if (isNumber(a)) {
                this.coeffs = [a]
            }
        }
        else {
            this.coeffs = Array.from(arguments)
        }
    }
    // 加法
    add(a) {
        if (a instanceof Polynomial) {
            if (this.coeffs.length !== 0) {
                if (a.coeffs.length !== 0) {
                    const result = []
                    const length = Math.max(this.coeffs.length, a.coeffs.length)
                    for (let i = 0; i < length; i++) {
                        result.push(0)
                    }
                    this.coeffs.forEach((e, i, arr) => {
                        result[i + length - arr.length] += e
                    })
                    a.coeffs.forEach((e, i, arr) => {
                        result[i + length - arr.length] += e
                    })
                    return new Polynomial(...result)
                } else {
                    return this
                }
            } else {
                return a
            }
        }
        else if (isNumber(a)) {
            return this.add(new Polynomial(a))
        }
    }
    // 减法
    sub(a) {
        if (a instanceof Polynomial) {
            return this.add(a.mul(-1))
        }
        else if (isNumber(a)) {
            return this.sub(new Polynomial(a))
        }
    }
    // 乘法
    multiple(a) {
        if (a instanceof Polynomial) {
            if (this.coeffs.length !== 0) {
                if (a.coeffs.length !== 0) {
                    const result = []
                    for (let i = 0; i < this.coeffs.length + a.coeffs.length - 1; i++) {
                        result.push(0)
                    }
                    this.coeffs.forEach((e, i) => {
                        a.coeffs.forEach((f, j) => {
                            result[i + j] += e * f
                        })
                    })
                    return new Polynomial(...result)
                } else {
                    return this
                }
            } else {
                return a
            }
        }
        else if (isNumber(a)) {
            return this.multiple(new Polynomial(a))
        }
    }
    mul(a) {
        return this.multiple(a)
    }
    // 幂
    pow(n = 1) {
        let s = new Polynomial(1)
        for (let i = 0; i < n; i++) {
            s = s.mul(this)
        }
        return s
    }
    // 求值
    valueOf(n) {
        if (n != undefined) {
            return this.coeffs.reduce((s, e, i, a) => s += e * n ** (a.length - 1 - i), 0)
        } else {
            console.error('[WARNING]: neccessary param is missing while calling function "valueOf"')
        }
    }
    // 指定范围，指定精确度的全部近似解(基于二分法)
    roots(min = 0, max = 1, fixed = 8) {
        const der = [this.coeffs]
        for (let i = this.coeffs.length; i > 2; i--) {
            const r = []
            for (let j = der.at(-1).length - 1; j > 0; j--) {
                const index = der.at(-1).length - 1 - j
                const c = j * der.at(-1)[index]
                r.push(c)
            }
            der.push(r)
        }
        der.reverse()
        let roots = []
        for (let k = 0; k < der.length; k++) {
            const temp = []
            const extreme = [min, max]
            const p = new Polynomial(...der[k])
            if (roots.length != 0) {
                extreme.push(...roots.at(-1))
            }
            const sorted = extreme
                .filter((e, i, a) => a.indexOf(e) == i)
                .filter(e => e >= min && e <= max)
                .filter(e => {
                    const v = p.valueOf(e)
                    if (v == 0) {
                        temp.push(e)
                    }
                    return v != 0
                })
                .toSorted((a, b) => a - b)

            sorted.forEach((e, i, a) => {
                const prev = a[i - 1]
                if (prev != undefined) {
                    const r = bisection(t => p.valueOf(t), prev, e, 1 / (10 ** fixed))
                    if (r != undefined) {
                        temp.push(r)
                    }
                }
            })
            roots.push(temp)


        }
        return roots.at(-1).map(e => Number(e.toFixed(fixed)))
    }

}