import util from './util'
import shader from './shader/common.wgsl?raw'
import MipmapGenerator from './MipmapGenerator'
// 公共类，含有一些会用到的公共资源
// uniformBuffer    统一缓冲区
// instanceBuffer   模型缓冲区
// bindGroupLayout  资源布局，或者资源绑定方式
// bindGroup        绑定到每个槽位上的资源
// shader           着色器代码
export default class Common {
    constructor({
        device,
        scene,
        canvas,
        mouse
    } = {}) {
        this.device = device
        this.scene = scene
        this.canvas = canvas
        this.mouse = mouse
    }
    async init() {

        // 1.创建统一缓冲区，一般存储场景相关信息
        this.uniformBuffer = this.device.createBuffer({
            size: (
                4 + // light view projection    [mat4]
                4 + // camera view projection   [mat4]
                1 + // center                   [vec4]
                1 + // light                    [vec4]
                1 + // camera                   [vec4]
                1 + // resolution               [vec4]
                1 + // mouse                    [vec4]
                1 + // timestamp                [vec4]
                1 + // date                     [vec4]
                1 + // fovy                     [vec4]
                1   // seed                     [vec4]
            ) * 4 * 4,
            usage:
                GPUBufferUsage.UNIFORM |
                GPUBufferUsage.COPY_DST, //可以向缓冲区写入数据writeBuffer，
        })

        // 2.创建模型缓冲区，一般存储模型的变换信息和颜色信息
        this.instanceBuffer = this.device.createBuffer({
            label: 'instance',
            size: (
                4 + // tranform matrix          [mat4]
                1   // color rgba 颜色          [vec4]
            ) * 4 * 4 * this.scene.instances.length,
            usage:
                GPUBufferUsage.STORAGE |
                GPUBufferUsage.COPY_DST, //可以向缓冲区写入数据writeBuffer，
        })

        /**
         * read-only-storage    类型只能用于ShaderStage::(Vertex|Fragment), 
         * storage              类型只能用于ShaderStage::(Fragment|Compute)
         */
        const bindGroupLayoutEntries = [
            {
                binding: 0,
                buffer: { type: 'uniform' },
                visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT | GPUShaderStage.COMPUTE
            },
            {
                binding: 1,
                buffer: { type: 'read-only-storage' },
                visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT | GPUShaderStage.COMPUTE
            },
            {
                binding: 2,
                buffer: { type: 'read-only-storage' },
                visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT | GPUShaderStage.COMPUTE
            },
        ]
        const bindGroupEntries = [
            { binding: 0, resource: { buffer: this.uniformBuffer } },
            { binding: 1, resource: { buffer: this.instanceBuffer } },
            { binding: 2, resource: { buffer: this.scene.triangleBuffer } },
        ]
        let bindings =
            `\n@group(0) @binding(0) var<uniform> scene: Scene;` +
            `\n@group(0) @binding(1) var<storage> instances: array<Instance>;` +
            `\n@group(0) @binding(2) var<storage> triangles: array<Triangle>;`

        // const urls = [
        //     `/src/assets/ibl/brdf.png`,
        //     `/src/assets/ibl/02.jpg`,
        //     `/src/assets/ibl/03.png`,
        //     [
        //         `/src/assets/ibl/cubemap/01.jpg`,
        //         `/src/assets/ibl/cubemap/02.jpg`,
        //         `/src/assets/ibl/cubemap/03.jpg`,
        //         `/src/assets/ibl/cubemap/04.jpg`,
        //         `/src/assets/ibl/cubemap/05.jpg`,
        //         `/src/assets/ibl/cubemap/06.jpg`
        //     ],
        //     `/src/assets/ibl/04.png`,
        //     `/src/assets/ibl/05.jpg`,
        // ]
        // this.textures = await Promise.all(urls.map(url => this.createBitmapTexture({ url })))
        // this.textures.forEach((texture, i) => {
        //     bindGroupLayoutEntries.push(
        //         {
        //             binding: i + 3,
        //             visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT | GPUShaderStage.COMPUTE,
        //             texture: {
        //                 sampleType: 'float',
        //                 viewDimension: texture.depthOrArrayLayers === 6 ? 'cube' : '2d'
        //             }
        //         }
        //     )
        //     bindGroupEntries.push(
        //         {
        //             binding: i + bindGroupLayoutEntries.length,
        //             visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT | GPUShaderStage.COMPUTE,
        //             resource: texture.createView({ dimension: texture.depthOrArrayLayers === 6 ? 'cube' : '2d' })
        //         }
        //     )
        //     bindings += `\n@group(0) @binding(${i + bindGroupLayoutEntries.length}) var texture_${i}: ${texture.depthOrArrayLayers === 6 ? 'texture_cube<f32>' : 'texture_2d<f32>'};`
        // })

        // 3.创建资源布局
        // 在WebGPU中，bindGroupLayout 定义了一系列的资源槽（slots），每个槽指定了绑定到这个槽位上的资源类型（比如，uniform buffer, storage buffer, sampler, 或者 texture view）。当定义了bindGroupLayout后，你可以创建一个与之匹配的 bindGroup，该 bindGroup 具体指定了将会绑定到每个槽位上的资源。
        this.bindGroupLayout = this.device.createBindGroupLayout({
            label: 'common',
            entries: bindGroupLayoutEntries
        })
        this.bindGroup = this.device.createBindGroup({
            label: 'common',
            layout: this.bindGroupLayout,
            entries: bindGroupEntries
        })
        this.shader = bindings + shader
    }
    update(timestamp) {
        const offset = this.instanceBuffer.size / this.scene.instances.length / 4  // ---- 个数：4 * 4 + 4
        this.device.queue.writeBuffer(this.instanceBuffer, 0, this.scene.instances.reduce((s, e, i) => {
            // ---- set 是 typedArray：Float32Array 的方法
            s.set(util.getModelTransformMatrix(e), i * offset)                  // ---- 写入变换矩阵
            s.set(Object.values(util.colorFormat(e.color)), i * offset + 4 * 4) // ---- 写入颜色
            return s
        }, new Float32Array(this.instanceBuffer.size / 4)))
        this.device.queue.writeBuffer(this.uniformBuffer, 0, util.getLightViewProjectionMatrix({
            light: this.scene.light,
            center: this.scene.center,
            up: this.scene.up,
            left: -10,
            right: 10,
            bottom: -10,
            top: 10,
            near: 0,
            far: 300,
        }))
        this.device.queue.writeBuffer(this.uniformBuffer, 16 * 4, util.getCameraViewProjectionMatrix({
            aspect: this.canvas.width / this.canvas.height,
            eye: this.scene.eye,
            center: this.scene.center,
            up: this.scene.up,
            fovy: this.scene.fovy,
            near: 0.1,
            far: 100000.0,
        }))
        this.device.queue.writeBuffer(this.uniformBuffer, 32 * 4, new Float32Array(this.scene.center))
        this.device.queue.writeBuffer(this.uniformBuffer, 36 * 4, new Float32Array(this.scene.light))
        this.device.queue.writeBuffer(this.uniformBuffer, 40 * 4, new Float32Array(this.scene.eye))
        this.device.queue.writeBuffer(this.uniformBuffer, 44 * 4, new Float32Array([this.canvas.width, this.canvas.height]))
        this.device.queue.writeBuffer(this.uniformBuffer, 48 * 4, new Float32Array([0, 0, 0, 0]))
        this.device.queue.writeBuffer(this.uniformBuffer, 52 * 4, new Float32Array([timestamp / 1000]))
        this.device.queue.writeBuffer(this.uniformBuffer, 56 * 4, new Float32Array([
            new Date().getFullYear(),
            new Date().getMonth() + 1,
            new Date().getDate(),
            new Date().getHours() * 3600 + new Date().getMinutes() * 60 + new Date().getSeconds()
        ]))
        this.device.queue.writeBuffer(this.uniformBuffer, 60 * 4, new Float32Array([this.scene.fovy]))
        const seed = [
            0xffffffff * Math.random(),
            0xffffffff * Math.random(),
            0xffffffff * Math.random(),
        ]
        this.device.queue.writeBuffer(this.uniformBuffer, 64 * 4, new Uint32Array(seed))
    }
    async createBitmapTexture({
        url,
        label = '',
        format = 'rgba8unorm',
        sampleCount = 1,
        usage =
        GPUTextureUsage.TEXTURE_BINDING |
        GPUTextureUsage.COPY_DST |
        GPUTextureUsage.RENDER_ATTACHMENT
    } = {}) {
        const bitmap = Array.isArray(url) ? await Promise.all(url.map(e => this.getBitmap(e))) : await this.getBitmap(url),
            width = Array.isArray(url) ? bitmap[0].width : bitmap.width,
            height = Array.isArray(url) ? bitmap[0].height : bitmap.height,
            size = [width, height, Array.isArray(url) ? bitmap.length : 1],
            texture = this.device.createTexture({
                label,
                size,
                format,
                mipLevelCount: Math.floor(Math.log2(width)) + 1, // TODO: 宽高不等的话取大还是取小？
                sampleCount,
                usage
            }),
            bitmaps = [bitmap].flat()
        bitmaps.forEach((e, j) => {
            this.device.queue.copyExternalImageToTexture(
                { source: e },
                { texture: texture, origin: [0, 0, j] },
                [e.width, e.height]
            )
        })
        const mipmapGenerator = new MipmapGenerator(this.device)
        return mipmapGenerator.generateMipmap(texture)
    }
    async getBitmap(url) {
        let res = await fetch(url),
            blob = await res.blob(),
            bitmap = await createImageBitmap(blob)
        //console.log(res, blob, bitmap)
        return bitmap
    }
}