import { createID, createUUID, splitTypedArray } from "./helper"
import { LineSeg, QuadraticBezier, CubicBezier, Line, Ray, Curve, Point, splitEachOther } from './Curve'
import { radians, normalDegrees, degrees, mix } from "./Math"
import * as vec2 from './vector2.js'
import { Bezier } from "bezier-js"
import { map } from "lodash"

const log = true

export class SVG {

    constructor(data) {
        this.data = data
        if (log) console.log('%c---- ---- ---- ---- ---- new SVG ---- ---- ---- ---- ----', 'color:orange')
        // 1.解析: 线条绘制命令
        this.paths = this.parse(); if (log) console.log('%c1. 绘制命令: ', 'color:orange', this.paths)
        // 2.绘制线条
        this.curveSets = this.paths.map(e => {
            const id = createID()
            const curves = getCurves(e)
            curves.forEach(e => e.pathId = [id])
            return curves
        })
        if (log) console.log('%c2. 绘制线条 :', 'color:orange', this.curveSets.map(e => e.map(e => e.points.map(e => e.valueOf()))))
    }

    parse() {
        const result = []
        // 1. path element
        {
            const matches = this.data.matchAll(/ d="([^"]+)"/g)
            // if (log) console.log(Array.from(matches))
            matches.forEach(match => {
                if (Array.isArray(match) && match.length >= 2) {
                    const pathData = match[1]
                    const list = []
                    let type = null
                    let points = []

                    // const parts = pathData.match(/[a-df-z]|[\-+]?(?:[\d\.]e[\-+]?|[^\s\-+,a-z])+/ig)
                    const parts = pathData.match(/[a-df-z]|[\-+]?\d*\.?\d+(?:e[\-+]?\d+)?/ig)
                    if (!parts) return list

                    parts.forEach(part => {
                        if (part.match(/[a-df-z]/i)) {
                            if (type !== null) {
                                list.push({ type, points })
                            }
                            type = part
                            points = []
                        } else {
                            points.push(parseFloat(part))
                        }
                    });

                    if (type !== null) {
                        list.push({ type, points })
                    }

                    result.push(list.map(e => {
                        const { type } = e
                        if (type == 'H' || type == 'h') {
                            points = [new Point(e.points[0], 0)]
                        } else if (type == 'V' || type == 'v') {
                            points = [new Point(0, e.points[0])]
                        } else if (type == 'Z' || type == 'z') {
                            points = []
                        } else {
                            points = splitTypedArray(e.points, 2).map(([x, y]) => new Point(x, y))
                        }
                        return { type, points }
                    }))
                }
            })
        }
        // 2. rect
        {
            const matches = this.data.matchAll(/<rect\b[^>]*>/g)
            // if (log) console.log(Array.from(matches))
            matches.forEach(e => {
                const rect = e[0]
                // if (log) console.log('rect:', rect)
                const x = Number(rect.match(/ x="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('x:', x)
                const y = Number(rect.match(/ y="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('y:', y)
                const w = Number(rect.match(/ width="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('w:', w)
                const h = Number(rect.match(/ height="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('h:', h)
                result.push([
                    { type: 'M', points: [new Point(x, y)] },
                    { type: 'h', points: [new Point(w, 0)] },
                    { type: 'v', points: [new Point(0, h)] },
                    { type: 'h', points: [new Point(-w, 0)] },
                    { type: 'v', points: [new Point(0, -h)] },
                    { type: 'z', points: [] },
                ])
            })
        }
        // 3. circle
        {
            const matches = this.data.matchAll(/<circle\b[^>]*>/g)
            // if (log) console.log(Array.from(matches))
            matches.forEach(e => {
                const circle = e[0]
                // if (log) console.log('circle:', circle)
                const cx = Number(circle.match(/ cx="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('cx:', cx)
                const cy = Number(circle.match(/ cy="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('cy:', cy)
                const r = Number(circle.match(/ r="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('r:', r)
                const rx = r
                const ry = r
                const k = (Math.sqrt(2) - 1) * 4 / 3 // 贝塞尔与圆弧接近而选定的经验值
                result.push([
                    { type: 'M', points: [new Point(cx, cy - ry)] },
                    { type: 'c', points: [new Point(rx * k, 0), new Point(rx, ry * (1 - k)), new Point(rx, ry)] },
                    { type: 's', points: [new Point(rx * (k - 1), ry), new Point(-rx, ry)] },
                    { type: 's', points: [new Point(-rx, ry * (k - 1)), new Point(-rx, -ry)] },
                    { type: 's', points: [new Point(rx * (1 - k), -ry), new Point(rx, -ry)] },
                    { type: 'Z', points: [] },
                ])
            })
        }
        // 4. ellipse
        {
            const matches = this.data.matchAll(/<ellipse\b[^>]*>/g)
            // if (log) console.log(Array.from(matches))
            matches.forEach(e => {
                const ellipse = e[0]
                // if (log) console.log('ellipse:', ellipse)
                const cx = Number(ellipse.match(/ cx="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('cx:', cx)
                const cy = Number(ellipse.match(/ cy="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('cy:', cy)
                const rx = Number(ellipse.match(/ rx="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('rx:', rx)
                const ry = Number(ellipse.match(/ ry="([^"]+)"/)?.at(1) ?? 0)
                // if (log) console.log('rx:', ry)
                const k = (Math.sqrt(2) - 1) * 4 / 3 // 贝塞尔与圆弧接近而选定的经验值
                result.push([
                    { type: 'M', points: [new Point(cx, cy - ry)] },
                    { type: 'c', points: [new Point(rx * k, 0), new Point(rx, ry * (1 - k)), new Point(rx, ry)] },
                    { type: 's', points: [new Point(rx * (k - 1), ry), new Point(-rx, ry)] },
                    { type: 's', points: [new Point(-rx, ry * (k - 1)), new Point(-rx, -ry)] },
                    { type: 's', points: [new Point(rx * (1 - k), -ry), new Point(rx, -ry)] },
                    { type: 'Z', points: [] },
                ])
            })
        }
        // 5. acr 不存在，实验性
        {
            const matches = this.data.matchAll(/<arc\b[^>]*>/g)
            // if (log) console.log(Array.from(matches))
            if (matches.length !== 0) {
                matches.forEach(e => {
                    // formal
                    const arc = e[0]
                    const cx = Number(arc.match(/ cx="([^"]+)"/)?.at(1) ?? 0)
                    const cy = Number(arc.match(/ cy="([^"]+)"/)?.at(1) ?? 0)
                    const r = Number(arc.match(/ r="([^"]+)"/)?.at(1) ?? 0)
                    const a = Number(arc.match(/ a="([^"]+)"/)?.at(1) ?? 0)
                    const b = Number(arc.match(/ b="([^"]+)"/)?.at(1) ?? 0)

                    const k = (Math.sqrt(2) - 1) * 4 / 3 // 贝塞尔与圆弧接近而选定的经验值

                    const re = (b - a) % 360
                    const clockwise = Math.sign(b - a)
                    const theta = re !== 0 ? re : 360 * clockwise

                    const rest = theta % 90
                    const quarters = parseInt(theta / 90)

                    const pc = new Point(cx, cy)
                    const p0 = new Point(cx + r, cy)
                    const pa = p0.rotate(pc, radians(normalDegrees(a)))
                    const pb = p0.rotate(pc, radians(normalDegrees(b)))

                    // if (log) console.log('start:', a)
                    // if (log) console.log('end:', b)
                    // if (log) console.log('theta:', `${theta} = ${quarters} * 90 + ${rest}`)
                    // if (log) console.log('clockwise:', clockwise == 0 ? null : (clockwise > 1))

                    const path = []
                    path.push({ type: 'M', points: [pc] })
                    path.push({ type: 'L', points: [pa] })

                    for (let i = 0; i < Math.abs(quarters); i++) {
                        const p1 = pa.rotate(pc, radians(i * 90 * clockwise))
                        const p2 = pa.rotate(pc, radians((i + 1) * 90 * clockwise))
                        const pm = pa.rotate(pc, radians((i + 1 / 2) * 90 * clockwise))
                        const vm = vec2.sub(pm.valueOf(), pc.valueOf())
                        const v1 = vec2.sub(p1.valueOf(), pc.valueOf())
                        const v2 = vec2.sub(p2.valueOf(), pc.valueOf())
                        const v3 = vec2.norm(vec2.perp(vm, v1))
                        const v4 = vec2.norm(vec2.perp(vm, v2))
                        const c1 = new Point(...vec2.scaleAndAdd(p1.valueOf(), v3, r * k))
                        const c2 = new Point(...vec2.scaleAndAdd(p2.valueOf(), v4, r * k))
                        path.push({ type: 'C', points: [c1, c2, p2] })
                    }
                    if (rest !== 0) {
                        const p1 = pa.rotate(pc, radians(quarters * 90))
                        const p2 = pb
                        const pm = pa.rotate(pc, radians(quarters * 90 + rest / 2))
                        const vm = vec2.sub(pm.valueOf(), pc.valueOf())
                        const v1 = vec2.sub(p1.valueOf(), pc.valueOf())
                        const v2 = vec2.sub(p2.valueOf(), pc.valueOf())
                        const v3 = vec2.norm(vec2.perp(vm, v1))
                        const v4 = vec2.norm(vec2.perp(vm, v2))
                        const c1 = new Point(...vec2.scaleAndAdd(p1.valueOf(), v3, r * k * (Math.abs(rest) / 90)))
                        const c2 = new Point(...vec2.scaleAndAdd(p2.valueOf(), v4, r * k * (Math.abs(rest) / 90)))
                        path.push({ type: 'C', points: [c1, c2, p2] })
                    }

                    path.push({ type: 'L', points: [pc] })
                    path.push({ type: 'Z', points: [] })
                    result.push(path)
                })
            }
        }
        return result
    }

    getGeometry(segs = 20) {
        //三角面
        const triangles = this.curveSets.flatMap(curves => {
            // 3. 交点切割后
            curves = splitEachOther(curves); if (log) console.log('%c3. 交点切割 :', 'color:orange', curves)
            // 6. 极值点切割
            curves = splitByExtremeY(curves); if (log) console.log('%c6. 轴线分割 :', 'color:orange', curves)
            // 7. 绘制图形
            const shapes = shapesFrom(curves); if (log) console.log('%c7. 填充图形 :', 'color:orange', shapes)
            // 6. 转为三角面
            const triangles = triangulate(shapes.flat(), segs); if (log) console.log('%c8. 三角面:', 'color:orange', triangles.length)

            return triangles
        })
        // 几何
        const geometry = geometryFromTriangles(triangles); // if (log) console.log('%cgeometry: ', 'color:rgb(60,150,255)', geometry)

        return geometry
    }

    getBorderGeometry(width = 10, segs = 20, borderCorner) {
        //三角面
        const triangles = this.curveSets.flatMap(curves => {
            // 交点切割
            curves = splitEachOther(curves)
            // 边框
            const completeShapes = getCompleteShapes(curves)//.map(shape => shape.filter((e, i) => i == 2))

            if (log) console.log('%c9. 需要描边的图形: ', 'color:rgb(255,0,80)', completeShapes)

            const borderShapes = completeShapes.map(e => getBorderShape(e, width, borderCorner))
            // const borderShapes = completeShapes.map(e => getBorderShape(e, width, borderCorner).filter(e => e.length > 4))

            if (log) console.log(`%c10. 边框的图形组: `, 'color:rgb(255,0,80)', borderShapes)
            // function drawPoint(x, y, id) {
            //     const body = document.body
            //     const ratio = window.devicePixelRatio
            //     const ele = document.createElement('div')
            //     ele.id = id
            //     ele.title = `x:${x},y:${y}`
            //     const style = {
            //         backgroundColor: 'red',
            //         width: '4px',
            //         height: '4px',
            //         borderRadius: '100%',
            //         position: 'absolute',
            //         top: `${(y * 4 + 300 - 2) / ratio}px`,
            //         left: `${(x * 4 + 300 - 2) / ratio}px`,
            //         zIndex: 2
            //     }
            //     for (const key in style) {
            //         const value = style[key]
            //         ele.style[key] = value
            //     }

            //     body.appendChild(ele)
            // }
            // borderShapes.forEach(group => {
            //     group.forEach((shape, shapeIndex) => {
            //         shape.forEach((curve, curveIndex) => {
            //             const p = curve.points[0]
            //             drawPoint(p.x, p.y, `${shapeIndex + 1}-${curveIndex + 1}`)
            //         })
            //     })
            // })

            curves = splitEachOther(borderShapes.flat(2))

            if (log) console.log(`%c10.1. 交点切割: `, 'color:rgb(255,0,80)', cloneShape(curves))

            curves = splitByExtremeY(curves)

            if (log) console.log(`%c10.2. 轴线分割: `, 'color:rgb(255,0,80)', cloneShape(curves))

            const quads = shapesFrom(curves, 'union', false)

            if (log) console.log(`%c11. 填充片元: `, 'color:rgb(255,0,80)', quads)

            return triangulate(quads.flat(), 10) // 根据宽度动态变化，设置seg的值
        })

        if (log) console.log('%c12. 边框三角面:', 'color:rgb(255,0,80)', triangles.length)

        // 几何
        const geometry = geometryFromTriangles(triangles); // if (log) console.log('%cgeometry: ', 'color:rgb(60,150,255)', geometry)

        return geometry
    }

    static Contour(lines, shape = []) {
        function StartOf(lines) {
            function format(e) {
                const [p1, p2] = e.points
                return p1.y <= p2.y ? e : e.toReversed()
            }
            const sorted = lines.toSorted((a, b) => {
                const l1 = format(a)
                const l2 = format(b)
                const p1 = l1.points.at(0)
                const p2 = l2.points.at(0)
                if (p2.y < p1.y) {
                    return 1
                } else if (p2.y == p1.y) {
                    if (p2.x > p1.x) {
                        return 1
                    } else if (p2.x == p1.x) {
                        const theta1 = l1.theta()
                        const theta2 = l2.theta()
                        if (theta2 < theta1) {
                            return 1
                        }
                    }
                }
                return -1
            })
            return format(sorted.at(0))
        }
        let start
        if (shape.length > 1) {
            const p1 = shape.at(0).points.at(0)
            const p2 = shape.at(-1).points.at(-1)
            if (p1.equals(p2)) {
                return shape
            } else {
                start = shape.at(-1)
            }
        } else {
            start = StartOf(lines)
            shape.push(start)
        }
        const tail = start.points.at(-1)
        // 过滤出与当前曲线尾点相连的全部曲线
        let related = lines.filter(e => {
            return !e.equals(start) && e.points.some(f => tail.equals(f))
        }).map(e => {
            return tail.equals(e.points.at(0)) ? e : e.toReversed()
        })
        if (related.length > 1) {
            const sorted = related.toSorted((a, b) => a.theta() - b.theta())
            shape.push(sorted[0])
        }
        else if (related.length == 1) {
            const c = related[0]
            shape.push(c)
        } else {
            return
        }
        return SVG.Contour(lines, shape)
    }

}

// 边框图形
function getBorderShape(shape, borderWidth = 10, borderCorner = 'beveled') {

    const result = []

    shape.forEach((e, i, a) => {
        const k = a.length - 1 // last index
        const j = i != k ? i + 1 : 0 // next index
        const f = a[j] // next

        const [p1, p2] = e.endPoints()
        const [p3, p4] = f.endPoints()

        if (p1.equals(p2)) return

        if (e instanceof LineSeg) {

            // outer
            const [p5, p6] = translate(p1, p2, borderWidth / 2, -90)
            // inner
            const [p7, p8] = translate(p1, p2, borderWidth / 2, 90)

            const rect = [new LineSeg(p5, p6), new LineSeg(p6, p8), new LineSeg(p8, p7), new LineSeg(p7, p5)]

            result.push(rect)
        }
        else if (e instanceof CubicBezier) {
            const outlineShape = e.outlineShape(borderWidth)
            if (outlineShape == undefined) {
                console.log('shittttt', i, e, borderWidth)
            } else {
                result.push(outlineShape)
            }
        }

        // corner
        const v1 = e.tangent(1)
        const v2 = f.tangent(0)
        const theta = degrees(vec2.angle2(v1, v2))
        let corner = []
        // 小于180°
        if (theta > 0 && theta < 180) {
            const p3 = p2.translate(v1, borderWidth / 2).rotate(p2, radians(-90))
            const p4 = p2.translate(v2, borderWidth / 2).rotate(p2, radians(-90))
            const p5 = p3.translate(v1, Math.tan(vec2.angle(v1, v2) / 2) * borderWidth / 2)
            switch (borderCorner) {
                case 'mitered': {
                    corner = [
                        new LineSeg(p2, p3),
                        new LineSeg(p3, p5),
                        new LineSeg(p5, p4),
                        new LineSeg(p4, p2),
                    ]
                    break
                }
                case 'rounded': {
                    corner = createArc(p2, p3, p4, 15).flatMap(e => e.segmentation(1))
                    corner.push(new LineSeg(corner.at(-1).points.at(-1), p2))
                    corner.push(new LineSeg(p2, corner.at(0).points.at(0)))
                    break
                }
                case 'beveled': {
                    corner = [
                        new LineSeg(p2, p3),
                        new LineSeg(p3, p4),
                        new LineSeg(p4, p2),
                    ]
                    break
                }
                default: {
                    break;
                }
            }
        }
        // 大于180°
        else if (theta > 180) {
            const p3 = p2.translate(v1, borderWidth / 2).rotate(p2, radians(90))
            const p4 = p2.translate(v2, borderWidth / 2).rotate(p2, radians(90))
            const p5 = p3.translate(v1, Math.tan(vec2.angle(v1, v2) / 2) * borderWidth / 2)
            switch (borderCorner) {
                case 'mitered': {
                    corner = [
                        new LineSeg(p2, p3),
                        new LineSeg(p3, p5),
                        new LineSeg(p5, p4),
                        new LineSeg(p4, p2),
                    ]
                    break
                }
                case 'rounded': {
                    corner = createArc(p2, p3, p4, 15).flatMap(e => e.segmentation(1))
                    corner.push(new LineSeg(corner.at(-1).points.at(-1), p2))
                    corner.push(new LineSeg(p2, corner.at(0).points.at(0)))
                    break
                }
                case 'beveled': {
                    corner = [
                        new LineSeg(p2, p3),
                        new LineSeg(p3, p4),
                        new LineSeg(p4, p2),
                    ]
                    break
                }
                default: {
                    break;
                }
            }

        }
        if (corner.length != 0) {
            result.push(corner)
        }

    })

    return result.map(e => {
        let id = createID()
        e.forEach(f => f.pathId = [id])
        return e
    })
}

function getCurves(path) {
    const curves = []
    let P0 // 路径起始点
    let P1 // 曲线起始点
    path.forEach((e, i, a) => {
        const { type, points } = e
        switch (type) {
            case 'm':
            case 'M': {
                P0 = P1 = points.at(0)
                break
            }
            case 'l':
            case 'L': {
                points.forEach(e => {
                    const curve = new LineSeg(P1, type === 'L' ? e : new Point(e.x + P1.x, e.y + P1.y))
                    curves.push(curve)
                    P1 = curve.points.at(-1)
                })
                break
            }
            case 'q':
            case 'Q': {
                points.split(2).forEach(e => {
                    const curve = new QuadraticBezier(P1, ...e.map(e => type === 'Q' ? e : new Point(e.x + P1.x, e.y + P1.y)))
                    curves.push(curve)
                    P1 = curve.points.at(-1)
                })
                break
            }
            case 'c':
            case 'C': {
                points.split(3).forEach(e => {
                    const curve = new CubicBezier(P1, ...e.map(e => type === 'C' ? e : new Point(e.x + P1.x, e.y + P1.y)))
                    curves.push(curve)
                    P1 = curve.points.at(-1)
                })
                break
            }
            case 's':
            case 'S': {
                points.forEach((f, j, b) => {
                    if (j % 2 == 0) {
                        let C
                        const lastCurve = curves.at(-1)
                        if (lastCurve instanceof LineSeg) {
                            C = lastCurve.points.at(-1)
                        }else if (lastCurve instanceof QuadraticBezier || lastCurve instanceof CubicBezier) {
                            C = lastCurve.points.at(-2)
                        }
                        const P2 = new Point(2 * P1.x - C.x, 2 * P1.y - C.y)
                        const P3 = f
                        const P4 = b[j + 1]
                        const curve = new CubicBezier(
                            P1,
                            P2,
                            type === 'S' ? P3 : P3.moveTo(P1.x, P1.y),
                            type === 'S' ? P4 : P4.moveTo(P1.x, P1.y),
                        )
                        curves.push(curve)
                        P1 = curve.points.at(-1)
                    }
                })
                break
            }
            case 't':
            case 'T': {
                points.forEach((f, j, b) => {
                    const lastCurve = curves.at(-1)
                    let C
                    if (lastCurve instanceof LineSeg) {
                        C = lastCurve.points.at(-1) // 直线
                    }else if (lastCurve instanceof QuadraticBezier || lastCurve instanceof CubicBezier) {
                        C = lastCurve.points.at(-2) // 曲线
                    }
                    const P2 = new Point(2 * P1.x - C.x, 2 * P1.y - C.y)
                    const P3 = f
                    const curve = new CubicBezier(
                        P1,
                        P2,
                        type === 'T' ? P3 : new Point(P3.x + P1.x, P3.y + P1.y)
                    )
                    curves.push(curve)
                    P1 = curve.points.at(-1)
                })

                break
            }
            case 'h':
            case 'H': {
                points.forEach(P2 => {
                    const curve = new LineSeg(P1, type === 'H' ? new Point(P2.x, P1.y) : new Point(P2.x + P1.x, P1.y))
                    curves.push(curve)
                    P1 = curve.points.at(-1)
                })
                break
            }
            case 'v':
            case 'V': {
                points.forEach(P2 => {
                    const curve = new LineSeg(P1, type === 'V' ? new Point(P1.x, P2.y) : new Point(P1.x, P2.y + P1.y))
                    curves.push(curve)
                    P1 = curve.points.at(-1)
                })
                break
            }
            case 'z':
            case 'Z': {
                if (!P1.equals(P0)) {
                    const curve = new LineSeg(P1, P0)
                    curves.push(curve)
                }
                break
            }
        }
    })
    return curves
}


// Y轴极值点切割
export function splitByExtremeY(curves) {

    // 极值点切割
    curves = curves.flatMap(e => {
        const extreme = e.extremeY()
        const pieces = e.split(...extreme.filter(e => e != 0 && e != 1))
        if (Array.isArray(pieces) && pieces.length != 0) {
            pieces.forEach(f => f.pathId = e.pathId)
            return pieces
        } else {
            return e
        }
    })
    if (log) console.log('%c自身极值点切割: ', 'color:orange', curves)

    // 获取分割线
    const lines = curves
        // 取首尾
        .flatMap(e => [e.points.at(0), e.points.at(-1)])
        // 去重
        .filter((e, i, a) => a.findIndex(f => e.y == f.y) == i)
        // 排序
        .toSorted((a, b) => a.y - b.y)
        // 画线
        .map(e => new Line(new Point(0, e.y), new Point(1, e.y)))

    // if (log) console.log('%c5. 分割线: ', 'color:orange', lines)
    if (log) console.log('%c5. 分割线: ', 'color:orange', lines.map(e => e.points[0].y))

    // 逐行扫描
    lines.forEach((e, i, a) => {
        if (i != 0) {
            const line1 = a[i - 1]
            const line2 = e
            // debugger
            curves = curves.flat()
            curves.forEach((c, i, a) => {
                if (c.scanned) return // 扫描过的跳过
                if (c.points.at(0).y == c.points.at(-1).y) return // 排除平行线段

                let x_c = [],
                    x1 = [],
                    x2 = []

                // 扫描线1经过曲线起点, 之所以不用intersect的原因是，在线和三次端点相交的求交点可能返回不正确的结果
                // TODO: 将它解决，要么修复intersect, 要么把这段代码塞进intersect里面，来达到优化的目的
                if (line1.points.at(0).y == c.points.at(0).y) {
                    x1.push([0, c.points.at(0).x])
                } else if (line1.points.at(0).y == c.points.at(-1).y) {
                    x1.push([1, c.points.at(-1).x])
                } else {
                    x1 = c.intersect(line1)
                }

                if (line2.points.at(0).y == c.points.at(0).y) {
                    x2.push([0, c.points.at(0).x])
                } else if (line2.points.at(0).y == c.points.at(-1).y) {
                    x2.push([1, c.points.at(-1).x])
                } else {
                    x2 = c.intersect(line2)
                }

                // debugger
                if (x1.length != 0) {
                    x1.forEach(e => x_c.push({ value: e[0], substitute: line1.lerp(e[1]) }))
                }
                if (x2.length != 0) {
                    x2.forEach(e => x_c.push({ value: e[0], substitute: line2.lerp(e[1]) }))
                }

                if (x_c.length == 2) {
                    x_c.sort((a, b) => a.value - b.value)
                    if (x_c[0].value == 0 && x_c[1].value == 1) {
                        c.scanned = true
                    } else {
                        // 分割
                        const x_c_filtered = x_c.filter(e => e.value != 0 && e.value != 1)
                        if (x_c_filtered.length != 0) {
                            const pieces = c.split(...x_c_filtered.map(e => e.value))
                            // 分割点替换
                            pieces.forEach((e, i, a) => {
                                e.pathId = c.pathId
                                const k = a.length - 1
                                if (i != 0) e.points.splice(0, 1, x_c_filtered[i - 1].substitute) // 非首替换
                                if (i != k) e.points.splice(-1, 1, x_c_filtered[i].substitute) // 非尾替换
                                // 如果每个分段夹在扫描线中间，则
                                const p1 = e.points.at(0)
                                const p2 = e.points.at(-1)
                                if (
                                    p1.y != p2.y &&
                                    [line1, line2].some(e => e.points.at(0).y == p1.y) &&
                                    [line1, line2].some(e => e.points.at(0).y == p2.y)
                                ) {
                                    e.scanned = true
                                }
                            })
                            // 曲线替换
                            a[i] = pieces
                        }
                    }
                }
            })
        }
    })
    // if (log) console.log('%c6. 极值点切割: ', 'color:orange', curves)
    curves = curves.flat()
    curves.forEach(e => delete e.scanned)

    return curves
}

// 扫描填充 evenodd union
export function shapesFrom(curves, rule = 'evenodd', drawLines) {
    const groups = []
    // 获取分割线
    const lines = curves
        // 取首尾
        .flatMap(e => [e.points.at(0), e.points.at(-1)])
        // 去重
        .filter((e, i, a) => a.findIndex(f => e.y == f.y) == i)
        // 排序
        .toSorted((a, b) => a.y - b.y)
        // 画线
        .map(e => new Line(new Point(0, e.y), new Point(1, e.y)))
    // console.log('分割线2:', lines)

    // 调整方向
    curves.forEach(e => {
        const [p1, p2] = e.endPoints() // first & last point
        if (p2.y < p1.y) {
            e.reverse()
        }
    })
    if (drawLines) {
        function drawLine(y, id) {
            const body = document.body
            const ratio = window.devicePixelRatio
            const ele = document.createElement('div')
            ele.id = id
            ele.title = `${id}: ${y}`
            const style = {
                backgroundColor: 'orange',
                width: '100%',
                height: '1px',
                position: 'absolute',
                top: `${(y * 4 + 300) / ratio}px`
            }
            for (const key in style) {
                const value = style[key]
                ele.style[key] = value
            }

            body.appendChild(ele)
        }
        lines.forEach((e, i) => {
            const y = e.points[0].y
            drawLine(y, `line(${i})`)
        })
        // drawLine(300)
    }
    // 1. 按分割线分组
    lines.forEach((e, i, a) => {
        if (i != a.length - 1) {
            const group = []
            const f = a[i + 1]
            curves.forEach(c => {
                const [p1, p2] = c.endPoints() // first & last point
                // 如果是平行于x轴的线段，则跳过
                if (p2.y == p1.y) {
                    return
                }
                // IMPORTANT: 曲线是否在两条分割线内的判定条件为近似相等，
                // 因为在轴线分割的时候端点近似相等判定的时候，直接返回了分割值，
                // 并没有替换掉端点
                if (
                    Math.abs(p1.y - e.points[0].y) <= 1e-4 &&
                    Math.abs(p2.y - f.points[0].y) <= 1e-4
                ) {
                    group.push(c)
                }
            })
            if (group.length > 1) {
                groups.push(group)
            }
        }
    })
    // 2. 组从高到低排序
    groups.sort((a, b) => {
        if (a.length == 0) {
            debugger
        }
        if (b.length == 0) {
            debugger
        }
        return a[0].points.at(0).y - b[0].points.at(0).y
    })
    // console.log(groups.map(e => e.map(f => f.endPoints().map(e => e.valueOf()))))
    // 3. 并集 规则填充
    let quads = groups.map((group, groupIndex) => {
        const shapes = []
        // 从右向左排序 ←
        group.sort((a, b) => {
            const pa = a.points.at(0)
            const pb = b.points.at(0)
            if (Math.abs(pa.x - pb.x) < 1e-3) { // 小数点后三位相同
                return b.lerp(0.5).x - a.lerp(0.5).x
            } else {
                return pb.x - pa.x
            }
        })
        // const [id_a, id_b] = group.flatMap(e => e.pathId).filter((e, i, a) => a.indexOf(e) == i)
        // const group_a = group.filter(e => e.pathId.indexOf(id_a) != -1)
        // const group_b = group.filter(e => e.pathId.indexOf(id_b) != -1)

        const ids = group.flatMap(e => e.pathId).filter((e, i, a) => a.indexOf(e) == i)
        const subGroups = ids.map(id => group.filter(e => e.pathId.indexOf(id) != -1))

        group.forEach((e, i, a) => {

            if (i != a.length - 1) {
                const f = a[i + 1] // next
                let fill = false
                if (rule == 'evenodd') {
                    fill = i % 2 == 0 || (i == 1 && a.length == 3)
                }
                else if (rule == 'union') {
                    // comparision >0 true <0 false
                    function onLeft(a, b) {
                        const pa = a.points.at(0)
                        const pb = b.points.at(0)
                        if (pa.x == pb.x) {
                            return a.lerp(0.5).x <= b.lerp(0.5).x
                        } else {
                            return pa.x <= pb.x
                        }
                    }

                    function toBeFilled(index, length) {
                        if (index < 0) {
                            return false
                        } else {
                            return index % 2 == 0 || (index == 1 && length == 3)
                        }
                    }

                    function contain_ef(g, i, a) {
                        if (i != a.length - 1) {
                            const h = a[i + 1] // next
                            let c1 = onLeft(e, g),
                                c2 = onLeft(h, e),
                                c3 = onLeft(f, g),
                                c4 = onLeft(h, f)
                            return c1 && c2 && c3 && c4
                        }
                    }

                    // let index_a = group_a.findIndex(contain_ef)
                    // let index_b = group_b.findIndex(contain_ef)

                    // let fill_a = toBeFilled(index_a)
                    // let fill_b = toBeFilled(index_b)

                    // fill = fill_a || fill_b

                    fill = subGroups.reduce((s, e) => {
                        let index = e.findIndex((f, i, a) => {
                            return contain_ef(f, i, a)
                        })
                        let condition = toBeFilled(index)
                        return condition || s
                    }, false)
                }
                if (fill) {
                    const [p1, p2] = e.endPoints()
                    const [p3, p4] = f.endPoints()

                    const shape = []

                    if (!p1.equals(p2, 1e-6)) shape.push(e)
                    if (!p2.equals(p4, 1e-6)) shape.push(new LineSeg(p2, p4))
                    if (!p4.equals(p3, 1e-6)) shape.push(f.toReversed())
                    if (!p3.equals(p1, 1e-6)) shape.push(new LineSeg(p3, p1))

                    shapes.push(shape)
                }
                // const newId = createID()
                // shapes.forEach(e => e.forEach(e => e.pathId = newId))
            }
        })
        return shapes
    })
    // console.log('quads: ', quads)
    return quads
}

// 图形有效，首尾相连
function isConstant(shape) {
    if (Array.isArray(shape)) {
        const valid = shape.filter(e => !e.points.at(0).equals(e.points.at(-1)))
        if (valid.length >= 3) {
            return valid.every((e, i, a) => {
                const k = a.length - 1
                const j = i == k ? 0 : i + 1
                const p1 = e.points.at(-1)
                const p2 = a[j].points.at(0)
                return p1.equals(p2)
            })
        }
    }

    return false
}

// 三角面
function triangulate(shapes, segs) {
    // copy
    shapes = shapes.map(e => e.map(e => new e.constructor(...e.points)))
    const triangles = []
    shapes.forEach(shape => {
        if (!isConstant(shape)) {
            // console.log('shape is no constant: ', shape)
        } else {
            if (shape.every(e => e instanceof LineSeg)) {
                if (shape.length === 3) {
                    triangles.push(shape.map(e => e.points.at(0)))
                }
                else if (shape.length === 4) {
                    const [a, b, c, d] = shape.map(e => e.points.at(0))
                    triangles.push([a, b, c])
                    triangles.push([a, c, d])
                }
            } else {
                const curves = shape.filter(e => e.points.at(0).y != e.points.at(-1).y)
                let lineSegs = curves.flatMap(e => e.segmentation(segs))
                lineSegs
                    .flatMap(e => e.points)
                    .filter((e, i, a) => a.findIndex(f => f.y == e.y) == i)
                    .toSorted((a, b) => a.y - b.y)
                    .forEach((e, i, a) => {
                        if (i != a.length - 1) {
                            const f = a[i + 1]
                            const line1 = new Line(new Point(0, e.y), new Point(1, e.y))
                            const line2 = new Line(new Point(0, f.y), new Point(1, f.y))
                            // 扫描：得到曲线合集, 应该只有两个
                            const curves = []
                            lineSegs.flat().forEach((e, i, a) => {
                                if (e.scanned) return // 扫描过的跳过
                                if (e.points.at(0).y == e.points.at(-1).y) return // 横线跳过
                                // const x1 = e.intersect(line1)
                                // const x2 = e.intersect(line2)

                                let x1 = [],
                                    x2 = []
                                if (line1.points.at(0).y == e.points.at(0).y) {
                                    x1.push([0, e.points.at(0).x])
                                } else if (line1.points.at(0).y == e.points.at(-1).y) {
                                    x1.push([1, e.points.at(-1).x])
                                } else {
                                    x1 = e.intersect(line1)
                                }
                                if (line2.points.at(0).y == e.points.at(0).y) {
                                    x2.push([0, e.points.at(0).x])
                                } else if (line2.points.at(0).y == e.points.at(-1).y) {
                                    x2.push([1, e.points.at(-1).x])
                                } else {
                                    x2 = e.intersect(line2)
                                }

                                if (x1.length == 1 && x2.length == 1) {
                                    let x = []
                                    x.push({ value: x1[0][0], substitute: line1.lerp(x1[0][1]) })
                                    x.push({ value: x2[0][0], substitute: line2.lerp(x2[0][1]) })
                                    // 过滤
                                    x = x.filter(e => e.value != 0 && e.value != 1)
                                    // 分割
                                    if (x.length == 0) {
                                        e.scanned = true
                                        curves.push(e)
                                    } else {
                                        const pieces = e.split(...x.map(e => e.value))
                                        // 分割点替换
                                        pieces.forEach((e, i, a) => {
                                            const k = a.length - 1
                                            if (i != 0) e.points.splice(0, 1, x[i - 1].substitute) // 非首替换
                                            if (i != k) e.points.splice(-1, 1, x[i].substitute) // 非尾替换
                                            // 如果每个分段夹在扫描线中间，则
                                            const p1 = e.points.at(0)
                                            const p2 = e.points.at(-1)
                                            if (
                                                p1.y != p2.y &&
                                                [line1, line2].some(e => e.points.at(0).y == p1.y) &&
                                                [line1, line2].some(e => e.points.at(0).y == p2.y)
                                            ) {
                                                e.scanned = true
                                                curves.push(e)
                                            }
                                        })
                                        // 曲线替换
                                        a[i] = pieces
                                    }
                                }
                            })
                            if (curves.length != 2) {
                                return
                            }
                            // 调整方向
                            curves.forEach(e => {
                                const [p1, p2] = e.endPoints()
                                if (p2.y < p1.y) {
                                    e.reverse()
                                }
                            })
                            // 排序：横向从右向左
                            curves.sort((a, b) => {
                                const pa = a.points.at(0)
                                const pb = b.points.at(0)
                                if (pb.x == pa.x) {
                                    return b.lerp(0.5).x - a.lerp(0.5).x
                                } else {
                                    return pb.x - pa.x
                                }
                            })
                            // 绘制：将直边多边形转化为三角面
                            const pa = curves[0].points.at(0)
                            const pb = curves[0].points.at(-1)
                            const pc = curves[1].points.at(-1)
                            const pd = curves[1].points.at(0)

                            const polygon = []

                            if (!pa.equals(pb, 1e-6)) polygon.push(new LineSeg(pa, pb))
                            if (!pb.equals(pc, 1e-6)) polygon.push(new LineSeg(pb, pc))
                            if (!pc.equals(pd, 1e-6)) polygon.push(new LineSeg(pc, pd))
                            if (!pd.equals(pa, 1e-6)) polygon.push(new LineSeg(pd, pa))

                            if (polygon.length === 3) {
                                triangles.push(polygon.map(e => e.points.at(0)))
                            }
                            else if (polygon.length === 4) {
                                const [a, b, c, d] = polygon.map(e => e.points.at(0))
                                triangles.push([a, b, c])
                                triangles.push([a, c, d])
                            }
                        }
                    })
            }
        }
    })
    return triangles
}

// 几何信息
function geometryFromTriangles(triangles) {
    const geometry = {
        vertices: [],
        normals: [],
        uvs: [],
        indices: [],
    }
    const points = triangles.flat()
    const minX = Math.min(...points.map(e => e[0]))
    const maxX = Math.max(...points.map(e => e[0]))
    const minY = Math.min(...points.map(e => e[1]))
    const maxY = Math.max(...points.map(e => e[1]))
    const w = maxX - minX
    const h = maxY - minY
    triangles.forEach((e, i) => {
        e.forEach(f => {
            const { x, y } = f
            geometry.vertices.push(x, y, 0)
            geometry.normals.push(0, 0, 1)
            const u = (x - minX) / w
            const v = (y - minY) / h
            geometry.uvs.push(u, v)
        })
        geometry.indices.push(i * 3, i * 3 + 1, i * 3 + 2)
    })
    return geometry
}

// 外轮廓和洞，也可改名为getCompleteShapes, 意为完整封闭非自交图形
export function getCompleteShapes(lines, shapes = [], shape = []) {
    lines = lines.filter(e => !e.isPoint())
    function getStart(lines) {
        // 调整方向
        lines.forEach(e => {
            const [p1, p2] = e.endPoints()
            if (p2.y < p1.y) {
                e.reverse()
            }
        })
        const sorted = lines.toSorted((a, b) => {
            const pa = a.points.at(0)
            const pb = b.points.at(0)
            if (pb.y < pa.y) {
                return 1
            } else if (pb.y == pa.y) {
                if (pb.x > pa.x) {
                    return 1
                } else if (pb.x == pa.x) {
                    if (b.lerp(0.5).x > a.lerp(0.5).x) {
                        return 1
                    }
                }
            }
            return -1
        })

        return sorted[0]
    }
    const unresolved = lines.filter(e => !e.resolved)
    if (unresolved.length == 0) {
        if (shape.length == 0) {
            lines.forEach(e => delete e.resolved)
            return shapes
        } else {
            shape.shift()
            shape.forEach(e => delete e.resolved)
            return getCompleteShapes(lines, shapes, [])
        }
    } else {
        let start
        if (shape.length === 0) {
            start = getStart(unresolved)
            start.resolved = true
            shape.push(start)
        } else {
            start = shape.at(-1)
        }

        const tail = start.points.at(-1)
        // 过滤出与当前曲线尾点相连的全部曲线
        let related = unresolved.filter((e, i, a) => {
            return !start.equals(e) && e.endPoints().some((f, i) => {
                if (tail.equals(f, 1e-3)) {
                    if (i == 1) {
                        e.reverse()
                    }
                    return true
                } else {
                    return false
                }
            })
        })
        if (related.length == 1) {
            related[0].resolved = true
            shape.push(related[0])
        } else if (related.length > 1) {
            const t = start.toReversed().tangent(0)
            // const tangent = related.map(e => degrees(vec2.angle2(t, e.tangent(0))))
            const sorted = related.toSorted((a, b) => {
                const ta = a.tangent(0)
                const tb = b.tangent(0)
                if (vec2.equals(ta, tb)) {
                    return b.lerp(0.5).x - a.lerp(0.5).x
                } else {
                    const theta_a = vec2.angle2(t, ta)
                    const theta_b = vec2.angle2(t, tb)
                    return theta_a - theta_b
                }
            })
            sorted[0].resolved = true
            shape.push(sorted[0])
        } else {
            shape.pop()
        }
        if (shape.length > 1) {
            const p1 = shape.at(0).points.at(0)
            const p2 = shape.at(-1).points.at(-1)
            if (p1.equals(p2, 1e-3)) {
                // TODO: 可优化，线段合并
                shape.forEach((e, i, a) => {
                    if (i == a.length - 1) return
                    const j = i + 1 // next index
                    if(e instanceof LineSeg && a[j] instanceof LineSeg){
                        if(e.points.at(-1).equals(a[j].points.at(0))){
                            a[i] = null
                            a[j] = new LineSeg(e.points.at(0), a[j].points.at(-1))
                        }
                    }
                })
                shapes.push(shape.filter(e => e != undefined))
                shape = []
            }
        }

        return getCompleteShapes(lines, shapes, shape)
    }
}

// 扇形
function createArc(pc, pa, pb, max = 90) {

    const r = vec2.length(vec2.sub(pa.valueOf(), pc.valueOf())) // 半径

    const v1 = vec2.norm(vec2.sub(pa.valueOf(), pc.valueOf()))
    const v2 = vec2.norm(vec2.sub(pb.valueOf(), pc.valueOf()))

    const theta = degrees(vec2.angle2(v2, v1)) // 左手定则，顺时针旋转

    const k = (Math.sqrt(2) - 1) * 4 / 3 // 贝塞尔与圆弧接近而选定的经验值

    const theta2 = theta % 360
    const clockwise = Math.sign(theta) // 顺时针
    const theta3 = theta2 !== 0 ? theta2 : 360 * clockwise

    const min = Math.min(max, 90)
    const theta4 = theta3 % min
    const quarters = parseInt(theta3 / min)

    const path = []
    for (let i = 0; i < Math.abs(quarters); i++) {
        const p1 = pa.rotate(pc, radians(i * min * clockwise))
        const p2 = pa.rotate(pc, radians((i + 1) * min * clockwise))
        const pm = pa.rotate(pc, radians((i + 1 / 2) * min * clockwise))
        const vm = vec2.sub(pm.valueOf(), pc.valueOf())
        const v1 = vec2.sub(p1.valueOf(), pc.valueOf())
        const v2 = vec2.sub(p2.valueOf(), pc.valueOf())
        const v3 = vec2.norm(vec2.perp(vm, v1))
        const v4 = vec2.norm(vec2.perp(vm, v2))
        const c1 = new Point(...vec2.scaleAndAdd(p1.valueOf(), v3, r * k * (Math.abs(theta4) / 90)))
        const c2 = new Point(...vec2.scaleAndAdd(p2.valueOf(), v4, r * k * (Math.abs(theta4) / 90)))
        path.push(new CubicBezier(p1, c1, c2, p2))
    }
    if (theta4 !== 0) {
        const p1 = pa.rotate(pc, radians(quarters * min))
        const p2 = pb
        const pm = pa.rotate(pc, radians(quarters * min + theta4 / 2))
        const vm = vec2.sub(pm.valueOf(), pc.valueOf())
        const v1 = vec2.sub(p1.valueOf(), pc.valueOf())
        const v2 = vec2.sub(p2.valueOf(), pc.valueOf())
        const v3 = vec2.norm(vec2.perp(vm, v1))
        const v4 = vec2.norm(vec2.perp(vm, v2))
        const c1 = new Point(...vec2.scaleAndAdd(p1.valueOf(), v3, r * k * (Math.abs(theta4) / 90)))
        const c2 = new Point(...vec2.scaleAndAdd(p2.valueOf(), v4, r * k * (Math.abs(theta4) / 90)))
        path.push(new CubicBezier(p1, c1, c2, p2))
    }
    return path
}

// 平移两点
function translate(p1, p2, width, deg) {
    p1 = p1.valueOf()
    p2 = p2.valueOf()
    const p = vec2.rotate(p2, p1, radians(deg))
    const v = vec2.norm(vec2.sub(p, p1))
    const p3 = new Point(...vec2.scaleAndAdd(p1, v, width))
    const p4 = new Point(...vec2.scaleAndAdd(p2, v, width))
    return [p3, p4]
}

function cloneShape(shape) {
    return shape.map(e => new e.constructor(...e.points.map(e => new Point(e.x, e.y))))
}