const EPSILON = 1e-6

const Utils = {
    isNumber(value) {
        return typeof value === 'number' && !isNaN(value)
    }
}

// 点
export class Point {
    // fixed为取近似点精度，默认为null
    constructor(x, y, fixed = 8) {
        if (x == undefined || y == undefined) {
            debugger
        }
        // this.x = parseFloat(x.toFixed(fixed))
        // this.y = parseFloat(y.toFixed(fixed))
        this.x = x
        this.y = y
    }
    /**
     * 当两点的距离小于等于指定数字的时候，视为相等，默认1e-3
     * 例如0.0011和0.0019相等，0.0006和0.0014相等
     * @param {*} b 另一个点
     * @param {*} tolerance 误差
     * @returns 
     */
    equals(b, tolerance = 1e-3) {
        if (b instanceof Point) {
            const dist = Math.hypot(Math.abs(this.x - b.x), Math.abs(this.y - b.y))
            return dist <= tolerance
        }
    }
    /**
     * 转换为单精度浮点数，然后比较是否完全相等
     * @param {*} b 另一个点
     * @returns 
     */
    exactEquals(b) {
        if (b instanceof Point) {
            return Math.fround(this.x) == Math.fround(b.x) && Math.fround(this.y) == Math.fround(b.y)
        }
    }
    // 以数组形式返回点坐标
    valueOf() {
        return [this.x, this.y]
    }
    move(x, y) {
        this.x += x
        this.y += y
    }
    moveTo(x, y) {
        return new Point(this.x + x, this.y + y)
    }
    // rotate around
    rotate(b, rad) {
        if (b instanceof Point) {
            //Translate point to the origin
            let p0 = this.x - b.x,
                p1 = this.y - b.y,
                sinC = Math.sin(rad),
                cosC = Math.cos(rad);
            //perform rotation and translate to correct position
            const x = p0 * cosC - p1 * sinC + b.x;
            const y = p0 * sinC + p1 * cosC + b.y;
            return new Point(x, y)
        }
    }
    /**
     * 平移
     * @param {*} a 
     * @param {*} dist 
     * @returns 
     */
    translate(a, dist) {
        const x = this.x + a[0] * dist
        const y = this.y + a[1] * dist
        return new Point(x, y)
    }
    distanceFrom(a) {
        if (a instanceof Point) {
            const x = this.x - a.x
            const y = this.y - a.y
            return Math.hypot(x, y)
        }
    }
}

// 曲线基类
export class Curve {
    constructor() {
        this.points = Array.from(arguments)
    }
    // t: 0-1
    static Lerp(points, t) {
        const results = []
        if (points.length > 1) {
            points.forEach((e, i, a) => {
                if (i < a.length - 1) {
                    const f = a[i + 1]
                    const v1 = mix(e.x, f.x, t)
                    const v2 = mix(e.y, f.y, t)
                    results.push(new Point(v1, v2))
                }
            })
            return Curve.Lerp(results, t)
        } else {
            return points[0]
        }
    }
    lerp(t) {
        if (Utils.isNumber(t)) {
            return Curve.Lerp(this.points, t)
        } else {
            console.error('%cnot a number: ', 'color:orange', t)
        }
    }
    toReversed() {
        return new this.constructor(...this.points.toReversed())
    }
    reverse() {
        this.points.reverse()
    }
    getBoundingBox() {
        if (
            this instanceof LineSeg ||
            this instanceof QuadraticBezier ||
            this instanceof CubicBezier
        ) {
            const points = [
                this.extremeX().map(e => this.lerp(e)),
                this.extremeY().map(e => this.lerp(e)),
            ].flat()
            const minX = Math.min(...points.map(e => e.x))
            const maxX = Math.max(...points.map(e => e.x))
            const minY = Math.min(...points.map(e => e.y))
            const maxY = Math.max(...points.map(e => e.y))
            this.boundingBox = {
                x: minX,
                y: minY,
                w: maxX - minX,
                h: maxY - minY
            }
            return this.boundingBox
        }
    }
    // 包围盒相交
    isBoundingBoxOverlapped(curve) {
        const boxA = this.boundingBox ?? this.getBoundingBox()
        const boxB = curve.boundingBox ?? curve.getBoundingBox()
        if (boxB && boxA) {
            if (
                boxA.x > boxB.x + boxB.w ||
                boxB.x > boxA.x + boxA.w ||
                boxA.y > boxB.y + boxB.h ||
                boxB.y > boxA.y + boxA.h
            ) {
                return false
            } else {
                return true
            }
        } else {
            if (boxA == undefined) {
                throw new Error('boxA undifined')
            } else {
                throw new Error('boxB undifined')
            }
        }
    }
    endPoints() {
        return this.points.toSpliced(1, this.points.length - 2)
    }
    isPoint() {
        return this.points.every(e => this.points.every(f => f == e || f.equals(e, 1e-3)))
    }
    // TODO: 无科学依据, 获取近似长度
    length() {
        // window.count ??= 0
        // window.count++
        // console.log(window.count)
        let result = 0
        const num = 20
        for (let i = 0; i < num; i++) {
            const p1 = this.lerp(i / num)
            const p2 = this.lerp((i + 1) / num)
            result += p1.distanceFrom(p2)
        }
        return result
    }
}

// 直线
export class Line extends Curve {
    constructor(p1, p2) {
        super(p1, p2)
    }
    intersect(curve) {
        // let t1 = new Date().valueOf()
        let result = []
        // 直线
        if (curve instanceof Line) {
            const [[x1, y1], [x2, y2]] = this.points.map(function holy(p) { return p.valueOf() }.bind(this))
            const [[x3, y3], [x4, y4]] = curve.points.map(function shit(p) { return p.valueOf() }.bind(this))

            const a0 = x2 - x1
            const b0 = x1
            const c0 = x4 - x3
            const d0 = x3

            const a1 = y2 - y1
            const b1 = y1
            const c1 = y4 - y3
            const d1 = y3

            const denominator = a1 * c0 - a0 * c1
            if (denominator != 0) {
                const t0 = ((d1 - b1) * c0 - (d0 - b0) * c1) / denominator
                const t1 = ((b1 - d1) * a0 - (b0 - d0) * a1) / -denominator
                result = [[t0, t1]]
            } else {
                // 这种情况可能没有交点，或者有无数个交点，例如重叠平行
            }
        }
        // 二次贝塞尔曲线
        else if (curve instanceof QuadraticBezier) {
            const [[x1, y1], [x2, y2]] = this.points.map(p => p.valueOf())
            const [[x3, y3], [x4, y4], [x5, y5]] = curve.points.map(p => p.valueOf())

            const a0 = x2 - x1
            const b0 = x1
            const c0 = x3 - 2 * x4 + x5
            const d0 = -2 * x3 + 2 * x4
            const e0 = x3

            const a1 = y2 - y1
            const b1 = y1
            const c1 = y3 - 2 * y4 + y5
            const d1 = -2 * y3 + 2 * y4
            const e1 = y3

            const A = a1 * c0 - a0 * c1
            const B = a1 * d0 - a0 * d1
            const C = a1 * (e0 - b0) - a0 * (e1 - b1)
            const delta = B ** 2 - 4 * A * C

            if (A == 0) {
                const t1 = -C / B
                if (a0 != 0) {
                    const v = new Polynomial([c0, d0, e0]).valueOf(t1)
                    const t0 = (v - b0) / a0
                    result = [[t0, t1]]
                }
                else if (a1 != 0) {
                    const v = new Polynomial([c1, d1, e1]).valueOf(t1)
                    const t0 = (v - b1) / a1
                    result = [[t0, t1]]
                }
            } else {
                if (delta > 0) {
                    const t1 = [(-B + Math.sqrt(delta)) / A / 2, (-B - Math.sqrt(delta)) / A / 2]
                    result = t1.map(e => {
                        if (a0 != 0) {
                            const v = new Polynomial([c0, d0, e0]).valueOf(e)
                            const t0 = (v - b0) / a0
                            return [t0, e]
                        }
                        else if (a1 != 0) {
                            const v = new Polynomial([c1, d1, e1]).valueOf(e)
                            const t0 = (v - b1) / a1
                            return [t0, e]
                        }
                    })
                }
                else if (delta == 0) {
                    const t1 = -B / A / 2
                    if (a0 != 0) {
                        const v = new Polynomial([c0, d0, e0]).valueOf(t1)
                        const t0 = (v - b0) / a0
                        result = [[t0, t1]]
                    }
                    else if (a1 != 0) {
                        const v = new Polynomial([c1, d1, e1]).valueOf(t1)
                        const t0 = (v - b1) / a1
                        result = [[t0, t1]]
                    }
                }
            }

        }
        // 三次贝塞尔曲线
        else if (curve instanceof CubicBezier) {
            const [[x1, y1], [x2, y2]] = this.points.map(p => p.valueOf())
            const [[x3, y3], [x4, y4], [x5, y5], [x6, y6]] = curve.points.map(p => p.valueOf())

            const a0 = x2 - x1
            const b0 = x1
            const c0 = -x3 + 3 * x4 - 3 * x5 + x6
            const d0 = 3 * x3 - 6 * x4 + 3 * x5
            const e0 = -3 * x3 + 3 * x4
            const f0 = x3

            const a1 = y2 - y1
            const b1 = y1
            const c1 = -y3 + 3 * y4 - 3 * y5 + y6
            const d1 = 3 * y3 - 6 * y4 + 3 * y5
            const e1 = -3 * y3 + 3 * y4
            const f1 = y3

            const A = a1 * c0 - a0 * c1
            const B = a1 * d0 - a0 * d1
            const C = a1 * e0 - a0 * e1
            const D = a1 * (f0 - b0) - a0 * (f1 - b1)

            // tested: Y
            if (A != 0) {
                // const t2 = solveCubicEquation2(A, B, C, D)
                // const t2 = new Polynomial(A, B, C, D).roots(0, 1) 
                // TODO: 平行于X轴的直线与三次贝塞尔曲线端点相交可能得到不值
                // 猜测原因可能是因为端点的问题，在二分法处理的时候出错，暂时处理办法稍微扩大允许值
                const t2 = new Polynomial(A, B, C, D).roots(0 - 0.001, 1 + 0.001)
                result = t2.map(t => {
                    if (a0 != 0) {
                        const v = new Polynomial([c0, d0, e0, f0]).valueOf(t)
                        return [(v - b0) / a0, t]
                    }
                    if (a1 != 0) {
                        const v = new Polynomial([c1, d1, e1, f1]).valueOf(t)
                        return [(v - b1) / a1, t]
                    }
                })
            }
            // tested: N
            else if (B != 0) {
                // const t2 = solveQuadraticEquation(B, C, D)
                const t2 = new Polynomial(B, C, D).roots(0, 1)
                result = t2.map(t => {
                    const v = new Polynomial([c0, d0, e0, f0]).valueOf(t)
                    if (a0 != 0) {
                        return [(v - b0) / a0, t]
                    }

                })
            }
            // tested: N
            else if (C != 0) {
                const t2 = [-D / C]
                result = t2.map(t => {
                    const v = new Polynomial([c0, d0, e0, f0]).valueOf(t)
                    if (a0 != 0) {
                        return [(v - b0) / a0, t]
                    }
                })
            }
        }

        // let t2 = new Date().valueOf()
        // console.log('%cintersect cost: ', 'color:rgb(255,255,80)', (t2 - t1) + ' ms', this.points, curve.points)

        // final
        return result.filter(e => {
            return Array.isArray(e) && e.length == 2 && this.valid(e[0]) && curve.valid(e[1])
        })
    }
    tangent() {
        const [p1, p2] = this.points
        const [x1, y1] = p1.valueOf()
        const [x2, y2] = p2.valueOf()
        const dy = y2 - y1
        const dx = x2 - x1
        return vector2.normalize([dx, dy])
    }
    // 夹角
    theta() {
        const [p1, p2] = this.points
        const v = vector2.normalize(vector2.sub(p2.valueOf(), p1.valueOf()))
        return vector2.angle2([1, 0], v)
    }
    // 参数不可以是数组
    split() {
        if (arguments.length > 1) {
            const t = Array.from(arguments)
            if (t.every(e => this.valid(e))) {
                const results = []
                t.filter((e, i, a) => a.indexOf(e) == i).toSorted((a, b) => a - b).forEach((e, i, a) => {
                    if (i == 0) {
                        results.push(new Ray(this.lerp(e), this.lerp(e - 1)))
                        results.push(new LineSeg(this.lerp(e), this.lerp(a[i + 1])))
                    }
                    else if (i == a.length - 1) {
                        results.push(new Ray(this.lerp(e), this.lerp(e + 1)))
                    }
                    else {
                        results.push(new LineSeg(this.lerp(e), this.lerp(a[i + 1])))
                    }
                })
                // console.log('curve is splitting:', this, arguments, results)
                return results
            }
        }
        else if (arguments.length == 1) {
            const t = arguments[0]
            if (this.valid(t)) {
                const [P0, P1] = this.points
                const Q = Curve.Lerp([P0, P1], t) // 分割点
                const Q1 = Curve.Lerp([P0, P1], t - 1) // 分割点
                const Q2 = Curve.Lerp([P0, P1], t + 1) // 分割点

                const L1 = new Ray(Q, Q1)
                const L2 = new Ray(Q, Q2)
                return [L1, L2]
            }
        }
    }
    valid(t) {
        return Utils.isNumber(t)
    }
    equals(r) {
        if (r.constructor === this.constructor) {
            return r.points.every(e => this.contains(e))
        }
        return false
    }
    // 包含, 在计算的时候涉及到浮点数精度问题
    contains(p, tolerance = 1e-14) {
        if (p instanceof Point) {
            const [p1, p2] = this.points
            const [x1, y1] = p1.valueOf()
            const [x2, y2] = p2.valueOf()
            const [x3, y3] = p.valueOf()
            if (x2 - x1 !== 0) {
                const t = (x3 - x1) / (x2 - x1)
                if (this.valid(t)) {
                    const [x4, y4] = this.lerp(t)
                    return Math.abs(y3 - y4) < tolerance
                }
            }
            else if (y2 - y1 !== 0) {
                const t = (y3 - y1) / (y2 - y1)
                if (this.valid(t)) {
                    const [x4, y4] = this.lerp(t)
                    return Math.abs(x3 - x4) < tolerance
                }
            }
        }
        return false
    }
}

// 射线
export class Ray extends Line {
    constructor(p1, p2) {
        super(p1, p2)
    }
    valid(t) {
        return Utils.isNumber(t) && t >= 0
    }
    split() {
        if (arguments.length == 1) {
            const t = arguments[0]
            if (t > 0) {
                const [P0, P1] = this.points
                const Q = Curve.Lerp([P0, P1], t) // 分割点
                const Q2 = Curve.Lerp([P0, P1], t + 1) // 分割点

                const L1 = new LineSeg(P0, Q)
                const L2 = new Ray(Q, Q2)
                return [L1, L2]
            } else {
                return [this]
            }
        }
        else if (arguments.length > 1) {
            const t = Array.from(arguments)
            if (t.every(e => this.valid(e))) {
                return t.toSorted((a, b) => a - b).reduce((s, e, i, a) => {
                    const prev = a[i - 1] ?? 0
                    const T = e - prev
                    s.push(...s.pop().split(T))
                    return s
                }, [this])
            }
        }
    }
    equals(r) {
        if (r.constructor === this.constructor) {
            const sameStart = r.points.at(0).equals(this.points.at(0))
            return sameStart && vector2.equals(this.tangent(0), r.tangent(0))
        }
        return false
    }
}

// 线段
export class LineSeg extends Line {
    constructor(p1, p2) {
        super(p1, p2)
    }
    valid(t) {
        return Utils.isNumber(t) && t >= 0 && t <= 1
    }
    split() {
        if (arguments.length == 1) {
            const t = arguments[0]
            if (t > 0 && t < 1) {
                const [P0, P1] = this.points
                const Q = Curve.Lerp([P0, P1], t) // 分割点

                const L1 = new LineSeg(P0, Q)
                const L2 = new LineSeg(Q, P1)
                return [L1, L2]
            } else {
                return [this]
            }
        }
        else if (arguments.length > 1) {
            const t = Array.from(arguments)
            if (t.every(e => this.valid(e))) {
                return t.toSorted((a, b) => a - b).reduce((s, e, i, a) => {
                    const prev = a[i - 1] ?? 0
                    const T = (e - prev) / (1 - prev)
                    s.push(...s.pop().split(T))
                    return s
                }, [this])
            }
        }
    }
    equals(r) {
        if (r.constructor === this.constructor) {
            const same1 = r.points.every((e, i) => e.equals(this.points.at(i))) // 完全一致
            const same2 = r.points.toReversed().every((e, i) => e.equals(this.points.at(i))) // 形状一致，控制点相反
            return same1 || same2
        }
        return false
    }
    extremeY() {
        return [0, 1]
    }
    extremeX() {
        return [0, 1]
    }
    segmentation(segs = 20) {
        const result = []
        for (let i = 0; i <= segs; i++) {
            if (i != segs) {
                const p1 = this.lerp(i / segs)
                const p2 = this.lerp((i + 1) / segs)
                const ls = new LineSeg(p1, p2)
                result.push(ls)
            }
        }
        return result
    }
}

// 二次贝塞尔曲线
export class QuadraticBezier extends Curve {
    constructor(p1, p2, p3) {
        super(p1, p2, p3)
    }
    intersect(curve) {
        let result = []
        if (!this.isBoundingBoxOverlapped(curve)) {
            return result
        }
        if (curve instanceof Line) {
            result = curve.intersect(this).map(e => e.toReversed())
        }
        else if (curve instanceof QuadraticBezier) {
            const [[x1, y1], [x2, y2], [x3, y3]] = this.points.map(p => p.valueOf())
            const [[x4, y4], [x5, y5], [x6, y6]] = curve.points.map(p => p.valueOf())

            const a0 = x1 - 2 * x2 + x3
            const b0 = -2 * x1 + 2 * x2
            const c0 = x1

            const a1 = x4 - 2 * x5 + x6
            const b1 = -2 * x4 + 2 * x5
            const c1 = x4

            const a2 = y1 - 2 * y2 + y3
            const b2 = -2 * y1 + 2 * y2
            const c2 = y1

            const a3 = y4 - 2 * y5 + y6
            const b3 = -2 * y4 + 2 * y5
            const c3 = y4

            const t1 = getEliminantEquation([
                [[a1], [b1], [-a0, -b0, c1 - c0]],
                [[a3], [b3], [-a2, -b2, c3 - c2]]
            ])

            const t2 = getEliminantEquation([
                [[a0], [b0], [-a1, -b1, c0 - c1]],
                [[a2], [b2], [-a3, -b3, c2 - c3]]
            ])

            result = t1.map(e => {
                const p1 = this.lerp(e)
                const find = t2.find(f => {
                    const p2 = curve.lerp(f)
                    return p1.equals(p2)
                })
                return [e, find]
            })
        }
        else if (curve instanceof CubicBezier) {
            const [[x1, y1], [x2, y2], [x3, y3]] = this.points.map(p => p.valueOf())
            const [[x4, y4], [x5, y5], [x6, y6], [x7, y7]] = curve.points.map(p => p.valueOf())

            const a0 = x1 - 2 * x2 + x3
            const b0 = -2 * x1 + 2 * x2
            const c0 = x1

            const a1 = -x4 + 3 * x5 - 3 * x6 + x7
            const b1 = 3 * x4 - 6 * x5 + 3 * x6
            const c1 = -3 * x4 + 3 * x5
            const d1 = x4

            const a2 = y1 - 2 * y2 + y3
            const b2 = -2 * y1 + 2 * y2
            const c2 = y1

            const a3 = -y4 + 3 * y5 - 3 * y6 + y7
            const b3 = 3 * y4 - 6 * y5 + 3 * y6
            const c3 = -3 * y4 + 3 * y5
            const d3 = y4

            const t1 = getEliminantEquation([
                [[a1], [b1], [c1], [-a0, -b0, d1 - c0]],
                [[a3], [b3], [c3], [-a2, -b2, d3 - c2]]
            ])

            const t2 = getEliminantEquation([
                [[a0], [b0], [-a1, -b1, -c1, c0 - d1]],
                [[a2], [b2], [-a3, -b3, -c3, c2 - d3]]
            ])

            result = t1.map(e => {
                const p1 = this.lerp(e)
                const find = t2.find(f => {
                    const p2 = curve.lerp(f)
                    return p1.equals(p2)
                })
                return [e, find]
            })
        }
        // final
        return result.filter(e => {
            return Array.isArray(e) && e.length == 2 && this.valid(e[0]) && curve.valid(e[1])
        })
    }
    split() {
        if (arguments.length == 1) {
            const t = arguments[0]
            if (t > 0 && t < 1) {
                const [P0, P1, P2] = this.points
                // 计算中间点
                let Q0 = P0;
                let Q1 = Curve.Lerp([P0, P1], t)
                let Q2 = Curve.Lerp([P0, P1, P2], t)

                let R0 = Curve.Lerp([P2, P1, P0], 1 - t)
                let R1 = Curve.Lerp([P2, P1], 1 - t)
                let R2 = P2

                let q1 = new QuadraticBezier(Q0, Q1, Q2)
                let q2 = new QuadraticBezier(R0, R1, R2)

                return [q1, q2]
            } else {
                return [this]
            }
        }
        else if (arguments.length > 1) {
            const t = Array.from(arguments)
            if (t.every(e => this.valid(e))) {
                return t.toSorted((a, b) => a - b).reduce((s, e, i, a) => {
                    const prev = a[i - 1] ?? 0
                    const T = (e - prev) / (1 - prev)
                    s.push(...s.pop().split(T))
                    return s
                }, [this])
            }
        }

    }
    toCubic() {
        const [P0, P1, P2] = this.points

        const Q0 = P0
        const Q1 = Curve.Lerp([P0, P1], 2 / 3)
        const Q2 = Curve.Lerp([P2, P1], 2 / 3)
        const Q3 = P2

        return new CubicBezier(Q0, Q1, Q2, Q3)
    }
    // 求极值
    extremeY() {
        // TODO: 待完善
    }
    // 求极值
    extremeX() {
        // TODO: 待完善
    }
    valid(t) {
        return Utils.isNumber(t) && t >= 0 && t <= 1
    }
    equals(r) {
        if (r.constructor === this.constructor) {
            const same1 = r.points.every((e, i) => e.equals(this.points.at(i))) // 完全一致
            const same2 = r.points.toReversed().every((e, i) => e.equals(this.points.at(i))) // 形状一致，控制点相反
            return same1 || same2
        }
        return false
    }
    contains(p) {
        const [x, y] = p
        const l = new Line(p, [x + 1, y])
        const intersections = this.intersect(l)
        const points = intersections.map(t => new Point(this.lerp(t[0])))
        return points.some(e => e.equals(new Point(p)))
    }
    segmentation(segs = 20) {
        const result = []
        for (let i = 0; i <= segs; i++) {
            if (i != segs) {
                const p1 = this.lerp(i / segs)
                const p2 = this.lerp((i + 1) / segs)
                const ls = new LineSeg(p1, p2)
                result.push(ls)
            }
        }
        return result
    }
    tangent(t) {
        const [[x1, y1], [x2, y2], [x3, y3]] = this.points.map(p => p.valueOf())

        const ax = (x1 - 2 * x2 + x3) * 2
        const bx = -2 * x1 + 2 * x2
        const px = new Polynomial(ax, bx)
        const dx = px.valueOf(t)

        const ay = (y1 - 2 * y2 + y3) * 2
        const by = -2 * y1 + 2 * y2
        const py = new Polynomial(ay, by)
        const dy = py.valueOf(t)

        return vector2.normalize([dx, dy])
    }
}

// 三次贝塞尔曲线
export class CubicBezier extends Curve {
    constructor(p1, p2, p3, p4) {
        super(p1, p2, p3, p4)
    }
    intersect(curve) {
        // let t1 = new Date().valueOf()
        let result = []
        // 如果包围盒不想交则返回，加速相交检测
        if (
            (
                curve instanceof LineSeg||
                curve instanceof QuadraticBezier ||
                curve instanceof CubicBezier
            )   && !this.isBoundingBoxOverlapped(curve)
            
        ) {
            return result
        }
        // 如果端点近似相等，则返回，加速相交检测
        let endPointEqual = false
        const [p1, p2] = this.endPoints()
        const [p3, p4] = curve.endPoints()
        if (p1.equals(p3)) {
            endPointEqual = true
            result.push([0, 0])
        }
        if (p1.equals(p4)) {
            endPointEqual = true
            result.push([0, 1])
        }
        if (p2.equals(p3)) {
            endPointEqual = true
            result.push([1, 0])
        }
        if (p2.equals(p4)) {
            endPointEqual = true
            result.push([1, 1])
        }

        if (endPointEqual) {
            return result
        }

        if (curve instanceof Line) {
            result = curve.intersect(this).map(e => e.toReversed())
        }
        else if (curve instanceof QuadraticBezier) {
            result = curve.intersect(this).map(e => e.toReversed())
        }
        else if (curve instanceof CubicBezier) {
            // console.log('christ')
            const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(p => p.valueOf())
            const [[x5, y5], [x6, y6], [x7, y7], [x8, y8]] = curve.points.map(p => p.valueOf())

            const a0 = -x1 + 3 * x2 - 3 * x3 + x4
            const b0 = 3 * x1 - 6 * x2 + 3 * x3
            const c0 = -3 * x1 + 3 * x2
            const d0 = x1

            const a1 = -y1 + 3 * y2 - 3 * y3 + y4
            const b1 = 3 * y1 - 6 * y2 + 3 * y3
            const c1 = -3 * y1 + 3 * y2
            const d1 = y1

            const a2 = -x5 + 3 * x6 - 3 * x7 + x8
            const b2 = 3 * x5 - 6 * x6 + 3 * x7
            const c2 = -3 * x5 + 3 * x6
            const d2 = x5

            const a3 = -y5 + 3 * y6 - 3 * y7 + y8
            const b3 = 3 * y5 - 6 * y6 + 3 * y7
            const c3 = -3 * y5 + 3 * y6
            const d3 = y5

            // 根据t2的系数结式求得t1
            const t1 = getEliminantEquation([
                [[a2], [b2], [c2], [-a0, -b0, -c0, d2 - d0]],
                [[a3], [b3], [c3], [-a1, -b1, -c1, d3 - d1]]
            ])
            const t2 = getEliminantEquation([
                [[a0], [b0], [c0], [-a2, -b2, -c2, d0 - d2]],
                [[a1], [b1], [c1], [-a3, -b3, -c3, d1 - d3]]
            ])

            result = t1.map(e => {
                const p1 = this.lerp(e)
                const find = t2.find(f => {
                    const p2 = curve.lerp(f)
                    return p1.equals(p2)
                })
                return [e, find]
            })
        }
        // window.count ??= 0
        // window.count++
        // console.log(
        //     'intersect count: ',
        //     window.count,
        //     this.points.map(e => e.valueOf()),
        //     curve.points.map(e => e.valueOf())
        // )
        // let t2 = new Date().valueOf()
        // console.log('%cintersect cost: ', 'color:rgb(255,0,80)', (t2 - t1) + ' ms', this.points, curve.points)
        // final
        return result.filter(e => {
            return Array.isArray(e) && e.length == 2 && this.valid(e[0]) && curve.valid(e[1])
        })
    }
    // 分割，参数不可以是数组
    split() {
        if (arguments.length == 1) {
            const t = arguments[0]
            if (isNumber(t) && t > 0 && t < 1) {
                const [P0, P1, P2, P3] = this.points

                // 计算中间点
                let Q0 = P0;
                let Q1 = Curve.Lerp([P0, P1], t,)
                let Q2 = Curve.Lerp([P0, P1, P2], t,)
                let Q3 = Curve.Lerp([P0, P1, P2, P3], t,)

                let R0 = Curve.Lerp([P3, P2, P1, P0], 1 - t,)
                let R1 = Curve.Lerp([P3, P2, P1], 1 - t,)
                let R2 = Curve.Lerp([P3, P2], 1 - t,)
                let R3 = P3

                let c1 = new CubicBezier(Q0, Q1, Q2, Q3)
                let c2 = new CubicBezier(R0, R1, R2, R3)

                return [c1, c2]
            }
        }
        else if (arguments.length > 1) {
            const t = Array.from(arguments)
            if (t.every(e => this.valid(e))) {
                return t.filter((e, i, a) => a.indexOf(e) == i).toSorted((a, b) => a - b).reduce((s, e, i, a) => {
                    const prev = a[i - 1] ?? 0
                    const T = (e - prev) / (1 - prev)
                    s.push(...s.pop().split(T))
                    return s
                }, [this])
            }
        }
        return [this]
    }
    // y极值(包含端点)
    extremeY() {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(e => e.valueOf())
        const a = -y1 + 3 * y2 - 3 * y3 + y4
        const b = 3 * y1 - 6 * y2 + 3 * y3
        const c = -3 * y1 + 3 * y2
        const d = y1
        const A = 3 * a
        const B = 2 * b
        const C = c
        const t = Math.abs(A) > EPSILON ? solveQuadraticEquation(A, B, C) : (-C / B)
        const p = new Polynomial(a, b, c, d)
        const values = [0, 1, t].flat()
        return values.filter((e, i, a) => a.indexOf(e) == i && e >= 0 && e <= 1)
    }
    // x极值(包含端点)
    extremeX() {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(e => e.valueOf())
        const a = -x1 + 3 * x2 - 3 * x3 + x4
        const b = 3 * x1 - 6 * x2 + 3 * x3
        const c = -3 * x1 + 3 * x2
        const d = x1
        const A = 3 * a
        const B = 2 * b
        const C = c
        const t = solveQuadraticEquation(A, B, C)
        const p = new Polynomial(a, b, c, d)
        const values = [0, 1, t].flat()
        return values.filter((e, i, a) => a.indexOf(e) == i && e >= 0 && e <= 1)
    }
    valid(t) {
        return Utils.isNumber(t) && t >= 0 && t <= 1
    }
    equals(r) {
        if (r.constructor === this.constructor) {
            const same1 = r.points.every((e, i) => e.equals(this.points.at(i))) // 完全一致
            const same2 = r.points.toReversed().every((e, i) => e.equals(this.points.at(i))) // 形状一致，控制点相反
            return same1 || same2
        }
        return false
    }
    contains(p) {
        const [x, y] = p
        const l = new Line(p, [x + 1, y])
        const intersections = this.intersect(l)
        const points = intersections.map(t => new Point(this.lerp(t[0])))
        return points.some(e => e.equals(new Point(p)))
    }
    segmentation(segs = 20) {
        const result = []
        for (let i = 0; i <= segs; i++) {
            if (i != segs) {
                const p1 = this.lerp(i / segs)
                const p2 = this.lerp((i + 1) / segs)
                const ls = new LineSeg(p1, p2)
                result.push(ls)
            }
        }
        return result
    }
    // 切线
    tangent(t) {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(p => p.valueOf())

        const a0 = parseFloat(-x1 + 3 * x2 - 3 * x3 + x4)
        const b0 = parseFloat(3 * x1 - 6 * x2 + 3 * x3)
        const c0 = parseFloat(-3 * x1 + 3 * x2)
        const d0 = parseFloat(x1)

        const px = new Polynomial(a0 * 3, b0 * 2, c0 * 1)
        let dx = px.valueOf(t)

        const a1 = parseFloat(-y1 + 3 * y2 - 3 * y3 + y4)
        const b1 = parseFloat(3 * y1 - 6 * y2 + 3 * y3)
        const c1 = parseFloat(-3 * y1 + 3 * y2)
        const d1 = parseFloat(y1)

        const py = new Polynomial(a1 * 3, b1 * 2, c1 * 1)
        let dy = py.valueOf(t)

        if (t == 1) {
            if (y3 == y4) {
                dy == 0
                if (x3 == x4) {
                    dx == 0
                    return this.tangent(t - 1e-8) // 不可导 
                }
            }
        }

        if (t == 0) {
            if (y1 == y2) {
                dy == 0
                if (x1 == x2) {
                    dx == 0
                    return this.tangent(t + 1e-8) // 不可导 
                }
            }
        }

        // if (dx == 0 && t == 0) {
        //     return this.tangent(t + 1e-6) // 不可导
        // } else if (dy == 0 && t == 1) {
        //     return this.tangent(t - 1e-6) // 不可导
        // } else {
        //     return Array.from(vector2.normalize([dx, dy]))
        // }
        return Array.from(vector2.normalize([dx, dy]))

    }
    // 法线
    normal(t, rotation = -90) {
        const tan = this.tangent(t)
        const d = this.derivative(t)
        const d2 = this.derivative2(t)
        const nor = vector2.rotate(tan, [0, 0], radians(rotation))
        return nor
    }
    // 一阶导数
    derivative(t) {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(p => p.valueOf())

        const a0 = -x1 + 3 * x2 - 3 * x3 + x4
        const b0 = 3 * x1 - 6 * x2 + 3 * x3
        const c0 = -3 * x1 + 3 * x2
        const d0 = x1

        const px = new Polynomial(a0 * 3, b0 * 2, c0 * 1)
        const dx = px.valueOf(t)

        const a1 = -y1 + 3 * y2 - 3 * y3 + y4
        const b1 = 3 * y1 - 6 * y2 + 3 * y3
        const c1 = -3 * y1 + 3 * y2
        const d1 = y1

        const py = new Polynomial(a1 * 3, b1 * 2, c1 * 1)
        const dy = py.valueOf(t)

        return dy / dx
    }
    // 一阶导数求反
    aDerivative(d) {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(p => p.valueOf())

        const a0 = -x1 + 3 * x2 - 3 * x3 + x4
        const b0 = 3 * x1 - 6 * x2 + 3 * x3
        const c0 = -3 * x1 + 3 * x2
        const d0 = x1

        const a1 = -y1 + 3 * y2 - 3 * y3 + y4
        const b1 = 3 * y1 - 6 * y2 + 3 * y3
        const c1 = -3 * y1 + 3 * y2
        const d1 = y1

        const top = new Polynomial(a1 * 3, b1 * 2, c1 * 1) // 分子
        const bot = new Polynomial(a0 * 3, b0 * 2, c0 * 1) // 分母

        if (!isFinite(d)) {
            // const roots = bot.roots()
            // return roots
            return solveQuadraticEquation(a0 * 3, b0 * 2, c0 * 1).filter(e => this.valid(e))
        } else if (d == 0) {
            // const roots = top.roots()
            // return roots
            return solveQuadraticEquation(a1 * 3, b1 * 2, c1 * 1).filter(e => this.valid(e))
        } else {
            // const p = new Polynomial(3 * (a1 - a0 * d), 2 * (b1 - b0 * d), c1 - c0 * d)
            // const roots = p.roots()
            // return roots
            return solveQuadraticEquation(3 * (a1 - a0 * d), 2 * (b1 - b0 * d), c1 - c0 * d).filter(e => this.valid(e))
        }
    }
    // 二阶导数
    derivative2(t) {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(p => p.valueOf())

        const a0 = -x1 + 3 * x2 - 3 * x3 + x4
        const b0 = 3 * x1 - 6 * x2 + 3 * x3
        const c0 = -3 * x1 + 3 * x2
        const d0 = x1

        const a1 = -y1 + 3 * y2 - 3 * y3 + y4
        const b1 = 3 * y1 - 6 * y2 + 3 * y3
        const c1 = -3 * y1 + 3 * y2
        const d1 = y1

        const dy = new Polynomial(a1 * 3, b1 * 2, c1 * 1) // 分子
        const dx = new Polynomial(a0 * 3, b0 * 2, c0 * 1) // 分母
        const dy2 = new Polynomial(a1 * 6, b1 * 2)
        const dx2 = new Polynomial(a0 * 6, b0 * 2)

        // (dy2*dx-dy*dx2)/dx**2

        return dy2.mul(dx).sub(dy.mul(dx2)).valueOf(t) / dx.valueOf(t) ** 2
    }
    // 二阶导数求反
    aDerivative2(v) {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(p => p.valueOf())

        const a0 = -x1 + 3 * x2 - 3 * x3 + x4
        const b0 = 3 * x1 - 6 * x2 + 3 * x3
        const c0 = -3 * x1 + 3 * x2
        const d0 = x1

        const a1 = -y1 + 3 * y2 - 3 * y3 + y4
        const b1 = 3 * y1 - 6 * y2 + 3 * y3
        const c1 = -3 * y1 + 3 * y2
        const d1 = y1

        const dy = new Polynomial(a1 * 3, b1 * 2, c1 * 1) // 分子
        const dx = new Polynomial(a0 * 3, b0 * 2, c0 * 1) // 分母
        const dy2 = new Polynomial(a1 * 6, b1 * 2)
        const dx2 = new Polynomial(a0 * 6, b0 * 2)

        // (dy2*dx-dy*dx2)/dx**2

        const p1 = dy2.mul(dx).sub(dy.mul(dx2))

        return p1.roots().filter(t => Math.abs(this.derivative2(t)) < 1e-6)
    }
    // 单调分割
    splitMonotonously() {
        const t = this.aDerivative2()
        if (t.length != 0) {
            // t.forEach(e => {
            //     console.log('单调分割:', this, e, this.lerp(e), this.derivative(e), this.derivative2(e))
            // })
            const pieces = this.split(...t)
            console.log('单调分割:', this, t, pieces)
            return pieces
        } else {
            return [this]
        }
    }
    /**
     * 求边框曲线
     * @param {*} w 宽度
     * @param {*} start 递归参数
     * @param {*} end 递归参数
     * @param {*} outlines 递归参数，缓存结果
     * @returns 
     */
    offset(w = 10, rotation = -90, start = 0, end = 1, outlines = []) {
        let curr,
            rest

        if (end == 1) {
            curr = this
        } else {
            const pieces = this.split(end)
            curr = pieces[0]
            rest = pieces[1]
        }
        const tan0 = curr.tangent(0)
        const tan1 = curr.tangent(1)
        const nor0 = curr.normal(0, rotation)
        const nor1 = curr.normal(1, rotation)

        const [p1, p2, p3, p4] = curr.points
        const [x1, y1] = p1.valueOf()
        const [x2, y2] = p2.valueOf()
        const [x3, y3] = p3.valueOf()
        const [x4, y4] = p4.valueOf()

        const p5 = p1.translate(nor0, w)  // 边框起点
        const p8 = p4.translate(nor1, w)  // 边框终点

        // 点的x坐标的多项式系数
        const a0 = -x1 + 3 * x2 - 3 * x3 + x4
        const b0 = 3 * x1 - 6 * x2 + 3 * x3
        const c0 = -3 * x1 + 3 * x2
        const d0 = x1

        // 点的y坐标的多项式系数
        const a1 = -y1 + 3 * y2 - 3 * y3 + y4
        const b1 = 3 * y1 - 6 * y2 + 3 * y3
        const c1 = -3 * y1 + 3 * y2
        const d1 = y1

        const dx = new Polynomial(a0 * 3, b0 * 2, c0 * 1)
        const dy = new Polynomial(a1 * 3, b1 * 2, c1 * 1)

        // 起点
        const points = [p5]
        // 控制点1
        {
            if (p1.equals(p2)) {
                points.push(p5)
            } else {
                const dy2 = new Polynomial(a1, b1, c1, d1 - y2)
                const dx2 = new Polynomial(a0, b0, c0, d0 - x2)
                const t = dx.mul(dx2).add(dy.mul(dy2)).roots().filter(e => e != 0)
                if (t.length != 0) {
                    const l1 = new Line(curr.lerp(t[0]), p2) // 法线
                    const l2 = new Line(p5, p5.translate(tan0, 1))
                    const x = l1.intersect(l2)
                    if (x.length != 0) {
                        const p6 = l2.lerp(x[0][1])
                        points.push(p6)
                    } else {
                        const p6 = p2.translate(nor0, w)
                        points.push(p6)
                        // return outlines // 像这种极端情况，4个点距离非常近，可以忽略
                    }
                } else {
                    points.push(p5)
                }
            }

        }
        // 控制点2
        {
            if (p3.equals(p4)) {
                points.push(p8)
            } else {
                const dy3 = new Polynomial(a1, b1, c1, d1 - y3)
                const dx3 = new Polynomial(a0, b0, c0, d0 - x3)
                const t = dx.mul(dx3).add(dy.mul(dy3)).roots().filter(e => e != 0)
                if (t.length != 0) {
                    const l1 = new Line(curr.lerp(t[0]), p3)
                    const l2 = new Line(p8, p8.translate(tan1, 1))
                    const x = l1.intersect(l2)
                    if (x.length != 0) {
                        const p7 = l2.lerp(x[0][1])
                        points.push(p7)
                    } else {
                        const p7 = p3.translate(nor1, w)
                        points.push(p7)
                        // return outlines // 像这种极端情况，4个点距离非常近，可以忽略
                    }
                } else {
                    points.push(p8)
                }
            }

        }
        // 终点
        points.push(p8)

        const outline = new CubicBezier(...points)

        /**
         * 检查边框平均误差是否在允许精度内
         * @param {*} c 三次贝塞尔曲线
         * @param {*} outline 边框曲线
         * @param {*} width 边框宽度
         * @param {*} rotation 方向
         * @param {*} num 采样数量(默认值:10)
         * @param {*} tolerance 误差精度(默认值:宽度的0.05)
         * @returns 
         */
        function errorCheck(c, outline, width, rotation, num = 10, tolerance = 0.05) {
            if (c.length() < 1e-3) {
                console.log('christttttttttttttt')
                return true
            }
            let error = 0
            for (let i = 1; i < num; i++) {
                const t1 = i / num
                const d = c.derivative(t1)
                const p1 = c.lerp(t1).translate(c.normal(t1, rotation), width)
                const t2 = outline.aDerivative(d)
                if (t2.length == 0) {
                    // console.error('serious problem occurs when check outline error')
                    continue
                }
                const p2 = outline.lerp(t2[0])
                const dist = p1.distanceFrom(p2)
                error = (error + dist) / 2
                // console.log('t1:', t1, 'p1:', p1, 'p2:', p2, 'distance:', dist)
            }
            // console.log('average_error:', error / width)
            return error / width <= tolerance
        }

        if (errorCheck(curr, outline, w, rotation)) {
            outlines.push(outline)
            if (end != 1) {
                return rest.offset(w, rotation, 0, 1, outlines)
            } else {
                return outlines
            }
        } else {
            return this.offset(w, rotation, start, (end - start) / 2, outlines)
        }
    }
    outlineShape(borderWidth = 20) {
        console.log('%coutlineShape start', 'color:pink', this.points.map(e => e.valueOf()))
        borderWidth /= 2

        // 单调分割
        const t = this.aDerivative2(0)
        let pieces = this.split(...t)
        console.log('单调分割:', pieces)

        // 曲率分割，并预过滤
        pieces = pieces.flatMap(e => {
            let t = e.aCurvature(1 / borderWidth).filter(t => t != 0 && t != 1)
            console.log('曲率分割点:', e, t)
            let pieces = e.split(...t)
            pieces.forEach((e, i, a) => {
                if (e.length() < 1e-3) {
                    t[i == 0 ? 0 : i - 1] = null
                }
            })
            const t_filtered = t.filter(e => e != undefined)
            return e.split(...t_filtered)
        })
        console.log('曲率分割:', pieces)

        console.log('---- ---- ---- ---- ---- ---- ---- ---- ---- ----')
        let outlineShape = []

        // 1.
        pieces.forEach((e, i, a) => {
            if (i == 0) {
                outlineShape.push(new LineSeg(
                    e.points.at(0),
                    e.points.at(0).translate(e.normal(0, -90), borderWidth)
                ))
            }
            const clockwise = e.isClockwise(0.5)
            const radius = e.radius(0.5)
            if (isNaN(radius)) {
                console.error('very very important')
                return false
            }
            let outlines = []
            if (radius >= borderWidth || clockwise) {
                outlines = e.offset(borderWidth, -90)
            } else {
                outlines = [
                    new LineSeg(
                        e.points.at(0).translate(e.normal(0, -90), borderWidth),
                        e.points.at(-1).translate(e.normal(1, -90), borderWidth)
                    )
                ]
            }

            console.log(`%c${i + 1}/${a.length} -90° ${radius >= borderWidth} ${clockwise}`, 'color:lightblue', outlines)

            outlineShape.push(...outlines)

            if (i == a.length - 1) {
                outlineShape.push(new LineSeg(
                    e.points.at(-1).translate(e.normal(1, -90), borderWidth),
                    e.points.at(-1)
                ))
            }
        })


        // 2.
        pieces.forEach((e, i, a) => {
            if (i == 0) {
                outlineShape.push(new LineSeg(
                    e.points.at(0),
                    e.points.at(0).translate(e.normal(0, 90), borderWidth)
                ))
            }
            const clockwise = e.isClockwise(0.5)
            const radius = e.radius(0.5)
            if (isNaN(radius)) return
            let outlines = []
            if (radius >= borderWidth || !clockwise) {
                outlines = e.offset(borderWidth, 90)
            } else {
                outlines = [
                    new LineSeg(
                        e.points.at(0).translate(e.normal(0, 90), borderWidth),
                        e.points.at(-1).translate(e.normal(1, 90), borderWidth)
                    )
                ]
            }

            console.log(`%c${i + 1}/${a.length} +90° ${radius >= borderWidth} ${!clockwise}`, 'color:lightblue', outlines)
            outlineShape.push(...outlines)

            if (i == a.length - 1) {
                outlineShape.push(new LineSeg(
                    e.points.at(-1).translate(e.normal(1, 90), borderWidth),
                    e.points.at(-1)
                ))
            }
        })

        console.log('%coutlineShape result', 'color:pink', outlineShape.map(e => new e.constructor(...e.points)))
        outlineShape = splitEachOther(outlineShape)
        outlineShape = getCompleteShapes(outlineShape)
        console.log('%coutlineShape final', 'color:pink', outlineShape)
        return outlineShape[0]

    }
    // 曲率
    curvature(t) {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(p => p.valueOf())

        const a0 = -x1 + 3 * x2 - 3 * x3 + x4
        const b0 = 3 * x1 - 6 * x2 + 3 * x3
        const c0 = -3 * x1 + 3 * x2
        const d0 = x1

        const a1 = -y1 + 3 * y2 - 3 * y3 + y4
        const b1 = 3 * y1 - 6 * y2 + 3 * y3
        const c1 = -3 * y1 + 3 * y2
        const d1 = y1

        const dx = new Polynomial(3 * a0, 2 * b0, c0).valueOf(t)    // x'(t)
        const dy = new Polynomial(3 * a1, 2 * b1, c1).valueOf(t)    // y'(t)
        const dx2 = new Polynomial(6 * a0, 2 * b0).valueOf(t)       // x"(t)
        const dy2 = new Polynomial(6 * a1, 2 * b1).valueOf(t)       // y"(t)

        // |x'(t)y"(t)-y'(t)x"(t)| / (x'(t)²+y'(t)²)**(3/2) 曲率公式

        return Math.abs(dx * dy2 - dy * dx2) / (dx ** 2 + dy ** 2) ** (3 / 2)
    }
    // 曲率求反
    aCurvature(v, fixed = 6) {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(p => p.valueOf())

        const a0 = -x1 + 3 * x2 - 3 * x3 + x4
        const b0 = 3 * x1 - 6 * x2 + 3 * x3
        const c0 = -3 * x1 + 3 * x2
        const d0 = x1

        const a1 = -y1 + 3 * y2 - 3 * y3 + y4
        const b1 = 3 * y1 - 6 * y2 + 3 * y3
        const c1 = -3 * y1 + 3 * y2
        const d1 = y1

        const a = new Polynomial(3 * a0, 2 * b0, c0)    // x'(t)
        const b = new Polynomial(3 * a1, 2 * b1, c1)    // y'(t)
        const c = new Polynomial(6 * a0, 2 * b0)       // x"(t)
        const d = new Polynomial(6 * a1, 2 * b1)       // y"(t)

        // |x'(t)y"(t)-y'(t)x"(t)| / (x'(t)²+y'(t)²)**(3/2) 曲率公式

        const e = a.mul(d).sub(b.mul(c))
        const f = a.pow(2).add(b.pow(2))
        const equation = e.pow(2).sub(f.pow(3).mul(v ** 2))
        return equation.roots(0, 1, fixed)
        // return equation.roots(0, 1, fixed).filter(e => 0.5 - Math.abs(e - 0.5) >= 0.01)
    }
    // 半径
    radius(t) {
        return 1 / this.curvature(t)
    }
    // 旋转方向
    isClockwise(t) {
        const [[x1, y1], [x2, y2], [x3, y3], [x4, y4]] = this.points.map(p => p.valueOf())

        const a0 = -x1 + 3 * x2 - 3 * x3 + x4
        const b0 = 3 * x1 - 6 * x2 + 3 * x3
        const c0 = -3 * x1 + 3 * x2
        const d0 = x1

        const a1 = -y1 + 3 * y2 - 3 * y3 + y4
        const b1 = 3 * y1 - 6 * y2 + 3 * y3
        const c1 = -3 * y1 + 3 * y2
        const d1 = y1

        const dx = new Polynomial(3 * a0, 2 * b0, c0).valueOf(t)    // x'(t)
        const dy = new Polynomial(3 * a1, 2 * b1, c1).valueOf(t)    // y'(t)
        const dx2 = new Polynomial(6 * a0, 2 * b0).valueOf(t)       // x"(t)
        const dy2 = new Polynomial(6 * a1, 2 * b1).valueOf(t)       // y"(t)

        // 曲率公式

        // |x'(t)y"(t)-y'(t)x"(t)| / (x'(t)²+y'(t)²)**(3/2)

        const sign = Math.sign((dx * dy2 - dy * dx2) / (dx ** 2 + dy ** 2) ** (3 / 2))

        return sign > 0 ? true : sign < 0 ? false : null
    }
    // 曲率 半径 方向列表
    curvatureList(num = 10) {
        for (let i = 0; i <= num; i++) {
            const t = i / num
            const curvature = this.curvature(t)
            const radius = this.radius(t)
            const isClockwise = this.isClockwise(t)
            console.log(`%c${t.toFixed(3)}`, 'color: yellowgreen', '曲率: ' + curvature.toFixed(4), '顺时针: ' + isClockwise, '半径: ' + radius.toFixed(2))
        }
    }
}

// 相交点切割
export function splitEachOther(curves) {
    // 先去重, 最后再去重
    curves = removeDuplicate(curves)
    curves = curves.reduce((s, c, i) => {
        if (i == 0) {
            return [c]
        } else {
            // s:待分割曲线组, c:当前切割曲线
            // x_c:当前切割线 c 的分割点
            // debugger
            const x_c = s.reduce((r, f, i, a) => {
                // f：当前被分割曲线
                if (!c.equals(f)) {
                    // window.count ??= 0
                    // window.count++
                    // console.log('intersect count: ', window.count)
                    const x = c.intersect(f)
                    // debugger
                    if (x.length !== 0) {
                        // 1. 当前被切割曲线的分割点
                        const x_f = x.map(e => {
                            const [t0, t1] = e
                            // 如果不是端点
                            if (t0 != 0 && t0 != 1) {
                                r.push({ value: t0, substitute: c.lerp(t0) })
                            }
                            if (t1 != 0 && t1 != 1) {
                                return { value: t1, substitute: c.lerp(t0) }
                            }
                        }).filter(e => e).toSorted((a, b) => a.value - b.value)

                        // 2. 一条曲线多个分割点，需要使用split方法，同时分割，不能逐一分割
                        // 在不改变待分割曲线组（s）的长度的前提下, 将当前被分割曲线（f）替换为分割后的曲线片段组
                        if (x_f.length !== 0) {
                            // 分割
                            const curves = f.split(...x_f.map(e => e.value))
                            // 分割点替换
                            curves.forEach((e, i, a) => {
                                e.pathId = f.pathId
                                if (i != 0) {
                                    e.points.splice(0, 1, x_f[i - 1].substitute)
                                }
                                if (i != a.length - 1) {
                                    e.points.splice(-1, 1, x_f[i].substitute)
                                }
                            })
                            a[i] = curves
                        }
                    }
                }
                return r
            }, []).filter((e, i, a) => a.findIndex(f => f.value == e.value) == i).toSorted((a, b) => a.value - b.value)

            // 将分割后的c片段添加到待分割曲线组
            if (x_c.length !== 0) {
                // 分割
                const curves = c.split(...x_c.map(e => e.value))
                // 分割点替换
                curves.forEach((e, i, a) => {
                    e.pathId = c.pathId
                    if (i != 0) {
                        e.points.splice(0, 1, x_c[i - 1].substitute)
                    }
                    if (i != a.length - 1) {
                        e.points.splice(-1, 1, x_c[i].substitute)
                    }
                })
                curves.forEach(e => {
                    s.push(e)
                })
            } else {
                s.push(c)
            }
            // 将二维数组展开为一维数组, 并去重，合并id
            return removeDuplicate(s.flat())
        }

    }, [])
    curves = removeDuplicate(curves)
    curves = removeInvalid(curves)
    return curves
}

// 去重, 合并id
function removeDuplicate(curves) {
    // return curves.filter((e, i, a) => {
    //     let index = a.findIndex(f => f.equals(e))
    //     if (index == i) {
    //         return true
    //     } else {
    //         if (Array.isArray(e.pathId)) {
    //             a[index].pathId ??= []
    //             a[index].pathId.push(...e.pathId)
    //             a[index].pathId = Array.from(new Set(a[index].pathId))
    //         }
    //         return false
    //     }
    // })
    curves.forEach((e, i, a) => {
        let index = a.findIndex(f => f.equals(e))
        if (index != i) {
            a[index].pathId ??= []
            e.pathId ??= []
            let pathId = [...a[index].pathId, ...e.pathId]
            a[index].pathId = Array.from(new Set(pathId))
            e.deleted = true
        }
    })
    curves = curves.filter(e => !e.deleted)
    return curves
}

// 去除首尾重合的
function removeInvalid(curves) {
    return curves.filter(e => !e.points.at(0).equals(e.points.at(-1)))
}