import initWebGPU from './initWebGPU'
import Scene from './Scene'
// import Panel from './Panel'
import Text from './Text'
import Test from './Test'
import Common from './Common'
import Rasterizer from './Rasterizer'

import ViewController from './ViewController'
import Coordinate from './Coordinate'
import Triangle from './triangle'
import vector3 from './vector3'
import matrix4 from './matrix4'
import util from './util'

export default class Aurora {
    constructor() {
        this.init()
    }
    async init() {
        const webGPUContext = window.WEBGPU = await initWebGPU('myCanvas')
        Object.assign(this, webGPUContext)
        this.scene = new Scene(this)
        this.common = new Common(this)
        await this.common.init()
        this.ViewController = new ViewController({
            scene: this.scene,
            ele: this.canvas
        })
        this.rasterizer = new Rasterizer(this)
        await this.rasterizer.init()
        this.coordinate = new Coordinate(this)
        await this.coordinate.init()
        this.triangle = new Triangle(this)
        // this.panel = new Panel(this)
        // await this.panel.init()
        // this.text = new Text(this)
        // await this.text.init()
        this.test = new Test(this)
        await this.test.init()
        // 启动渲染循环
        requestAnimationFrame(this.render.bind(this))
        // setInterval(() => {
        //     this.scene.instances[1].rotation[1] = this.scene.instances[1].rotation[1] + 1
        // }, 10);
        // util.print('scene', this.scene)
    }
    // 渲染循环
    render(timestamp) {
        // 在每一帧开始时创建一个命令编码器
        const commandEncoder = this.device.createCommandEncoder()

        // 执行图形操作，将命令添加到编码器中
        this.common.update(timestamp)
        this.rasterizer.runPass(commandEncoder, timestamp)
        this.coordinate.runPass(commandEncoder, this.rasterizer.frameBuffer.createView())
        // this.triangle.runPass(commandEncoder, this.rasterizer.frameBuffer.createView())
        // this.panel.runPass(commandEncoder, this.rasterizer.frameBuffer.createView())
        // this.text.runPass(commandEncoder, this.rasterizer.frameBuffer.createView())
        this.test.runPass(commandEncoder, this.rasterizer.frameBuffer.createView())
        // 提交命令编码器并在屏幕上渲染
        const commandBuffer = commandEncoder.finish()
        this.device.queue.submit([commandBuffer])

        requestAnimationFrame(this.render.bind(this))
    }
}