// 数组类扩展
// Array entends

// 类树结构数组遍历
Array.prototype.forEachDeep = function (func, childrenName = 'children') {
    if (this.length != 0) {
        this.forEach(function loop(e) {
            func(e)
            if (Array.isArray(e[childrenName])) {
                e[childrenName].forEachDeep(func)
            }
        }.bind(this))
    }
}
Array.prototype.pushWhile = function () {
    const [condition, ...args] = Array.from(arguments)
    if (condition) {
        this.push(...args)
    }
}
Array.prototype.split = function (size) {
    const result = []
    let temp = []
    this.forEach((e, i, a) => {
        temp.push(e)
        if (temp.length == size) {
            result.push(temp)
            temp = []
        }
    })
    return result
}

// 将一维数组转为指定大小的二维数组
export function splitTypedArray(arr, chunkSize) {
    // 计算拆分后的数组个数
    const numChunks = Math.ceil(arr.length / chunkSize)

    // 存储拆分后的类型化数组
    const splittedArrays = []

    // 迭代拆分
    for (let i = 0; i < numChunks; i++) {
        const start = i * chunkSize
        const end = start + chunkSize

        // 使用 slice 方法拆分数组
        const chunk = arr.slice(start, end)

        // 将拆分的数组添加到结果数组中
        splittedArrays.push(chunk)
    }
    return splittedArrays
}

export function splitArrayByElement(array, delimiter) {
    const result = [];
    let currentArray = [];

    for (const item of array) {
        if (item === delimiter) {
            result.push(currentArray);
            currentArray = [];
        } else {
            currentArray.push(item);
        }
    }

    if (currentArray.length > 0) {
        result.push(currentArray);
    }

    return result;
}

export function forEachDeep(arr, func) {
    if (Array.isArray(arr) && arr.length != 0) {
        arr.forEach(e => {
            func(e)
            forEachDeep(e.children, func)
        })
    }
}
const generatedUUIDs = new Set();

export function createUUID() {
    let uuid;
    do {
        uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
            const r = (Math.random() * 16) | 0;
            const v = c === 'x' ? r : (r & 0x3) | 0x8;
            return v.toString(16);
        });
    } while (generatedUUIDs.has(uuid));

    generatedUUIDs.add(uuid);
    return uuid;
}

let id = 0

export function createID() {
    return ++id
}

export function isNumber(value) {
    return typeof value === 'number' && !isNaN(value)
}