export class Animation {
    constructor({
        duration = 1000,
        repeat = false,
        delay = 0,
        bezier = t => t, // linear
        startTime = null,
        action = t => { },
        onFinish = () => { },
    } = {}) {
        this.duration = duration
        this.bezier = bezier
        this.repeat = repeat
        this.startTime = startTime
        this.action = action
        this.onFinish = onFinish
        if (delay != undefined) {
            if (delay === 0) {
                this.start()
                // this.animationFrameId = requestAnimationFrame(this.start.bind(this)) 
                // 上面这种方式也可以，与this.start的区别在于
                // 这个不是立即开始，而是在下一帧刷新的时候才开始
            } else {
                this.timeout = setTimeout(() => {
                    this.start()
                }, delay)
            }
        }
    }
    start(t = performance.now()) {
        // console.log(`%c${t}`, 'color:pink')
        if (!this.startTime) { // ---- 初始化起始帧时间
            this.startTime = t
        } else {
            let passTime = this.passTime = t - this.startTime
            if (passTime - this.duration < 1000 / 60) {
                let p = passTime / this.duration
                if (p > 1) p = 1
                this.action(this.bezier(p))
            }
            else if (passTime - this.duration >= 1000 / 60) {
                if (this.repeat) {
                    let p = passTime / this.duration
                    this.action(this.bezier(p % 1))
                }
            }
            if (Math.abs(passTime - this.duration) <= 1000 / 60) {
                if (!this.repeat) {
                    this.endTime = t
                    this.action(this.bezier(1))
                    this.onFinish && this.onFinish.call(this)
                    return
                }
            }
            // ---- todo 改变动画停止判定条件
        }
        this.animationFrameId = requestAnimationFrame(this.start.bind(this))
    }
    stop() {
        this.timeout && clearTimeout(this.timeout)
        this.animationFrameId && window.cancelAnimationFrame(this.animationFrameId)
    }
}
export const createAnim = async (config = {}, i = 0) => {
    if (Array.isArray(config)) {
        if (config.length === 0 || config.length === i) return
        let anim = await createAnim(config[i])
        { // ---- 为连续动画组的下一个动画设置起始帧时间，为上一组的结束帧时间
            if (config[++i]) {
                if (anim.endTime) {
                    config[i].startTime = anim.endTime
                }
            }
        }
        await createAnim(config, i)
    } else {
        if (config.repeat) {
            new Animation({
                ...config,
                onFinish() {
                    config.onFinish && config.onFinish.call(this)
                }
            })
            return Promise.resolve()
        } else {
            return new Promise(resolve => {
                new Animation({
                    ...config,
                    onFinish() {
                        config.onFinish && config.onFinish.call(this)
                        resolve(this)
                    }
                })
            })
        }
    }
}