import Polynomial from "./Polynomial"

export function mix(e1, e2, t) {
    if (e1 === e2) {
        return e1
    } else {
        return (e1 * (1 - t) + e2 * t)
    }

}
export function clamp(e, low, high) {
    return Math.min(Math.max(e, low), high)
}
export function smoothstep(low, high, x) {
    let t = clamp((x - low) / (high - low), 0.0, 1.0)
    return t * t * (3.0 - 2.0 * t)
}
export function degrees(e1) {
    return e1 * 180 / Math.PI
}
export function radians(e1) {
    return e1 * Math.PI / 180
}
export function normalDegrees(e) {
    e = e % 360
    if (e <= 0) {
        e += 360
    }
    return e
}
// 匀速直线运动
export function move(v, a, t) {
    return v * t + 0.5 * a * t * t
}

// 计算方程组系数结式
export function getEliminantEquation(coefs) {

    // 系数结式
    const coef = coefs.reduce((s, e) => {
        for (let i = 0; i < e.length - 1; i++) {
            const a = [...e]
            for (let k = 0; k < e.length - 2; k++) {
                a.push([0])
            }
            for (let j = 0; j < i; j++) {
                a.unshift(a.pop())
            }
            s.push(a)
        }
        return s
    }, [])

    // 生成指定长度自然排列
    const a = []
    for (let i = 0; i < (coefs[0].length - 1) * 2; i++) {
        a.push(i)
    }

    // 枚举所有排列组合
    const arrays = permute(a)

    // 将结式转为多项式
    const p = arrays.reduce((s, e) => {
        const inversion = countInversions(e)
        const pi = new Polynomial([Math.pow(-1, inversion)])
        const p = e.reduce((t, f, i) => t.multiple(new Polynomial(coef[i][f])), new Polynomial([1])).multiple(pi)
        return s.add(p)
    }, new Polynomial([0]))

    return p.roots()
}

// 枚举n阶所有排列组合

// 使用示例
// const elements = [1, 2, 3, 4];  // 可以换成任何你想要的数组
// const permutations = permute(elements);
// console.log(permutations);
export function permute(arr) {
    const results = [];

    function permuteRecursive(subArr, currentArr) {
        if (currentArr.length === arr.length) {
            results.push(currentArr.slice());
            return;
        }

        for (let i = 0; i < subArr.length; i++) {
            const newCurrentArr = currentArr.concat(subArr[i]);
            const newSubArr = subArr.slice(0, i).concat(subArr.slice(i + 1));
            permuteRecursive(newSubArr, newCurrentArr);
        }
    }

    permuteRecursive(arr, []);
    return results;
}

// 计算一组数字排列的逆序数：

// 使用示例
// const arr = [3, 1, 2, 4];
// const inverseCount = countInversions(arr);
// console.log(`排列 [${arr}] 的逆序数是: ${inverseCount}`);

export function countInversions(arr) {
    let inversions = 0;
    const n = arr.length;

    for (let i = 0; i < n; i++) {
        for (let j = i + 1; j < n; j++) {
            if (arr[i] > arr[j]) {
                inversions++;
            }
        }
    }

    return inversions;
}

// 复数类
export class Complex {
    constructor(
        re = 0,
        im = 0
    ) {
        this.re = re
        this.im = im
    }
    add(c) {
        if (c instanceof Complex) {
            return new Complex(
                this.re + c.re,
                this.im + c.im
            )
        }
    }
    mul(e) {
        if (e instanceof Complex) {
            return new Complex(
                this.re * e.re - this.im * e.im,
                this.re * e.im + this.im * e.re
            )
        }
    }
    cbrt() {
        const r = Math.hypot(this.re, this.im)
        const theta = Math.atan2(this.im, this.re) // 夹角
        const roots = []
        for (let k = 0; k < 3; k++) {
            const arg = (theta + 2 * k * Math.PI) / 3
            const mag = Math.pow(r, 1 / 3)
            roots.push(new Complex(
                mag * Math.cos(arg),
                mag * Math.sin(arg)
            ))
        }
        return roots
    }
}

/**
 * 求解一元三次方程 ax^3 + bx^2 + cx + d = 0
 * 公式法
 * @param {number} a 三次项系数
 * @param {number} b 二次项系数
 * @param {number} c 一次项系数
 * @param {number} d 常数项
 * @returns {Array} 方程的实数根
 */
// 示例用法：
// console.log('roots: ', solveCubicEquation(1, -2, -5, 6)); // [1, -2, 3]
export function solveCubicEquation(a, b, c, d) {
    // 将方程化为标准形式 x^3 + px^2 + qx + r = 0
    const p = b / a
    const q = c / a
    const r = d / a
    // 计算判别式
    const m = p * q / 6 - (p / 3) ** 3 - r / 2
    const n = q / 3 - (p / 3) ** 2
    const discriminant = m ** 2 + n ** 3

    // 判别式为正，方程有三个实数根
    // console.log('discriminant: ', discriminant)
    const roots = []
    if (discriminant >= 0) {
        // console.log('∆ >= 0')
        const i = new Complex(-1 / 2, Math.sqrt(3) / 2)
        const j = new Complex(-1 / 2, -Math.sqrt(3) / 2)
        const u = new Complex(m + Math.sqrt(discriminant)).cbrt()
        const v = new Complex(m - Math.sqrt(discriminant)).cbrt()
        const w = new Complex(-p / 3)
        u.forEach(e => v.forEach(f => roots.push(e.add(f).add(w))))
        u.forEach(e => v.forEach(f => roots.push(e.mul(i).add(f.mul(j)).add(w))))
        u.forEach(e => v.forEach(f => roots.push(e.mul(j).add(f.mul(i)).add(w))))
    }
    else {
        // console.log('∆ < 0')
        const i = new Complex(-1 / 2, Math.sqrt(3) / 2)
        const j = new Complex(-1 / 2, -Math.sqrt(3) / 2)
        const u = new Complex(m, Math.sqrt(-discriminant)).cbrt()
        const v = new Complex(m, -Math.sqrt(-discriminant)).cbrt()
        const w = new Complex(-p / 3)
        u.forEach(e => v.forEach(f => roots.push(e.add(f).add(w))))
        u.forEach(e => v.forEach(f => roots.push(e.mul(i).add(f.mul(j)).add(w))))
        u.forEach(e => v.forEach(f => roots.push(e.mul(j).add(f.mul(i)).add(w))))
    }
    // console.log(roots)
    // return Array.from(new Set((roots.filter(e => Math.abs(e.im) < 1e-10).map(e => Number(e.re.toFixed(3))))))
    return Array.from(new Set((roots.filter(e => Math.abs(e.im) < 1e-10).map(e => Number(e.re)))))
}

/**
 * 求解一元三次方程 ax^3 + bx^2 + cx + d = 0 
 * 推导法：(B站:BV187411373B)
 * @param {number} a 三次项系数
 * @param {number} b 二次项系数
 * @param {number} c 一次项系数
 * @param {number} d 常数项
 * @returns {Array} 方程的实数根
 */
export function solveCubicEquation2(a, b, c, d) {
    const roots = []
    const B = b / a
    const C = c / a
    const D = d / a
    const p = C - 1 / 3 * B ** 2
    const q = 2 / 27 * B ** 3 - 1 / 3 * B * C + D
    const w = new Complex(-B / 3)
    const delta = q ** 2 + 4 / 27 * p ** 3
    if (delta >= 0) {
        // console.log('∆ >= 0')
        const u = new Complex(-q / 2 + Math.sqrt(delta) / 2).cbrt()
        const v = new Complex(-q / 2 - Math.sqrt(delta) / 2).cbrt()
        u.forEach(e => v.forEach(f => roots.push(e.add(f).add(w))))
    } else {
        // console.log('∆ < 0')
        const u = new Complex(-q / 2, Math.sqrt(-delta) / 2).cbrt()
        const v = new Complex(-q / 2, - Math.sqrt(-delta) / 2).cbrt()
        u.forEach(e => v.forEach(f => roots.push(e.add(f).add(w))))
    }
    // console.log(roots)
    // return Array.from(new Set((roots.filter(e => Math.abs(e.im) < 1e-10).map(e => Number(e.re.toFixed(3))))))
    return Array.from(new Set((roots.filter(e => Math.abs(e.im) < 1e-10).map(e => Number(e.re)))))
}

// 一元二次方程精确求解
export function solveQuadraticEquation(a, b, c) {
    const roots = []
    const delta = b ** 2 - 4 * a * c
    if (a == 0) {
        roots.push(- c / b)
    } else {
        if (delta > 0) {
            roots.push((- b + Math.sqrt(delta)) / a / 2, (- b - Math.sqrt(delta)) / a / 2)
        } else if (delta == 0) {
            roots.push(- b / a / 2)
        }
    }
    return roots
}

// 二元一次方程组
export function solveEquations(coefs) {
    const [[a, b, c], [d, e, f]] = coefs
    const x1 = (b * f - c * e) / (a * e - b * d)
    const x2 = (c * d - a * f) / (a * e - b * d)
    return [x1, x2]
}

/**
 * 数值二分法
 * @param {*} calc 计算函数
 * @param {*} a 最小值
 * @param {*} b 最大值
 * @param {*} tolerance 容差 
 * @returns 
 */
export function bisection(calc, a, b, tolerance = 1e-6) {
    if (Math.abs(a - b) < tolerance) {
        // 2024.10.14 another severe bug fixed
        const v1 = calc(a)
        const v2 = calc(b)
        if (v1 * v2 < 0) {
            return (a + b) / 2
        }
    } else {
        // 可以在这里计算时间复杂度
        const v1 = calc(a)
        const v2 = calc(b)
        if (v1 * v2 < 0) {
            const c = (a + b) / 2
            const v3 = calc(c)
            if (v1 * v3 < 0) {
                return bisection(calc, a, c, tolerance)
            }
            else if (v1 * v3 > 0) {
                return bisection(calc, b, c, tolerance)
            }
            else {
                return c
            }
        }
    }
}