import bufferShader from './shader/radiosity.buffer.wgsl?raw'
import textureShader from './shader/radiosity.texture.wgsl?raw'
import ComputePipeline from './ComputePipeline'
import util from './util'
export default class Radiosity {
    constructor({
        device,
        scene,
        common
    } = {}) {
        this.device = device
        this.scene = scene
        this.common = common

        this.lightmapFormat = 'rgba16float'                                                     // ---- 光照贴图格式
        this.lightmapSize = [256, 256]                                                            // ---- 光照贴图大小    256*256
        this.lightmapTexels = this.lightmapSize[0] * this.lightmapSize[1] * this.scene.triangles.length    // ---- 纹素的总数      256*256*37
        this.accumulationToLightmapWorkgroupSize = [16, 16]                                     // ---- 工作组size

        // ---- 统一缓冲 [uniform buffer]
        this.uniformBuffer = this.device.createBuffer({
            label: 'uniformBuffer',
            size: 8 * 4, // 8 x f32
            usage:
                GPUBufferUsage.UNIFORM |
                GPUBufferUsage.COPY_DST, //可以向缓冲区写入数据writeBuffer
        })

        // ---- 所有纹素的存储缓冲 [storage buffer]
        this.texelBuffer = this.device.createBuffer({
            label: 'texelBuffer',
            size: this.lightmapTexels * 4 * 4, // 一个纹素存4个分量
            usage: GPUBufferUsage.STORAGE
        })
        util.print('texelBuffer', this.texelBuffer.size)

        // ---- 光照贴图 [lightmap texture]
        this.lightmap = this.device.createTexture({
            label: 'lightmap',
            size: {
                width: this.lightmapSize[0],
                height: this.lightmapSize[1],
                depthOrArrayLayers: this.scene.triangles.length,
            },
            format: this.lightmapFormat,
            usage:
                GPUTextureUsage.TEXTURE_BINDING |   // 可以作为采样纹理
                GPUTextureUsage.STORAGE_BINDING,    // 可以作为存储纹理，写入数据
        })

        // ---- 资源组布局 for bindGroup and pipeline
        this.bindGroupLayout = this.device.createBindGroupLayout({
            label: 'bindGroupLayout',
            entries: [
                { buffer: { type: 'uniform' } },
                { buffer: { type: 'storage' } },
                {
                    storageTexture: {
                        access: 'write-only',
                        format: this.lightmapFormat, // required 格式必填
                        viewDimension: '2d-array'
                    }
                },
            ].map((e, i) => {
                e.binding = i
                e.visibility = GPUShaderStage.COMPUTE
                return e
            }),
        })

        // ---- 资源组 for pipeline
        this.bindGroup = this.device.createBindGroup({
            label: 'bindGroup',
            layout: this.bindGroupLayout,
            entries: [
                { resource: { buffer: this.uniformBuffer } },
                { resource: { buffer: this.texelBuffer } },
                { resource: this.lightmap.createView() },
            ].map((e, i) => {
                e.binding = i
                return e
            }),
        })

        // ---- 资源组布局组 for pipeline
        const bindGroupLayouts = [
            this.common.bindGroupLayout,
            this.bindGroupLayout
        ]

        // ---- 计算管线
        this.computePipelines = [
            // ---- 累积缓冲
            new ComputePipeline({
                device: this.device,
                label: 'radiosity',
                bindGroupLayouts,
                code: this.common.shader + bufferShader,
                entryPoint: 'radiosity',
                constants: {
                    WorkgroupSizeX: 256,              // ---- 每个工作组的光子数
                    PhotonEnergy: 1000,                // ---- 光子能量
                },
                workgroups: [1024, 1, 1],             // ---- 每帧的工作组数
            }),
            // ---- 光照贴图
            new ComputePipeline({
                device: this.device,
                label: 'accumulationToLightmapPipeline',
                bindGroupLayouts,
                code: this.common.shader + textureShader,
                entryPoint: 'accumulation_to_lightmap',
                constants: {
                    WorkgroupSizeX: this.accumulationToLightmapWorkgroupSize[0],
                    WorkgroupSizeY: this.accumulationToLightmapWorkgroupSize[1],
                },
                workgroups: [
                    Math.ceil(this.lightmapSize[0] / this.accumulationToLightmapWorkgroupSize[0]),    // 256 / 16
                    Math.ceil(this.lightmapSize[1] / this.accumulationToLightmapWorkgroupSize[1]),    // 256 / 16
                    this.scene.triangles.length
                ],
            })
        ]

    }
    runPass(commandEncoder) {

        // Update the radiosity uniform buffer data.
        this.device.queue.writeBuffer(this.uniformBuffer, 0 * 4, new Float32Array([this.getLightmapScale()]))
        this.device.queue.writeBuffer(this.uniformBuffer, 1 * 4, new Float32Array([this.getTexelBufferScale()]))
        this.device.queue.writeBuffer(this.uniformBuffer, 2 * 4, new Float32Array([this.scene.lightWidth, this.scene.lightHeight]))
        this.device.queue.writeBuffer(this.uniformBuffer, 4 * 4, new Float32Array(this.scene.lightCenter))

        const passEncoder = commandEncoder.beginComputePass()
        passEncoder.setBindGroup(0, this.common.bindGroup)
        passEncoder.setBindGroup(1, this.bindGroup)

        this.computePipelines.forEach(pipeline => {
            pipeline.attachPassEncoder(passEncoder)
        })

        passEncoder.end()
    }
    getLightmapScale() {
        this.mean ??= 0
        // 计算累积缓冲区的新平均值
        const ppl = this.computePipelines[0]
        const cst = ppl.constants
        const photonEnergy = cst.PhotonEnergy
        const workgroupPhotons = cst.WorkgroupSizeX
        const workgroupsX = ppl.workgroups[0]
        const photons = workgroupsX * workgroupPhotons   // ---- 每帧的光子数
        this.mean += photons * photonEnergy / this.lightmapTexels
        return 1 / this.mean    // 通过平均值计算 'accumulation' -> 'lightmap' 的比例
    }
    getTexelBufferScale() {
        const meanMax = 0x11111111;// ---- [8位16进制][32位2进制]最大值
        // 如果平均值大于最大值，则减少“accumulation”缓冲区值以防止u32溢出。
        const scale = this.mean > 2 * meanMax ? 0.5 : 1
        this.mean *= scale
        return scale
    }
    createMappedBuffer({
        size = 0,
        usage = GPUBufferUsage.VERTEX,
        source,
        typedArray = Float32Array
    } = {}) {
        const GPUbuffer = this.device.createBuffer({ size, usage, mappedAtCreation: true })
        new typedArray(GPUbuffer.getMappedRange()).set(source)
        GPUbuffer.unmap()
        return GPUbuffer
    }
}