import shader from './shader/coordinate.wgsl?raw'
import util from './util'
import vector3 from './vector3'
import matrix4 from './matrix4'
// world coordinate system in the top right corner
export default class Coordinate {
    constructor({
        device,
        scene,
        common,
        canvas,
        format,
        context,
    } = {}) {
        this.device = device
        this.scene = scene
        this.common = common
        this.format = format
        this.canvas = canvas
        this.context = context
        this.multisample = true
    }
    async init() {
        this.instances = [
            {
                name: 'x',
                position: [0, 0, 0],
                rotation: [0, 0, 0],
                scale: [1, 1, 1],
                color: [1, 0.2, 0.2],
                mesh: {
                    positions: [
                        [0, 0, 0],
                        [1, 0, 0]
                    ],
                    primitives: [
                        [0, 1]
                    ]
                },
            },
            {
                name: 'y',
                position: [0, 0, 0],
                rotation: [0, 0, 0],
                scale: [1, 1, 1],
                color: [0, 1, 0],
                mesh: {
                    positions: [
                        [0, 0, 0],
                        [0, 1, 0]
                    ],
                    primitives: [
                        [0, 1]
                    ]
                },
            },
            {
                name: 'z',
                position: [0, 0, 0],
                rotation: [0, 0, 0],
                scale: [1, 1, 1],
                color: [0, 0.5, 1],
                mesh: {
                    positions: [
                        [0, 0, 0],
                        [0, 0, 1]
                    ],
                    primitives: [
                        [0, 1]
                    ]
                },
            },
        ]
        this.uniformBuffer = this.device.createBuffer({
            size: (
                4 * 4 // camera view projection matrix [mat4]
            ) * 4,
            usage:
                GPUBufferUsage.UNIFORM |
                GPUBufferUsage.COPY_DST, //可以向缓冲区写入数据writeBuffer
        })
        this.instanceBuffer = this.device.createBuffer({
            label: 'instance',
            size: (
                4 * 4 // tranform matrix [mat4]
            ) * 4 * this.instances.length,
            usage:
                GPUBufferUsage.STORAGE |
                GPUBufferUsage.COPY_DST, //可以向缓冲区写入数据writeBuffer
        })
        this.vertexBufferLayout = [
            {
                arrayStride: (3 + 3 + 2 + 3 + 1 + 1) * 4, // * 4 这里的4是4字节的意思
                attributes: [
                    { shaderLocation: 0, offset: 0 * 4, format: 'float32x3' },  // 3 position
                    { shaderLocation: 1, offset: 3 * 4, format: 'float32x3' },  // 3 normal
                    { shaderLocation: 2, offset: 6 * 4, format: 'float32x2' },  // 2 uv
                    { shaderLocation: 3, offset: 8 * 4, format: 'float32x3' },  // 3 color
                    { shaderLocation: 4, offset: 11 * 4, format: 'float32' },   // 1 emissive
                    { shaderLocation: 5, offset: 12 * 4, format: 'float32' },   // 1 triangle index
                ]
            }
        ]
        this.instances.forEach(instance => {
            const { color, mesh } = instance,
                { positions, primitives } = mesh,
                vertexData = new Float32Array(primitives.map(indice => {
                    return indice.map(i => {
                        return [
                            ...positions[i],        // 3 position
                            0, 0, 0,                // 3 normal
                            0, 0,                   // 2 uv
                            ...color,               // 3 color
                            1,                      // 1 emissive
                            0                       // 1 triangle index
                        ]
                    }).flat()
                }).flat()),
                indexData = new Uint32Array(primitives.map((indice, i) => {
                    return [
                        // i * 3,
                        // i * 3 + 1,
                        // i * 3 + 2
                        i * 2,
                        i * 2 + 1
                    ]
                }).flat())
            instance.vertexBuffer = this.createMappedBuffer({
                size: vertexData.byteLength,
                usage: GPUBufferUsage.VERTEX,
                source: vertexData,
                typedArray: Float32Array
            })
            instance.indexBuffer = this.createMappedBuffer({
                size: indexData.byteLength,
                usage: GPUBufferUsage.INDEX,
                source: indexData,
                typedArray: Uint32Array
            })
        })
        const bindGroupLayoutEntries = [
            {
                binding: 0,
                buffer: { type: 'uniform' },
                visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT
            },
            {
                binding: 1,
                buffer: { type: 'read-only-storage' },
                visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT
            }
        ]
        const bindGroupEntries = [
            { binding: 0, resource: { buffer: this.uniformBuffer } },
            { binding: 1, resource: { buffer: this.instanceBuffer } },
        ]
        this.bindGroupLayout = this.device.createBindGroupLayout({
            entries: bindGroupLayoutEntries
        })
        this.bindGroup = this.device.createBindGroup({
            layout: this.bindGroupLayout,
            entries: bindGroupEntries
        })
        const bindings =
            `\n@group(0) @binding(0) var<uniform> scene: Scene;` +
            `\n@group(0) @binding(1) var<storage> instances: array<Instance>;`
        const code = bindings + shader

        this.pipeline = await this.device.createRenderPipelineAsync({
            layout: this.device.createPipelineLayout({
                bindGroupLayouts: [
                    this.bindGroupLayout
                ]
            }),
            vertex: {
                module: this.device.createShaderModule({ code }),
                entryPoint: 'main_vert',
                buffers: this.vertexBufferLayout,
            },
            fragment: {
                module: this.device.createShaderModule({ code }),
                entryPoint: 'main_frag',
                targets: [{ format: this.format }]
            },
            primitive: {
                topology: 'line-list' // Related settings: drawIndexed, indexBuffer
            },
            multisample: { count: this.multisample ? 4 : 1 }
        })
    }
    writeBuffer() {
        const cameraViewProjectionMatrix = util.getCameraViewProjectionMatrix({
            aspect: this.canvas.width / this.canvas.height,
            eye: vector3.sub(this.scene.eye, this.scene.center),
            center: [0, 0, 0],
            up: this.scene.up,
            fovy: 1,
            near: 0.1,
            far: 100000.0,
        })
        this.device.queue.writeBuffer(this.uniformBuffer, 0, cameraViewProjectionMatrix)

        this.device.queue.writeBuffer(this.instanceBuffer, 0, this.instances.reduce((s, e, i) => {
            e.position = this.invertRasterizeByOffset({
                canvas: this.canvas,
                eye: this.scene.eye,
                center: this.scene.center,
                up: this.scene.up,
                viewProjection: cameraViewProjectionMatrix,
                x: this.canvas.width / 2 - 60, // 平面坐标系屏幕中心坐标[0,0]
                y: this.canvas.height / 2 - 60,
            })
            let view = vector3.sub(this.scene.eye, this.scene.center)
            let scale = vector3.length(view) / this.canvas.height / 120 * 30 // 120是相对于fovy为1得出经验值，1:50为模型长度和屏幕像素长度的比值
            e.scale = [scale, scale, scale]

            s.set(util.getModelTransformMatrix(e), i * 16)
            return s
        }, new Float32Array(this.instanceBuffer.size / 4)))
    }
    invertRasterizeByOffset({
        canvas,
        eye,
        center,
        up = [0, 1, 0],
        viewProjection,
        x,
        y,
    } = {}) {
        eye = vector3.create(eye)
        center = vector3.create(center)
        up = vector3.create(up)
        let view = vector3.sub(eye, center)
        let half_view = vector3.scale(view, 0.5)
        let right = vector3.normalize(vector3.cross(up, view))
        let a = Math.atan(y / x)
        let m = matrix4.fromRotation(view, a)
        let diagonal = vector3.transformMat4(right, m)
        let v = vector3.add(half_view, diagonal)
        let v_rasterized = vector3.transformMat4(v, viewProjection)
        let scale = Math.abs(x) / (canvas.width / 2) / v_rasterized[0]
        let scale2 = Math.abs(y) / (canvas.height / 2) / v_rasterized[0]
        diagonal = vector3.scale(diagonal, scale)
        v = vector3.add(half_view, diagonal)
        return v
    }
    runPass(commandEncoder, output) {
        this.writeBuffer()
        const pass = commandEncoder.beginRenderPass({
            colorAttachments: [
                {
                    view: this.multisample ?
                        output :
                        this.context.getCurrentTexture().createView(),
                    resolveTarget: this.multisample ?
                        this.context.getCurrentTexture().createView() :
                        undefined,
                    clearValue: [0, 0, 0, 1],
                    loadOp: 'load', // load 是叠加，clear 是清除重新画
                    storeOp: 'store',
                }
            ]
        })
        pass.setPipeline(this.pipeline)
        pass.setBindGroup(0, this.bindGroup)
        this.instances.forEach((e, i) => {
            pass.setVertexBuffer(0, e.vertexBuffer)
            pass.setIndexBuffer(e.indexBuffer, 'uint32')
            pass.drawIndexed(e.mesh.primitives.length * 2, 1, 0, 0, i)
        })
        pass.end()
    }
    createMappedBuffer({
        size = 0,
        usage = GPUBufferUsage.VERTEX,
        source,
        typedArray = Float32Array
    } = {}) {
        const GPUbuffer = this.device.createBuffer({ size, usage, mappedAtCreation: true })
        new typedArray(GPUbuffer.getMappedRange()).set(source)
        GPUbuffer.unmap()
        return GPUbuffer
    }
}