
import rasterizerShader from './shader/rasterizer.wgsl?raw'
import BlinnPhongShader from './shader/BlinnPhong.wgsl?raw'
import RayTracingShader from './shader/RayTracing.wgsl?raw'
import ShadowMapper from './ShadowMapper'
import Radiosity from './Radiosity'
import util from './util'
export default class Rasterizer {
    constructor({
        device,
        canvas,
        scene,
        common,
        format,
        context,
    } = {}) {
        this.device = device
        this.canvas = canvas
        this.scene = scene
        this.common = common
        this.format = format
        this.context = context
        this.multisample = true
        // this.init()
    }
    async init() {
        // ---- 深度缓冲，是纹理, 不是用于阴影，是用于光栅化
        this.depthBuffer = this.device.createTexture({
            label: 'depthBuffer',
            size: [this.canvas.width, this.canvas.height],
            format: 'depth24plus-stencil8',
            usage: GPUTextureUsage.RENDER_ATTACHMENT,
            sampleCount: this.multisample ? 4 : 1, // 如果是4，usage必须包含RENDER_ATTACHMENT，并且一定不能包含STORAGE_BINDING
        })
        // ---- 帧缓冲，是纹理
        this.frameBuffer = this.device.createTexture({
            label: 'frameBuffer',
            size: [this.canvas.width, this.canvas.height],
            format: this.format,
            usage: GPUTextureUsage.RENDER_ATTACHMENT | GPUTextureUsage.COPY_SRC,
            sampleCount: this.multisample ? 4 : 1, // 如果是4，usage必须包含RENDER_ATTACHMENT，并且一定不能包含STORAGE_BINDING
        })
        // ---- 光照模型
        if (this.scene.lightingModel == 'Blinn-Phong') {
            this.shadowMapper = new ShadowMapper(this)
            await this.shadowMapper.init()
            this.bindGroupLayoutEntries = [
                { // shadow map
                    binding: 0,
                    texture: { sampleType: 'depth' },
                    visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT | GPUShaderStage.COMPUTE
                },
                { // shadow sampler
                    binding: 1,
                    sampler: { type: 'comparison' },
                    visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT | GPUShaderStage.COMPUTE
                }
            ]
            this.bindGroupEntries = [
                { // shadow map
                    binding: 0,
                    resource: this.shadowMapper.texture.createView()
                },
                { // shadow sampler
                    binding: 1,
                    resource: this.device.createSampler({ compare: 'less' })
                }
            ]
            const bindings =
                `\n@group(1) @binding(0) var shadowmap: texture_depth_2d;` +
                `\n@group(1) @binding(1) var shadowSampler: sampler_comparison;`
            this.shaderCode = this.common.shader + bindings + rasterizerShader + BlinnPhongShader
        }
        if (this.scene.lightingModel == 'Ray-Tracing') {
            this.radiosity = new Radiosity(this)
            this.bindGroupLayoutEntries = [
                { // light map sampler
                    binding: 0,
                    sampler: { type: 'filtering' },
                    visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT | GPUShaderStage.COMPUTE
                },
                { // light map
                    binding: 1,
                    texture: { viewDimension: '2d-array' },
                    visibility: GPUShaderStage.VERTEX | GPUShaderStage.FRAGMENT | GPUShaderStage.COMPUTE
                }
            ]
            this.bindGroupEntries = [
                { // light map sampler
                    binding: 0,
                    resource: this.device.createSampler({
                        addressModeU: 'clamp-to-edge', // 默认值是clamp-to-edge 纹理坐标被限制在0.0和1.0之间，包括0.0和1.0。
                        addressModeV: 'clamp-to-edge', // repeat
                        magFilter: 'linear', // nearest: 返回最接近纹理坐标的纹素的值。
                        minFilter: 'linear', // linear: 在每个维度中选择两个纹素值，并返回它们值之间的线性插值。
                    }),
                },
                { // light map
                    binding: 1,
                    resource: this.radiosity.lightmap.createView()
                }
            ]
            const bindings =
                `\n@group(1) @binding(0) var textureSampler: sampler;` +
                `\n@group(1) @binding(1) var lightmap: texture_2d_array<f32>;`

            this.shaderCode = this.common.shader + bindings + rasterizerShader + RayTracingShader
        }

        this.bindGroupLayout = this.device.createBindGroupLayout({
            label: 'rasterizer',
            entries: this.bindGroupLayoutEntries,
        })

        this.bindGroup = this.device.createBindGroup({
            label: 'rasterizer',
            layout: this.bindGroupLayout,
            entries: this.bindGroupEntries
        })

        //console.log(this.shaderCode)
        this.pipeline = await this.device.createRenderPipelineAsync({
            label: 'solid',
            layout: this.device.createPipelineLayout({
                bindGroupLayouts: [
                    this.common.bindGroupLayout,
                    this.bindGroupLayout
                ],
            }),
            vertex: {
                module: this.device.createShaderModule({ code: this.shaderCode }),
                entryPoint: 'main_vert',
                buffers: this.scene.vertexBufferLayout
            },
            fragment: {
                module: this.device.createShaderModule({ code: this.shaderCode }),
                entryPoint: 'main_frag',
                targets: [
                    {
                        format: this.format,
                        writeMask: GPUColorWrite.ALL,
                    }
                ],
            },
            depthStencil: {
                depthWriteEnabled: true,
                depthCompare: 'less',
                format: 'depth24plus-stencil8'
            },
            primitive: {
                topology: this.scene.topology,
                frontFace: this.scene.frontFace,
                cullMode: this.scene.cullMode
            },
            multisample: { count: this.multisample ? 4 : 1 }
        })

        this.pipelineAlpha = await this.device.createRenderPipelineAsync({
            label: 'alpha',
            layout: this.device.createPipelineLayout({
                bindGroupLayouts: [
                    this.common.bindGroupLayout,
                    this.bindGroupLayout
                ],
            }),
            vertex: {
                module: this.device.createShaderModule({ code: this.shaderCode }),
                entryPoint: 'main_vert',
                buffers: this.scene.vertexBufferLayout
            },
            fragment: {
                module: this.device.createShaderModule({ code: this.shaderCode }),
                entryPoint: 'main_frag',
                targets: [
                    {
                        format: this.format,
                        writeMask: GPUColorWrite.ALL,
                        blend: {
                            color: {
                                srcFactor: "src-alpha",
                                dstFactor: "one-minus-src-alpha",
                                // dstFactor: "dst-alpha",
                                operation: "add"
                            },
                            alpha: {
                                srcFactor: "src-alpha",
                                dstFactor: "one-minus-src-alpha",
                                // dstFactor: "dst-alpha",
                                operation: "add"
                            }
                        }
                    }
                ],
            },
            depthStencil: {
                depthWriteEnabled: false,
                depthCompare: 'less',
                format: 'depth24plus-stencil8'
            },
            primitive: {
                topology: this.scene.topology,
                frontFace: this.scene.frontFace,
                cullMode: this.scene.cullMode
            },
            multisample: { count: this.multisample ? 4 : 1 }
        })

        this.resize({
            width: window.innerWidth * devicePixelRatio,
            height: window.innerHeight * devicePixelRatio
        })
        window.onresize = () => {
            this.resize({
                width: window.innerWidth * devicePixelRatio,
                height: window.innerHeight * devicePixelRatio
            })
        }
    }
    runPass(commandEncoder, time) {
        // ---- 光照模型
        if (this.scene.lightingModel == 'Blinn-Phong') {
            this.shadowMapper.runPass(commandEncoder)
        }
        if (this.scene.lightingModel == 'Ray-Tracing') {
            // if (time <= 5000) {
            this.radiosity.runPass(commandEncoder)
            // }
        }
        const pass = commandEncoder.beginRenderPass({
            colorAttachments: [
                {
                    view: this.multisample ? this.frameBuffer.createView() : this.context.getCurrentTexture().createView(),
                    resolveTarget: this.multisample ? this.context.getCurrentTexture().createView() : undefined,
                    clearValue: util.colorFormat(this.scene.bgColor),
                    loadOp: 'clear',
                    storeOp: 'store',
                }
            ],
            depthStencilAttachment: {
                view: this.depthBuffer.createView(),
                depthClearValue: 1.0,
                depthLoadOp: 'clear',
                depthStoreOp: 'store',
                stencilClearValue: 0,
                stencilLoadOp: 'clear',
                stencilStoreOp: 'store',
            },
        })
        pass.setBindGroup(0, this.common.bindGroup) // ---- caution: 在pipeline中需要同时正确设置bindGroupLayouts
        pass.setBindGroup(1, this.bindGroup)
        pass.setPipeline(this.pipeline)
        let solid = this.scene.instances.filter(e => e.color[3] == 1),
            alpha = this.scene.instances.filter(e => e.color[3] != 1)
        solid.forEach((e, i) => {
            pass.setVertexBuffer(0, e.vertexBuffer)
            pass.setIndexBuffer(e.indexBuffer, 'uint32')
            pass.drawIndexed(e.mesh.triangles.length * 3, 1, 0, 0, i)
        })
        pass.setPipeline(this.pipelineAlpha)
        alpha.forEach((e, i) => {
            i += solid.length
            pass.setVertexBuffer(0, e.vertexBuffer)
            pass.setIndexBuffer(e.indexBuffer, 'uint32')
            pass.drawIndexed(e.mesh.triangles.length * 3, 1, 0, 0, i)
        })
        pass.end()
    }
    resize({
        width,
        height
    } = {}) {
        this.canvas.width = width
        this.canvas.height = height
        this.frameBuffer = this.resizeTexture(this.frameBuffer, [width, height])
        this.depthBuffer = this.resizeTexture(this.depthBuffer, [width, height])
    }
    resizeTexture(texture, size) {
        texture.destroy()
        const newTexture = Object.assign(texture, { size })
        return this.device.createTexture(newTexture)
    }
}