    createMeshBuffer(mesh) {

        const { color, svgData, coords, anchor, scale, borderColor, borderWidth = 0, borderCorner } = mesh

        const svg = new SVG(svgData)
        const segs = 30

        // shape
        const { vertices, normals, uvs, indices } = mesh.geometry = svg.getGeometry(segs)
        const positions = this.transform(vertices, coords, anchor, scale) // 变换
        mesh.vertexBuffer = this.createVertexBuffer(positions, normals, uvs, color)
        mesh.indexBuffer = this.createIndexBuffer(indices)

        // border
        if (borderWidth != 0) {
            const { vertices, normals, uvs, indices } = mesh.borderGeometry = svg.getBorderGeometry(borderWidth, segs, borderCorner)
            const positions = this.transform(vertices, coords, anchor, scale) // 变换
            mesh.borderVertexBuffer = this.createVertexBuffer(positions, normals, uvs, borderColor)
            mesh.borderIndexBuffer = this.createIndexBuffer(indices)
        }
    }

    getVertexBufferLayout(vertexLayout = []) {
        let offset = 0
        const arrayStride = vertexLayout.reduce((s, e) => s + e, 0) * 4 // * 4 这里的4是4字节的意思
        const attributes = vertexLayout.map((e, i) => {
            const attribute = { shaderLocation: i, offset, format: `float32x${e}` }
            offset += e * 4
            return attribute
        })
        return [{ arrayStride, attributes }]
    }

    createVertexBuffer(positions, normals, uvs, ...extra) {

        positions = splitTypedArray(positions, 3)
        normals = splitTypedArray(normals, 3)
        uvs = splitTypedArray(uvs, 2)

        // this.vertexLayout = []

        const vertice = positions.map((position, i) => {
            const vertex = [
                position,           // 3 position
                normals[i],         // 3 normal
                uvs[i],             // 2 uv
                ...extra            // ...
            ]
            this.vertexLayout = vertex.map(e => e.length)
            return vertex
        }).flat(2) // 变为一维数组

        // vertexBufferLayout
        this.vertexBufferLayout = this.getVertexBufferLayout(this.vertexLayout)
        // console.log('%cvertexBufferLayout:', 'color:yellowgreen', this.vertexBufferLayout)

        const vertexData = new Float32Array(vertice)
        // console.log('%cvertexData', 'color:yellowgreen', vertexData)

        return this.createMappedBuffer({
            size: vertexData.byteLength,
            usage: GPUBufferUsage.VERTEX,
            source: vertexData,
            typedArray: Float32Array
        })

    }

    createIndexBuffer(indices) {
        const indexData = new Uint32Array(indices)
        // console.log('%cindexData', 'color:yellowgreen', indexData)
        return this.createMappedBuffer({
            size: indexData.byteLength,
            usage: GPUBufferUsage.INDEX,
            source: indexData,
            typedArray: Uint32Array
        })
    }

    createMappedBuffer({
        size = 0,
        usage = GPUBufferUsage.VERTEX,
        source,
        typedArray = Float32Array
    } = {}) {
        const GPUbuffer = this.device.createBuffer({ size, usage, mappedAtCreation: true })
        new typedArray(GPUbuffer.getMappedRange()).set(source)
        GPUbuffer.unmap()
        return GPUbuffer
    }

    writeBuffer({
        buffer,
        index,
        values
    } = {}) {
        this.device.queue.writeBuffer(buffer, index, new Float32Array(values))
    }

    transform(positions, translate = [0, 0], anchor = [0, 0], scale = [1, 1]) {
        const t = [...translate, 0]
        const a = [...anchor, 0]
        const s = [...scale, 1]
        const splitPos = splitTypedArray(positions, 3)
        // 获取图形的最大宽度和高度
        let range = []
        for (let i = 0; i < 3; i++) {
            let values = splitPos.map(e => e[i]),
                max = Math.max(...values),
                min = Math.min(...values)
            range.push(max - min)
        }
        // position: [1,1,1] 为点的坐标值
        return splitPos.map(position => {
            // eg: value = 1, i = 0, 表示点对应x轴的值为1
            return position.map((value, i) => {
                // 根据锚点anchor和偏移translate变换点的坐标
                return value * s[i] + t[i] - a[i] * range[i] * s[i]
            })
        }).flat()
    }

    runPass(commandEncoder, output) {
        if (this.meshes.length == 0) return
        this.writeBuffer({
            buffer: this.uniformBuffer,
            index: 0,
            values: [this.canvas.width, this.canvas.height]
        })
        {
            const pass = commandEncoder.beginRenderPass({
                colorAttachments: [
                    {
                        view:
                            this.multisample ?
                                output :
                                this.context.getCurrentTexture().createView(),
                        resolveTarget:
                            this.multisample ?
                                this.context.getCurrentTexture().createView() :
                                undefined,
                        clearValue: [0, 0, 0, 1],
                        loadOp: 'load', // load 是叠加，clear 是清除重新画
                        storeOp: 'store',
                    }
                ]
            })
            pass.setPipeline(this.pipeline)
            pass.setBindGroup(0, this.bindGroups[0])

            this.meshes.forEachDeep(e => {
                pass.setVertexBuffer(0, e.vertexBuffer)
                pass.setIndexBuffer(e.indexBuffer, 'uint32')
                pass.drawIndexed(parseInt(e.indexBuffer.size / 4), 1, 0, 0, 0)
                if (e.borderWidth != 0) {
                    pass.setVertexBuffer(0, e.borderVertexBuffer)
                    pass.setIndexBuffer(e.borderIndexBuffer, 'uint32')
                    pass.drawIndexed(parseInt(e.borderIndexBuffer.size / 4), 1, 0, 0, 0)
                }
            })

            pass.end()
        }
    }

