// 2d ui
const triangleVertWGSL = `
@vertex
fn main(
  @builtin(vertex_index) VertexIndex : u32
) -> @builtin(position) vec4<f32> {
  var pos = array<vec2<f32>, 6>(
    vec2(0.5, 0.5),
    vec2(-0.5, 0.5),
    vec2(-0.5, -0.5),
    vec2(0.5, 0.5),
    vec2(-0.5, -0.5),
    vec2(0.5, -0.5)
  );

  return vec4<f32>(pos[VertexIndex], 0.0, 1.0);
}`
const redFragWGSL = `
@fragment
fn main() -> @location(0) vec4<f32> {
  return vec4(1.0, 0.0, 0.0, 0.5);
}`
export default class {
    constructor({
        device,
        scene,
        common,
        canvas,
        format,
        context,
    } = {}) {
        this.device = device
        this.scene = scene
        this.common = common
        this.format = format
        this.canvas = canvas
        this.context = context
        this.multisample = true
        this.init()
    }
    init() {
        this.pipeline = this.device.createRenderPipeline({
            layout: 'auto',
            vertex: {
                module: this.device.createShaderModule({ code: triangleVertWGSL, }),
                entryPoint: 'main',
            },
            fragment: {
                module: this.device.createShaderModule({ code: redFragWGSL }),
                entryPoint: 'main',
                targets: [
                    {
                        format: this.format,
                        writeMask: GPUColorWrite.ALL,
                        blend: {
                            color: {
                                srcFactor: "src-alpha",
                                dstFactor: "one-minus-src-alpha",
                                operation: "add"
                            },
                            alpha: {
                                srcFactor: "src-alpha",
                                dstFactor: "one-minus-src-alpha",
                                operation: "add"
                            }
                        }
                    }

                ],
            },
            primitive: {
                topology: 'triangle-list',
            },
            multisample: { count: this.multisample ? 4 : 1 }
        });
    }
    runPass(commandEncoder, output) {
        const pass = commandEncoder.beginRenderPass({
            colorAttachments: [
                {
                    view: this.multisample ?
                        output :
                        this.context.getCurrentTexture().createView(),
                    resolveTarget: this.multisample ?
                        this.context.getCurrentTexture().createView() :
                        undefined,
                    loadOp: 'load', // load 是叠加，clear 是清除重新画
                    storeOp: 'store',
                }
            ]
        })

        pass.setPipeline(this.pipeline)
        pass.draw(6, 1, 0, 0)
        pass.end()
    }
}