import util from "./util"
import { clamp } from "./Math"

function fromValues(x, y, z) {
    var out = new Float32Array(3)
    out[0] = x
    out[1] = y
    out[2] = z
    return out
}
function create() {
    if (util.isObject(arguments[0])) {
        return fromValues(...Object.values(arguments[0]))
    }
    if (util.isArray(arguments[0])) {
        return fromValues(...arguments[0])
    }
    if (util.isNumber(arguments[0])) {
        return fromValues(...arguments)
    }
    return new Float32Array(3)
}
function normalize(a) {
    let out = vector3.create()
    var x = a[0]
    var y = a[1]
    var z = a[2]
    var len = x * x + y * y + z * z

    if (len > 0) {
        len = 1 / Math.sqrt(len)
    }

    out[0] = a[0] * len
    out[1] = a[1] * len
    out[2] = a[2] * len
    return out
}
function subtract(a, b) {
    let out = vector3.create()
    out[0] = a[0] - b[0]
    out[1] = a[1] - b[1]
    out[2] = a[2] - b[2]
    return out
}
function dot(a, b) {
    return a[0] * b[0] + a[1] * b[1] + a[2] * b[2]
}
function cross(a, b) {
    let out = vector3.create()
    var ax = a[0],  // 0
        ay = a[1],  // 1
        az = a[2]   // 0
    var bx = b[0],  // 0
        by = b[1],  // 1
        bz = b[2]   // 0
    out[0] = ay * bz - az * by
    out[1] = az * bx - ax * bz
    out[2] = ax * by - ay * bx
    return out
}
function transformMat3(a, m) {
    let out = vector3.create()
    var x = a[0],
        y = a[1],
        z = a[2]
    out[0] = x * m[0] + y * m[3] + z * m[6]
    out[1] = x * m[1] + y * m[4] + z * m[7]
    out[2] = x * m[2] + y * m[5] + z * m[8]
    return out
}
function transformMat4(a, m) {
    if (!m) return create(a)
    let out = vector3.create()
    var x = a[0],
        y = a[1],
        z = a[2]
    var w = m[3] * x + m[7] * y + m[11] * z + m[15]
    w = w || 1.0
    out[0] = (m[0] * x + m[4] * y + m[8] * z + m[12]) / w
    out[1] = (m[1] * x + m[5] * y + m[9] * z + m[13]) / w
    out[2] = (m[2] * x + m[6] * y + m[10] * z + m[14]) / w
    return out
}
function add(a, b) {
    let out = vector3.create()
    out[0] = a[0] + b[0]
    out[1] = a[1] + b[1]
    out[2] = a[2] + b[2]
    return out
}
function scale(a, b) {
    let out = vector3.create()
    out[0] = a[0] * b;
    out[1] = a[1] * b;
    out[2] = a[2] * b;
    return out;
}
// a + b * scale
function scaleAndAdd(a, b, scale) {
    let out = vector3.create()
    out[0] = a[0] + b[0] * scale;
    out[1] = a[1] + b[1] * scale;
    out[2] = a[2] + b[2] * scale;
    return out;
}
/**
 * 
 * @param {*} a 
 * @param {*} b 
 * @param {*} axis right hand
 * @returns 
 */
function angle(a, b, axis) {
    a = normalize(a)
    b = normalize(b)
    if (axis) {
        axis = normalize(axis)
        let c = normalize(cross(a, b))
        let cosin = clamp(vector3.dot(axis, c), -1, 1)
        if (cosin === -1) {
            return -Math.acos(clamp(vector3.dot(a, b), -1, 1))
        }
    }
    return Math.acos(clamp(vector3.dot(a, b), -1, 1))
}
function length(a) {
    var x = a[0];
    var y = a[1];
    var z = a[2];
    return Math.hypot(x, y, z);
}
/**
 * 求向量A在向量B的投影向量
 * @param {*} a 
 * @param {*} b 
 * @returns 
 */
function projection(a, b) {
    b = normalize(b)
    return scale(b, dot(a, b))
}
/**
 * 求向量A到向量B的垂线向量
 * @param {*} a 
 * @param {*} b 
 * @returns 
 */
function perpendicular(a, b) {
    let proj = projection(a, b)
    return subtract(a, proj)
}

const vector3 = {
    len: length,
    length,
    fromValues,
    create,
    cv3: create,
    normalize,
    add,
    scale,
    scaleAndAdd,
    subtract,
    sub: subtract,
    dot,
    cross,
    transformMat3,
    transformMat4,
    angle,
    proj: projection,
    projection,
    perp: perpendicular,
    perpendicular,
}

export default vector3