import shadowShader from './shader/shadow.wgsl?raw'

export default class ShadowMapper {
    constructor({
        device,
        scene,
        common,
    } = {}) {
        this.device = device
        this.scene = scene
        this.common = common
        this.init()
    }
    async init() {
        this.texture = this.device.createTexture({
            label: 'shadow',
            size: [8192, 8192, 1],
            format: 'depth32float',
            usage: GPUTextureUsage.RENDER_ATTACHMENT | GPUTextureUsage.TEXTURE_BINDING,
        })
        this.pipeline = await this.device.createRenderPipelineAsync({
            label: 'shadow',
            layout: this.device.createPipelineLayout({
                bindGroupLayouts: [
                    this.common.bindGroupLayout,
                ],
            }),
            vertex: {
                module: this.device.createShaderModule({ code: this.common.shader + shadowShader }),
                entryPoint: 'main',
                buffers: this.scene.vertexBufferLayout
            },
            depthStencil: {
                depthWriteEnabled: true,
                depthCompare: 'less',
                format: 'depth32float'
            },
            primitive: {
                topology: 'triangle-list',
                cullMode: 'none'
            },
        })
    }
    runPass(commandEncoder) {
        const pass = commandEncoder.beginRenderPass({
            colorAttachments: [],
            depthStencilAttachment: {
                view: this.texture.createView(),
                depthClearValue: 1.0,
                depthLoadOp: 'clear',
                depthStoreOp: 'store',
            },
        })
        pass.setPipeline(this.pipeline)
        pass.setBindGroup(0, this.common.bindGroup)
        this.scene.instances.forEach((e, i) => {
            pass.setVertexBuffer(0, e.vertexBuffer)
            pass.setIndexBuffer(e.indexBuffer, 'uint32')
            pass.drawIndexed(e.mesh.triangles.length * 3, 1, 0, 0, i)
        })
        pass.end()
    }
}