function colorFormat(color) {
    return {
        r: color[0] / 255,
        g: color[1] / 255,
        b: color[2] / 255,
        a: color[3]
    }
}

function getGeometry(mesh) {
    return {
        vertex: new Float32Array(mesh.positions.map((e, i) => {
            e.push(...mesh.normals[i])
            e.push(...mesh.uvs[i])
            if (Array.isArray(mesh.tangents)) e.push(...mesh.tangents[i])
            if (Array.isArray(mesh.bitangents)) e.push(...mesh.bitangents[i])
            return e
        }).flat()),                     // ---- 14
        index: new Uint32Array(mesh.triangles.flat()),
        vertexCount: mesh.positions.length,
        indexCount: mesh.triangles.length * 3,
        triangles: mesh.triangles
    }
}
