async function loadObj(url) {
    let res = await fetch(url)
    let text = await res.text()
    const result = {
        vertices: [],
        normals: [],
        uvs: [],
        faces: 0,
    }
    const mesh = {
        triangles: [],
        positions: [],
        normals: [],
        uvs: [],
    }
    if (text.indexOf('\r\n') !== - 1) {
        // This is faster than String.split with regex that splits on both
        text = text.replace(/\r\n/g, '\n')
    }
    if (text.indexOf('\\\n') !== - 1) {
        // join lines separated by a line continuation character (\)
        text = text.replace(/\\\n/g, '')
    }
    const lines = text.split('\n')
    for (let i = 0, l = lines.length; i < l; i++) {
        const line = lines[i].trimStart()
        if (line.length === 0) continue
        const lineFirstChar = line.charAt(0) // @todo invoke passed in handler if any
        if (lineFirstChar === '#') continue
        if (lineFirstChar === 'v') {
            const data = line.split(/\s+/)
            switch (data[0]) {
                case 'v':
                    result.vertices.push([parseFloat(data[1]), parseFloat(data[2]), parseFloat(data[3])])
                    break
                case 'vn':
                    result.normals.push([parseFloat(data[1]), parseFloat(data[2]), parseFloat(data[3])])
                    break
                case 'vt':
                    result.uvs.push([parseFloat(data[1]), parseFloat(data[2])])
                    break
            }
        } else if (lineFirstChar === 'f') {
            const lineData = line.slice(1).trim()
            const vertexData = lineData.split(/\s+/)
            const baseIndex = mesh.triangles.length > 0 ? mesh.triangles.at(-1).at(-1) + 1 : 0
            for (let j = 0; j < vertexData.length; j++) {
                const vertex = vertexData[j]
                if (vertex.length > 0) {
                    const vertexParts = vertex.split('/'),
                        vertexIndex = vertexParts[0] - 1,
                        textureIndex = vertexParts[1] - 1,
                        normalIndex = vertexParts[2] - 1
                    mesh.positions.push(result.vertices[vertexIndex])
                    mesh.normals.push(result.normals[normalIndex])
                    mesh.uvs.push(result.uvs[textureIndex])
                }
                if (j < vertexData.length - 2) {
                    mesh.triangles.push([baseIndex, baseIndex + j + 1, baseIndex + j + 2])
                }
            }
        }
    }
    // mesh.tangents = computeTangents(mesh.positions, mesh.triangles, mesh.uvs)
    // mesh.bitangents = computeBitangents(mesh.positions, mesh.triangles, mesh.uvs)
    console.log(mesh)
    return mesh
}

async function loadOBJ(url) {
    const timeRecorded = new Date().valueOf()
    let res = await fetch(url)
    let text = await res.text()
    const mesh = {
        positions: [],
        normals: [],
        uvs: [],
        faces: [],
        triangles: []
    }
    if (text.indexOf('\r\n') !== - 1) {
        // This is faster than String.split with regex that splits on both
        text = text.replace(/\r\n/g, '\n')
    }
    if (text.indexOf('\\\n') !== - 1) {
        // join lines separated by a line continuation character (\)
        text = text.replace(/\\\n/g, '')
    }
    const lines = text.split('\n')
    for (let i = 0, l = lines.length; i < l; i++) {
        const line = lines[i].trimStart()
        if (line.length === 0) continue
        const lineFirstChar = line.charAt(0) // @todo invoke passed in handler if any
        if (lineFirstChar === '#') continue
        if (lineFirstChar === 'v') {
            const data = line.split(/\s+/)
            switch (data[0]) {
                case 'v':
                    mesh.positions.push([parseFloat(data[1]), parseFloat(data[2]), parseFloat(data[3])])
                    break
                case 'vn':
                    mesh.normals.push([parseFloat(data[1]), parseFloat(data[2]), parseFloat(data[3])])
                    break
                case 'vt':
                    mesh.uvs.push([parseFloat(data[1]), parseFloat(data[2])])
                    break
            }
        }
        if (lineFirstChar === 'f') {
            const lineData = line.slice(1).trim()
            const vertexData = lineData.split(/\s+/)
            const face = []
            for (let j = 0; j < vertexData.length; j++) {
                const vertex = vertexData[j]
                if (vertex.length > 0) {
                    const vertexParts = vertex.split('/'),
                        vertexIndex = vertexParts[0] - 1
                    face.push(vertexIndex)
                }
            }
            mesh.faces.push(face)
            face.forEach((f, i, arr) => {
                if (i < arr.length - 2) {
                    mesh.triangles.push([arr[0], arr[i + 1], arr[i + 2]])
                }
            })
        }
    }
    if (mesh.normals.length === 0) {
        mesh.normals = computeSurfaceNormals(mesh.positions, mesh.triangles)
    }
    if (mesh.uvs.length === 0) {
        mesh.uvs = computeProjectedPlaneUVs(mesh.positions)
    }
    console.log(mesh.triangles.length, (new Date().valueOf() - timeRecorded) / 1000)
    return getGeometry(mesh)
}

