// 相机透视投影矩阵
function getCameraViewProjectionMatrix({
    aspect,
    eye,
    center,
    fovy = 30,
    up = [0, 1, 0],
    near = 0.1,
    far = 100000,
} = {}) {
    let v_eye = vector3.create(eye),
        v_center = vector3.create(center),
        v_up = vector3.create(up),
        v = matrix4.lookAt(v_eye, v_center, v_up),// view matrix
        p = matrix4.perspectiveZO(radians(fovy), aspect, near, far)// projection matrix
    return matrix4.multiply(p, v)
}
// 灯光正交投影矩阵
function getLightViewProjectionMatrix({
    light = [-40, 200, 40],
    center = [0, 0, 0],
    up = [0, 1, 0],
    left = -1,
    right = 1,
    bottom = -1,
    top = 1,
    near = 1,
    far = 300,
} = {}) {
    let v_light = vector3.create(light),
        v_center = vector3.create(center),
        v_up = vector3.create(up),
        v = matrix4.lookAt(v_light, v_center, v_up),// view matrix
        p = matrix4.ortho(left, right, bottom, top, near, far)// projection matrix
    return matrix4.multiply(p, v)
}

function getModelTransformMatrix({
    anchor = [0, 0, 0],
    position = [0, 1, 0],
    rotation = [0, 0, 0],
    scale = [1, 1, 1],
} = {}) {
    let m = matrix4.create()
    m = matrix4.translate(m, position)
    m = matrix4.rotateX(m, rotation[0] * Math.PI / 180)
    m = matrix4.rotateY(m, rotation[1] * Math.PI / 180)
    m = matrix4.rotateZ(m, rotation[2] * Math.PI / 180)
    m = matrix4.scale(m, scale)
    m = matrix4.translate(m, anchor.map(e => -e))
    return m
}

function print(title, msg, {
    length = 15,
    symbol = '-',
    color = 'yellowgreen'
} = {}) {
    if (msg) {
        let str = ''
        for (let index = 0; index < length; index++) {
            str += symbol
        }
        str = `[${str}]`
        let front = str.slice(0, Math.floor((length + 2 - title.length) / 2)),
            back = str.slice(Math.floor((length + 2 - title.length) / 2) + title.length)
        console.log(`%c${front}${title}${back}:`, `color: ${color}`, msg)
    }
}

function invertRasterizeByOffset({
    canvas,
    eye,
    center,
    up = [0, 1, 0],
    offsetX = 50,
    offsetY = 50,
    vp
} = {}) {
    let v_eye = vector3.create(eye)
    let v_eye_half = vector3.scale(v_eye, 0.5)
    let v_up = vector3.create(up)
    let right = vector3.cross(v_up, v_eye_half)
    let a = Math.atan((canvas.height / 2 - offsetY) / (canvas.width / 2 - offsetX))
    let m = matrix4.fromRotation(v_eye, a)
    let v_diagonal = vector3.normalize(vector3.transformMat4(right, m))
    let v = vector3.add(v_eye_half, v_diagonal)
    let v_rasterized = vector3.transformMat4(v, vp)
    v_diagonal = vector3.scale(v_diagonal, Math.abs(offsetX - canvas.width / 2) / (canvas.width / 2) / v_rasterized[0])
    v = vector3.add(v_eye_half, v_diagonal)
    return v
}

function isArray(value) {
    return Array.isArray(value)
}

function isTypedArray(arr) {
    return ArrayBuffer.isView(arr) && !(arr instanceof DataView);
}

function isFunction(value) {
    var type = typeof value
    return value != null && type == 'function'
}

function isObject(value) {
    var type = typeof value
    return value != null && (type == 'object' || type == 'function')
}

function isNumber(value) {
    return typeof value === 'number' && !isNaN(value);
}

