import util from "./util"
import { clamp } from "./Math"
const EPSILON = 1e-6

/** 
 * The biggest differences between this lib and others are: 
 * 1. The return value is always a new variable; 
 * 2. when calculating the angle between vectors , it returns a value with a sign.
*/

export function fromValues(x, y) {
    var out = new Float32Array(2)
    out[0] = x
    out[1] = y
    return out
}
export function create() {
    if (util.isObject(arguments[0])) {
        return fromValues(...Object.values(arguments[0]))
    }
    if (util.isArray(arguments[0])) {
        return fromValues(...arguments[0])
    }
    if (util.isNumber(arguments[0])) {
        return fromValues(...arguments)
    }
    return new Float32Array(2)
}
export function normalize(a) {
    let out = create()
    var x = a[0]
    var y = a[1]
    var len = x * x + y * y

    if (len > 0) {
        len = 1 / Math.sqrt(len)
    }

    out[0] = a[0] * len
    out[1] = a[1] * len
    return out
}

export const norm = normalize
export function subtract(a, b) {
    let out = create()
    out[0] = a[0] - b[0]
    out[1] = a[1] - b[1]
    return out
}

export const sub = subtract

export function add(a, b) {
    let out = create()
    out[0] = a[0] + b[0]
    out[1] = a[1] + b[1]
    return out
}

export function scale(a, b) {
    let out = create()
    out[0] = a[0] * b
    out[1] = a[1] * b
    return out
}

export function dot(a, b) {
    return a[0] * b[0] + a[1] * b[1];
}

/**
 * Adds two vec2's after scaling the second operand by a scalar value
 *
 * @param {ReadonlyVec2} a the first operand
 * @param {ReadonlyVec2} b the second operand
 * @param {Number} scale the amount to scale b by before adding
 * @returns {vec2} out
 */
export function scaleAndAdd(a, b, scale) {
    let out = create()
    out[0] = a[0] + b[0] * scale;
    out[1] = a[1] + b[1] * scale;
    return out;
}

/**
 * Rotate a 2D vector
 * @param {ReadonlyVec2} a The vec2 point to rotate
 * @param {ReadonlyVec2} b The origin of the rotation
 * @param {Number} rad The angle of rotation in radians
 * @returns {vec2} out
 */
export function rotate(a, b, rad) {
    let out = create()
    //Translate point to the origin
    let p0 = a[0] - b[0],
        p1 = a[1] - b[1],
        sinC = Math.sin(rad),
        cosC = Math.cos(rad);
    //perform rotation and translate to correct position
    out[0] = p0 * cosC - p1 * sinC + b[0];
    out[1] = p0 * sinC + p1 * cosC + b[1];
    return out;
}

export function equals(a, b, eps = EPSILON) {
    let a0 = a[0],
        a1 = a[1];
    let b0 = b[0],
        b1 = b[1];
    return (
        Math.abs(a0 - b0) <=
        eps * Math.max(1.0, Math.abs(a0), Math.abs(b0)) &&
        Math.abs(a1 - b1) <=
        eps * Math.max(1.0, Math.abs(a1), Math.abs(b1))
    );
}

export function angle(a, b) {
    let x1 = a[0],
        y1 = a[1],
        x2 = b[0],
        y2 = b[1],
        // mag is the product of the magnitudes of a and b
        mag = Math.sqrt(x1 * x1 + y1 * y1) * Math.sqrt(x2 * x2 + y2 * y2),
        // mag &&.. short circuits if mag == 0
        cosine = mag && (x1 * x2 + y1 * y2) / mag;
    // Math.min(Math.max(cosine, -1), 1) clamps the cosine between -1 and 1
    return Math.acos(Math.min(Math.max(cosine, -1), 1));
}
// 右手定则
export function angle2(a, b) {
    let rad = angle(a, b)
    let rotated = normalize(rotate(a, [0, 0], rad))
    if (equals(rotated, normalize(b))) {
        return rad
    } else {
        return -rad + Math.PI * 2
    }
}
// 左手定则
export function angle3(a, b) {
    let rad = angle(a, b)
    let rotated = normalize(rotate(b, [0, 0], rad))
    if (equals(rotated, normalize(a))) {
        return rad
    } else {
        return -rad + Math.PI * 2
    }
}

export function length(a) {
    var x = a[0],
        y = a[1];
    return Math.hypot(x, y);
}

/**
 * 求向量A在向量B的投影向量
 * @param {*} a 
 * @param {*} b 
 * @returns 
 */
export function projection(a, b) {
    b = normalize(b)
    return scale(b, dot(a, b))
}
/**
 * 求向量A到向量B的垂线向量
 * @param {*} a 
 * @param {*} b 
 * @returns 
 */
export function perpendicular(a, b) {
    let proj = projection(a, b)
    return subtract(a, proj)
}

export const perp = perpendicular

export const len = length

/**
 * 平移
 * @param {*} a 点
 * @param {*} b 方向
 * @param {*} c 距离
 */
export function translate(a, b, c) {
    b = normalize(b)
    return scaleAndAdd(a, b, c)
}