export default async (canvasId) => {
    const canvas = document.getElementById(canvasId)
    canvas.width = window.innerWidth
    canvas.height = window.innerHeight
    const adapter = await navigator.gpu.requestAdapter({ powerPreference: 'high-performance' })
    const device = await adapter.requestDevice()
    const context = canvas.getContext('webgpu')
    const format = navigator.gpu.getPreferredCanvasFormat()
    context.configure({
        device: device,
        format: format,
        usage: GPUTextureUsage.RENDER_ATTACHMENT | GPUTextureUsage.COPY_DST | GPUTextureUsage.COPY_SRC,
        alphaMode: 'premultiplied'
    })
    const limits = device.limits

    // setTimeout(() => {
    console.log(`✨%c ---- Aurora ---- ✨`, 'color: yellowgreen')
    // console.log('%c' +
    //     '╭───────────────╮   \n' +
    //     '│               │   \n' +
    //     '│               │   \n' +
    //     '│     Aurora    │   \n' +
    //     '│               │   \n' +
    //     '│               │   \n' +
    //     '╰───────────────╯   ',
    //     'color: yellowgreen')
    console.log('%climits:', 'color:yellowgreen', limits)
    // }, 200)
    return {
        canvas,
        adapter,
        context,
        device,
        format
    }
}