import vector3 from './vector3'
import matrix4 from './matrix4'
import { Animation } from './Animation'
import { radians, mix, degrees } from './Math'
import Easing from './Easing'
// 键盘类事件频率：30次每秒
// 鼠标类事件频率：1000次每秒
// 滚轮类事件频率：0.1 ~ 60次每秒
export default class {
    constructor({
        ele,
        scene, // 三维场景实例，需要具有setEye, getEye, setCenter, getCenter
        smooth = {
            enabled: true,
            duration: 500, // 高优先级
            friction: 0.05, // 摩擦系数
        },
        // 鼠标事件配置
        mouseEventEnabled = true,
        mouseButtonsMap = {
            // 左键
            1: 'rotate',
            // 中键
            4: 'move',
            // 右键
            2: 'rotateCenter'
        },
        // 键盘事件配置
        keyboardEventEnabled = true,
        keyMap = {
            moveForward: 'w',
            moveBackward: 's',
            moveLeft: 'a',
            moveRight: 'd',
            dive: 'q',
            float: 'e',
            turnUp: 'arrowup',
            turnDown: 'arrowdown',
            turnLeft: 'arrowleft',
            turnRight: 'arrowright',
            speedUp: 'shift',
            reset: 'r'
        },
        // 触控事件配置
        touchEventEnabled = true,
        touchMap = 'move',
        // 滚轮事件配置
        wheelEventEnabled = true,
        // 旋转配置
        rotateSpeed = 0.1, // degree per pixel
        rotateAround = [0, 0, 0], // 旋转中心: string or arrya, preset: center/eye
        beforeRotate,
        // 平移配置
        moveSpeed = 0.01, // pixel per second
        moveCoords = ['right', 'horizonFront'], // 平移坐标系
        beforeMove,
        // 缩放配置
        wheelSpeed = 0.2, // percent than before
        scaleType = 'percent', // 缩放值类型: percent or fixed
        beforeScale,
        // 自由模式
        free = false,
    } = {}) {
        this.ele = ele
        this.free = free
        this.smooth = smooth
        this.rotateSpeed = rotateSpeed
        this.moveSpeed = moveSpeed
        this.wheelSpeed = wheelSpeed

        this.rotateAround = rotateAround
        this.moveCoords = moveCoords
        this.scaleType = scaleType

        this.keyMap = keyMap
        this.mouseButtonsMap = mouseButtonsMap
        this.touchMap = touchMap

        this.beforeRotate = beforeRotate
        this.beforeMove = beforeMove
        this.beforeScale = beforeScale

        this.originalEye = scene.getEye()
        this.originalCenter = scene.getCenter()
        this.active = true
        this.keys = [] // KeyEvent keys
        this.keysLog = []
        this.moves = []
        this.movesLog = []

        this.getEye = scene.getEye.bind(scene)
        this.setEye = scene.setEye.bind(scene)
        this.getCenter = scene.getCenter.bind(scene)
        this.setCenter = scene.setCenter.bind(scene)

        if (mouseEventEnabled) {
            this.ele.addEventListener('mousedown', this.mousedownListener.bind(this), { passive: false })
            this.ele.addEventListener('mousemove', this.mousemoveListener.bind(this), { passive: false })
            this.ele.addEventListener("mouseup", this.mouseupListener.bind(this), { passive: false })
        }
        if (touchEventEnabled) {
            this.ele.addEventListener('touchstart', this.touchstartListener.bind(this), { passive: false })
            this.ele.addEventListener('touchend', this.touchendListener.bind(this), { passive: false })
            this.ele.addEventListener('touchmove', this.touchmoveListener.bind(this), { passive: false })
        }
        if (wheelEventEnabled) {
            this.ele.addEventListener('wheel', this.wheelListener.bind(this))
        }
        if (keyboardEventEnabled) {
            window.addEventListener('keydown', this.keydownListener.bind(this), { passive: false })
            window.addEventListener('keyup', this.keyupListener.bind(this), { passive: false })
        }

        document.oncontextmenu = () => false
        this.frame()
    }
    // 触控类
    touchstartListener(event) {
        if (!this.active) return
        this.touchEvent = event
        event.preventDefault()
    }
    touchendListener(event) {
        if (!this.active) return
        this.touchEvent = event
        event.preventDefault()
    }
    touchmoveListener(event) {
        if (
            this.active &&
            this.touchEvent
        ) {
            // 单指
            if (event.touches.length === 1) {
                const touch = event.touches[0],
                    touch2 = this.touchEvent.touches[0],
                    x = touch.clientX - touch2.clientX,
                    y = touch.clientY - touch2.clientY
                switch (this.touchMap) {
                    case 'rotateEye': {
                        this.rotate({ x, y, around: 'center', smooth: this.smooth.enabled })
                    } break
                    case 'rotateCenter': {
                        this.rotate({ x, y, around: 'eye', smooth: this.smooth.enabled })
                    } break
                    case 'rotate': {
                        this.rotate({ x, y, around: this.rotateAround, smooth: this.smooth.enabled })
                    } break
                    case 'move': {
                        this.move({ x, y, coords: this.moveCoords, speed: 5 })
                    } break
                }
                this.touchEvent = event
            }
            // 双指
            if (event.touches.length === 2) {
                // 计算双指间距离
                const calcDist = (event) => {
                    let touch1 = event.touches[0],
                        touch2 = event.touches[1]
                    return Math.hypot(touch1.clientX - touch2.clientX, touch1.clientY - touch2.clientY)
                }
                const calcAngle = (event) => {
                    let touch1 = event.touches[0],
                        touch2 = event.touches[1]
                    return Math.atan2(touch2.clientY - touch1.clientY, touch2.clientX - touch1.clientX)
                }
                let scale = calcDist(this.touchEvent) / calcDist(event)
                this.scale(scale)
                let rad = degrees(calcAngle(this.touchEvent) - calcAngle(event))
                this.rotate({ x: rad / this.rotateSpeed, y: 0, around: 'center', smooth: this.smooth.enabled })
            }
            this.touchEvent = event
            event.preventDefault()
        }
    }
    // 鼠标类
    mousedownListener(event) {
        if (!this.active) return
        // 清除缓动
        this.move({ clear: true })
        this.rotate({ clear: true })
        this.moves = []
        this.movesLog = []
        this.mouseEvent = event
        event.preventDefault()
    }
    mouseupListener(event) {
        if (!this.active) return
        this.moves = []
        this.mouseEvent = null
        event.preventDefault()
    }
    mousemoveListener(event) {
        if (!this.active || !this.mouseEvent) return
        let x = event.clientX - this.mouseEvent.clientX, // 屏幕坐标，x轴向右
            y = event.clientY - this.mouseEvent.clientY,  // 屏幕坐标，y轴向下
            action = this.mouseButtonsMap[event.buttons]
        this.moves.push({ x, y, action })
        this.mouseEvent = event
        event.preventDefault()
    }
    // 滚轮类
    wheelListener(event) {
        if (
            this.active &&
            event.buttons === 0
        ) {
            this.wheelEvent = event
        }
    }
    // 键盘类
    keydownListener(event) {
        if (!this.active) return
        this.addKey(event.key)
    }
    keyupListener(event) {
        if (!this.active) return
        this.removeKey(event.key)
    }
    // 按键
    addKey(key, log = true) {
        let index = this.keys.findIndex(e => e === key.toLowerCase())
        if (index < 0) {
            this.keys.push(key.toLowerCase())
            if (log) this.keysLog.push(key.toLowerCase())
        }
    }
    removeKey(key) {
        let index = this.keys.findIndex(e => e === key.toLowerCase())
        if (index >= 0) {
            this.keys.splice(index, 1)
        }
    }
    removeKeyLog(key) {
        let index = this.keysLog.findIndex(e => e === key.toLowerCase())
        if (index >= 0) {
            this.keysLog.splice(index, 1)
        }
    }
    hasKey(key) {
        return this.keys.indexOf(key.toLowerCase()) >= 0
    }
    hasKeyLog(key) {
        return this.keysLog.indexOf(key.toLowerCase()) >= 0
    }
    frame() {
        // 鼠标移动
        if (this.moves.length > 0) {
            this.movesLog = new Array(...this.moves) // todo: 尝试赋值给一个局域变量
            let x = -this.movesLog.reduce((s, e) => s + e.x, 0),
                y = this.movesLog.reduce((s, e) => s + e.y, 0),
                action = this.movesLog.at(-1).action
            switch (action) {
                case 'rotateEye': {
                    this.rotate({
                        x: radians(x) * this.rotateSpeed,
                        y: radians(y) * this.rotateSpeed,
                        around: 'center',
                    })
                } break
                case 'rotateCenter': {
                    this.rotate({
                        x: radians(x) * this.rotateSpeed,
                        y: radians(y) * this.rotateSpeed,
                        around: 'eye',
                    })
                } break
                case 'rotate': {
                    this.rotate({
                        x: radians(x) * this.rotateSpeed,
                        y: radians(y) * this.rotateSpeed,
                        around: this.rotateAround,
                    })
                } break
                case 'move': {
                    this.move({
                        x: x * this.moveSpeed,
                        y: y * this.moveSpeed,
                        coords: this.moveCoords,
                    })
                } break
            }
            this.moves = []
        }
        else if (this.movesLog.length > 0) {
            if (!this.mouseEvent) {
                let x = -this.movesLog.reduce((s, e) => s + e.x, 0),
                    y = this.movesLog.reduce((s, e) => s + e.y, 0),
                    action = this.movesLog.at(-1).action
                switch (action) {
                    case 'rotateEye': {
                        this.rotate({
                            x: radians(x) * this.rotateSpeed,
                            y: radians(y) * this.rotateSpeed,
                            around: 'center',
                            smooth: this.smooth.enabled
                        })
                    } break
                    case 'rotateCenter': {
                        this.rotate({
                            x: radians(x) * this.rotateSpeed,
                            y: radians(y) * this.rotateSpeed,
                            around: 'eye',
                            smooth: this.smooth.enabled
                        })
                    } break
                    case 'rotate': {
                        this.rotate({
                            x: radians(x) * this.rotateSpeed,
                            y: radians(y) * this.rotateSpeed,
                            around: this.rotateAround,
                            smooth: this.smooth.enabled
                        })
                    } break
                    case 'move': {
                        this.move({
                            x: x * this.moveSpeed,
                            y: y * this.moveSpeed,
                            coords: this.moveCoords,
                            smooth: true,
                        })
                    } break
                }
                this.movesLog = []
            }
        }

        // 滚轮
        if (this.wheelEvent) {
            if (this.scaleSmoothAnim) {
                // console.log('缓动阶段')
                if (this.lastWheelEvent && this.wheelEvent.deltaY !== this.lastWheelEvent.deltaY) {
                    this.scaleSmoothAnim.stop()
                    this.scaleSmoothAnim = null
                }
            }
            else if (this.scaleInertiaAnim) {
                // console.log('惯性阶段')
                this.scaleInertiaAnim.stop()
                this.scaleInertiaAnim = null
                this.scale({
                    value: this.wheelSpeed * Math.sign(this.wheelEvent.deltaY),
                    type: this.scaleType,
                    smooth: true,
                    inertia: true
                })
            }
            else {
                // console.log('结束阶段')
                this.scale({
                    value: this.wheelSpeed * Math.sign(this.wheelEvent.deltaY),
                    type: this.scaleType,
                    smooth: true,
                    inertia: true
                })
            }
            this.lastWheelEvent = this.wheelEvent
            this.wheelEvent = null
        }

        // 加速
        if (this.hasKey(this.keyMap.speedUp)) {
            if (!this.speedUpEnabled) {
                this.speedUpEnabled = true
                this.moveSpeed *= 2
            }
        }
        else {
            this.removeKeyLog(this.keyMap.speedUp)
            if (this.speedUpEnabled) {
                this.speedUpEnabled = false
                this.moveSpeed /= 2
            }
        }

        // 重置
        if (this.hasKey(this.keyMap.reset)) {
            this.setView()
        }
        else {
            this.removeKeyLog(this.keyMap.reset)
        }

        // 向前 ↑
        if (this.hasKey(this.keyMap.moveForward)) {
            this.move({
                x: 0,
                y: 25 * this.moveSpeed,
                coords: this.free ? ['right', 'ray'] : ['right', 'horizonFront'],
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.moveForward)) {
            this.removeKeyLog(this.keyMap.moveForward)
            this.move({
                x: 0,
                y: 25 * this.moveSpeed,
                coords: this.free ? ['right', 'ray'] : ['right', 'horizonFront'],
                smooth: this.smooth.enabled,
                clear: true,
            })
        }

        // 向后 ↓
        if (this.hasKey(this.keyMap.moveBackward)) {
            this.move({
                x: 0,
                y: -25 * this.moveSpeed,
                coords: this.free ? ['right', 'ray'] : ['right', 'horizonFront'],
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.moveBackward)) {
            this.removeKeyLog(this.keyMap.moveBackward)
            this.move({
                x: 0,
                y: -25 * this.moveSpeed,
                coords: this.free ? ['right', 'ray'] : ['right', 'horizonFront'],
                smooth: this.smooth.enabled,
                clear: true,
            })
        }

        // 向左 ←
        if (this.hasKey(this.keyMap.moveLeft)) {
            this.move({
                x: -25 * this.moveSpeed,
                y: 0,
                coords: this.free ? ['right', 'ray'] : ['right', 'horizonFront'],
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.moveLeft)) {
            this.removeKeyLog(this.keyMap.moveLeft)
            this.move({
                x: -25 * this.moveSpeed,
                y: 0,
                coords: this.free ? ['right', 'ray'] : ['right', 'horizonFront'],
                smooth: this.smooth.enabled,
                clear: true
            })
        }

        // 向右 →
        if (this.hasKey(this.keyMap.moveRight)) {
            this.move({
                x: 25 * this.moveSpeed,
                y: 0,
                coords: this.free ? ['right', 'ray'] : ['right', 'horizonFront'],
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.moveRight)) {
            this.removeKeyLog(this.keyMap.moveRight)
            this.move({
                x: 25 * this.moveSpeed,
                y: 0,
                coords: this.free ? ['right', 'ray'] : ['right', 'horizonFront'],
                smooth: this.smooth.enabled,
                clear: true
            })
        }

        // 上浮(O.O )
        if (this.hasKey(this.keyMap.float)) {
            this.move({
                x: 0,
                y: 25 * this.moveSpeed,
                coords: ['right', 'up'],
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.float)) {
            this.removeKeyLog(this.keyMap.float)
            this.move({
                x: 0,
                y: 25 * this.moveSpeed,
                coords: ['right', 'up'],
                smooth: this.smooth.enabled,
                clear: true
            })
        }

        // 下潜(>.< )
        if (this.hasKey(this.keyMap.dive)) {
            this.move({
                x: 0,
                y: -25 * this.moveSpeed,
                coords: ['right', 'up'],
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.dive)) {
            this.removeKeyLog(this.keyMap.dive)
            this.move({
                x: 0,
                y: -25 * this.moveSpeed,
                coords: ['right', 'up'],
                smooth: this.smooth.enabled,
                clear: true
            })
        }

        // 抬头( ^  )
        if (this.hasKey(this.keyMap.turnUp)) {
            this.rotate({
                x: 0,
                y: radians(-25) * this.rotateSpeed,
                around: 'eye',
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.turnUp)) {
            this.removeKeyLog(this.keyMap.turnUp)
            this.rotate({
                x: 0,
                y: radians(-25) * this.rotateSpeed,
                around: 'eye',
                smooth: this.smooth.enabled,
                clear: true
            })
        }

        // 低头(. . )
        if (this.hasKey(this.keyMap.turnDown)) {
            this.rotate({
                x: 0,
                y: radians(25) * this.rotateSpeed,
                around: 'eye',
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.turnDown)) {
            this.removeKeyLog(this.keyMap.turnDown)
            this.rotate({
                x: 0,
                y: radians(25) * this.rotateSpeed,
                around: 'eye',
                smooth: this.smooth.enabled,
                clear: true
            })
        }

        // 左转
        if (this.hasKey(this.keyMap.turnLeft)) {
            this.rotate({
                x: radians(-25) * this.rotateSpeed,
                y: 0,
                around: 'eye',
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.turnLeft)) {
            this.removeKeyLog(this.keyMap.turnLeft)
            this.rotate({
                x: radians(-25) * this.rotateSpeed,
                y: 0,
                around: 'eye',
                smooth: this.smooth.enabled,
                clear: true
            })
        }

        // 右转
        if (this.hasKey(this.keyMap.turnRight)) {
            this.rotate({
                x: radians(25) * this.rotateSpeed,
                y: 0,
                around: 'eye',
                clear: true
            })
        }
        else if (this.hasKeyLog(this.keyMap.turnRight)) {
            this.removeKeyLog(this.keyMap.turnRight)
            this.rotate({
                x: radians(25) * this.rotateSpeed,
                y: 0,
                around: 'eye',
                smooth: this.smooth.enabled,
                clear: true
            })
        }

        requestAnimationFrame(this.frame.bind(this))
    }
    /**
     * 限制旋转角度
     * @param {*} y 旋转角度
     * @param {*} max 最大角度
     * @returns 
     */
    limitRotation(y, max) {
        let b = max - Math.PI // 补角
        if (y >= max) {
            y = max > radians(0.1) ? max - radians(0.1) : 0
        }
        else if (y <= b) {
            y = b < -radians(0.1) ? b + radians(0.1) : 0
        }
        return y
    }
    rotate({
        eye = this.getEye(), // start eye
        center = this.getCenter(), // start center
        x = 0, // x radians around up
        y = 0, // y radians toward up
        around, // point that rotate around
        speed = 1,
        smooth = false,
        clear = false,
        smoothOnly = false
    } = {}) {
        return new Promise(resolve => {
            // 清除缓动
            if (clear) {
                if (this.rotateSmoothAnim) {
                    this.rotateSmoothAnim.stop()
                    this.rotateSmoothAnim = null
                    // console.log('clear rotateSmoothAnim')
                }
            }
            if (!smoothOnly) {
                if (x || y) {
                    if (around === 'center') {
                        around = center
                    } else if (around === 'eye') {
                        around = eye
                    }
                    let up = [0, 1, 0],
                        view = vector3.sub(eye, center),
                        left = vector3.cross(view, up),
                        m1 = matrix4.fromRotation(up, x * speed),
                        m2 = matrix4.fromRotation(left, this.limitRotation(y * speed, vector3.angle(view, up))),
                        m = matrix4.multiply(m1, m2),
                        eye_around = vector3.sub(eye, around),
                        center_around = vector3.sub(center, around)

                    eye_around = vector3.transformMat4(eye_around, m)
                    center_around = vector3.transformMat4(center_around, m)

                    view = vector3.sub(eye_around, center_around)
                    let a = vector3.angle(view, up)
                    if (a === 0 || a === Math.PI) return
                    eye = vector3.add(around, eye_around)
                    center = vector3.add(around, center_around)

                    if (this.isFunction(this.beforeRotate)) {
                        let result = this.beforeRotate(eye, center)
                        if (this.isObject(result)) {
                            const { eye, center } = result
                            this.setEye(eye)
                            this.setCenter(center)
                        }
                    } else {
                        this.setEye(eye)
                        this.setCenter(center)
                    }
                }
            }

            if (smooth) {
                const time = 1000 / 60,
                    vx = x / time, // 除以time只是为了获得即时初速度
                    vy = y / time,
                    v = Math.hypot(vx, vy),
                    duration = this.smooth.duration ?? v / (this.smooth.friction / 1000),
                    ax = -vx / duration,
                    ay = -vy / duration

                this.rotateSmoothAnim = new Animation({
                    duration,
                    bezier: v => v,
                    action: percent => {
                        const t = duration * percent
                        this.rotate({
                            eye,
                            center,
                            x: vx * t + 0.5 * ax * t * t,
                            y: vy * t + 0.5 * ay * t * t,
                            speed,
                            around,
                        })
                    },
                    onFinish: () => {
                        resolve()
                    }
                })
            }
        })
    }
    move({
        eye = this.getEye(), //start eye
        center = this.getCenter(), // start center
        x, // x轴移动距离
        y, // y轴移动距离
        coords, // 平面坐标系
        smooth = false,
        clear = false,
        smoothOnly = false
    } = {}) {
        return new Promise(resolve => {
            // 清除缓动
            if (clear) {
                if (this.moveSmoothAnim) {
                    this.moveSmoothAnim.stop()
                    this.moveSmoothAnim = null
                    // console.log('clear moveSmoothAnim')
                }
            }
            if (!smoothOnly) {
                if (x || y) {
                    if (coords) {
                        // console.log(x, y, speed)
                        let view = vector3.sub(eye, center),
                            up = [0, 1, 0],
                            a = vector3.angle(view, up),
                            dirX = vector3.scale(this.getDirection(coords[0]), x),
                            dirY = vector3.scale(this.getDirection(coords[1]), (a <= Math.PI / 2) ? y : -y),
                            dir = vector3.add(dirX, dirY)
                        eye = vector3.add(eye, dir)
                        center = vector3.add(center, dir)
                        if (this.isFunction(this.beforeMove)) {
                            let result = this.beforeMove(eye, center)
                            if (this.isObject(result)) {
                                const { eye, center } = result
                                this.setEye(eye)
                                this.setCenter(center)
                            }
                        } else {
                            this.setEye(eye)
                            this.setCenter(center)
                        }
                    }
                }
            }

            if (smooth) {
                const time = 1000 / 60,
                    vx = x / time, // 除以time只是为了获得即时初速度
                    vy = y / time,
                    v = Math.hypot(vx, vy),
                    duration = this.smooth.duration ?? v / (this.smooth.friction / 1000),
                    ax = -vx / duration,
                    ay = -vy / duration
                // console.log(duration)
                // console.log({ x, y, vx, vy, time })
                this.moveSmoothAnim = new Animation({
                    duration,
                    bezier: v => v, // 必须是linear
                    action: percent => {
                        let t = duration * percent
                        this.move({
                            eye,
                            center,
                            x: vx * t + 0.5 * ax * t * t,
                            y: vy * t + 0.5 * ay * t * t,
                            coords,
                        })
                    },
                    onFinish: () => {
                        resolve()
                    }
                })
            }
        })

    }
    scale({
        eye = this.getEye(), //start eye
        center = this.getCenter(), // start center
        value = 0,
        type = 'percent', // percent or fixed
        smooth = false,
        duration = 250,
        bezier = e => e,
        inertia = false,
    } = {}) {
        if (smooth) {
            this.scaleSmoothAnim = new Animation({
                duration,
                bezier,
                action: percent => {
                    // console.log(percent)
                    this.scale({
                        eye,
                        center,
                        value: value * percent,
                        type
                    })
                },
                onFinish: () => {
                    this.scaleSmoothAnim = null
                    // console.log(this.getEye())
                    if (inertia) {
                        const eye = this.getEye(),
                            center = this.getCenter(),
                            v = value / 250,
                            d = 500,
                            a = -v / d
                        // console.log(v, a)
                        this.scaleInertiaAnim = new Animation({
                            duration: d,
                            bezier: e => e,
                            action: percent => {
                                // console.log(percent)
                                let t = d * percent
                                this.scale({
                                    eye,
                                    center,
                                    value: v * t + 0.5 * a * t * t,
                                    type: this.scaleType
                                })
                            },
                            onFinish: () => {
                                this.scaleInertiaAnim = null
                            }
                        })
                    }
                }
            })
        } else {
            let view = vector3.sub(eye, center),
                len = vector3.len(view),
                times = type === 'percent' ? (value + 1) : (value / len + 1)
            if (times <= 0) return
            eye = vector3.scaleAndAdd(center, view, times)
            if (this.isFunction(this.beforeScale)) {
                let result = this.beforeScale(eye, center)
                if (this.isObject(result)) {
                    const { eye, center } = result
                    this.setEye(eye)
                }
            } else {
                this.setEye(eye)
            }
        }
    }
    activate() {
        this.active = true
    }
    deactive() {
        this.active = false
    }
    setView({
        eye = this.originalEye,
        center = this.originalCenter,
        duration = 100,
        bezier = Easing.easeOut
    } = {}) {
        if (this.viewChanging) return
        this.viewChanging = true
        let currentEye = this.getEye(),
            currentCenter = this.getCenter()
        new Animation({
            duration,
            bezier,
            action: p => {
                if (Array.isArray(eye)) {
                    this.setEye(eye.map((e, i) => mix(currentEye[i], e, p)))
                }
                if (Array.isArray(center)) {
                    this.setCenter(center.map((e, i) => mix(currentCenter[i], e, p)))
                }
            },
            onFinish: () => {
                this.viewChanging = false
            }
        })
    }
    isObject(value) {
        if (value == undefined) return false
        return typeof value === 'object' && value instanceof Object
    }
    isFunction(value) {
        if (value == undefined) return false
        return typeof value === 'function'
    }
    isString(value) {
        if (value == undefined) return false
        return typeof value === 'string'
    }
    isFloat32Array(value) {
        if (value == undefined) return false
        return value instanceof Float32Array
    }
    getDirection(value) {
        let dir = value
        if (this.isString(value)) {
            switch (value) {
                case 'up':
                    dir = this.getUp()
                    break
                case 'down':
                    dir = this.getDown()
                    break
                case 'view':
                    dir = this.getView()
                    break
                case 'ray':
                    dir = this.getRay()
                    break
                case 'left':
                    dir = this.getLeft()
                    break
                case 'right':
                    dir = this.getRight()
                    break
                case 'cameraUp':
                    dir = this.getCameraUp()
                    break
                case 'cameraDown':
                    dir = this.getCameraDown()
                    break
                case 'horizonFront':
                    dir = this.getHorizonFront()
                    break
                case 'horizonBack':
                    dir = this.getHorizonBack()
                    break
            }
        }
        return vector3.normalize(dir)
    }
    getUp() {
        return [0, 1, 0]
    }
    getDown() {
        return [0, -1, 0]
    }
    getView() {
        const eye = this.getEye(),
            center = this.getCenter()
        return vector3.sub(eye, center)
    }
    getRay() {
        const eye = this.getEye(),
            center = this.getCenter()
        return vector3.sub(center, eye)
    }
    getLeft() {
        let view = this.getView(),
            up = vector3.create(0, 1, 0)
        return vector3.cross(view, up)
    }
    getRight() {
        let view = this.getView(),
            up = vector3.create(0, 1, 0)
        return vector3.cross(up, view)
    }
    getCameraUp() {
        let view = this.getView(),
            left = this.getLeft()
        return vector3.cross(left, view)
    }
    getCameraDown() {
        let view = this.getView(),
            left = this.getLeft()
        return vector3.cross(view, left)
    }
    getHorizonFront() {
        let left = this.getLeft(),
            up = [0, 1, 0]
        return vector3.cross(left, up)
    }
    getHorizonBack() {
        let left = this.getLeft(),
            up = [0, 1, 0]
        return vector3.cross(up, left)
    }
}