import util from "./util"
// 场景类，实例化后，每个场景实例会有
// vertexBuffer         顶点缓冲区
// indexBuffer          索引缓冲区
// vertexBufferLayout   顶点缓冲区数据布局
// triangleBuffer       三角面缓冲区
// instances            模型实例
// triangles            全部模型三角面
export default class Scene {
    constructor({
        device,
        light = [1, 2, 10],
        eye = [15, 15, 15],
        // center = [8, 0, 0],
        // eye = [0, 30, 0.1],
        // center = [0, 0, 0],
        // eye = [0, 10, 10], // 测试相机最短视距Math.hypot(1.13,1.13,1.13)
        center = [0, 0, 0],
        up = [0, 1, 0],
        fovy = 45,
        bgColor = [20, 20, 20, 1],
        topology = 'triangle-list',
        frontFace = 'ccw', // defaulting to ccw 逆时针
        cullMode = 'none' // 三角形前后面都会显示
    } = {}) {
        this.device = device
        this.eye = eye
        this.center = center
        this.up = up
        this.fovy = fovy
        this.bgColor = bgColor
        this.topology = topology
        this.frontFace = frontFace
        this.cullMode = cullMode
        this.light = light
        this.lightCenter = [0, 4.95, 0]
        this.lightWidth = 2
        this.lightHeight = 2
        this.lightingModel = 'Blinn-Phong'
        // this.lightingModel = 'Ray-Tracing'
        this.init()
    }
    init() {

        // 1.模型实例，按透明度排序：不透明在前，透明在后
        // 每个实例中包含anchor、position、rotation、scale、
        // color、emissive、mesh等属性，最重要的是mesh(模型网状结构)
        // mesh由positions（点的三维坐标二维数组）、normals（法线的二维数组）、
        // uvs（UV坐标的二维数组）、triangles（每个模型三角面的顶点索引组成的二维数组）组成，
        // positions、normals、uvs的数量是对应的
        this.instances = [
            // {
            //     name: 'light',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [5, 5, 5, 1],
            //     emissive: 1,
            //     mesh: util.createRectangleZX(2, 4.95, 2, true),
            // },
            // {
            //     name: 'box-front',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [0.95, 0.95, 0.95, 1],
            //     emissive: 0,
            //     mesh: util.createRectangleXY(10, 10, -5),
            // },
            // {
            //     name: 'box-back',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 1, 1, 1],
            //     emissive: 0,
            //     mesh: util.createRectangleXY(10, 10, 5, true),
            // },
            // {
            //     name: 'box-left',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [0.9, 0.9, 0.9, 1],
            //     emissive: 0,
            //     mesh: util.createRectangleYZ(-5, 10, 10),
            // },
            // {
            //     name: 'box-right',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 1, 1, 1],
            //     emissive: 0,
            //     mesh: util.createRectangleYZ(5, 10, 10, true),
            // },
            // {
            //     name: 'box-top',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 1, 1, 1],
            //     emissive: 0,
            //     mesh: util.createRectangleZX(10, 5, 10, true),
            // },
            {
                name: 'box-bottom',
                anchor: [0, 0, 0],
                position: [0, -1, 0],
                rotation: [0, 0, 0],
                scale: [1, 1, 1],
                color: [1, 1, 1, 0],
                emissive: 0,
                mesh: util.createRectangleZX(10, 0, 10),
            },
            // {
            //     name: 'cube-l',
            //     anchor: [0, 0, 0],
            //     position: [-1.5, -2, -1.5],
            //     rotation: [0, 15, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 0, 1, 0.3],
            //     emissive: 0,
            //     mesh: util.createCube(3, 6, 3, true),
            // },
            {
                name: 'cube-s',
                anchor: [0, 0, 0],
                //position: [1.5, -3.5, 1.5],
                position: [0, 0.01, 0],
                rotation: [0, 0, 0],
                scale: [1, 1, 1],
                color: [0.4, 0.4, 0.4, 0], // apple green
                // color: [0.5, 0.8, 0.2, 1], // apple green
                // color: [125 / 255, 83 / 255, 50 / 255, 1], // chocolate
                emissive: 0,
                mesh: util.createCube(2, 2, 2),
            },
            // {
            //     name: 'front',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 0, 0, 0.5],
            //     emissive: 0,
            //     mesh: util.createRectangleXY(3, 3, -1.5),
            // },
            // {
            //     name: 'back',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 0, 0, 0.5],
            //     emissive: 0,
            //     mesh: util.createRectangleXY(3, 3, 1.5, true),
            // },
            // {
            //     name: 'left',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 0, 0, 0.5],
            //     emissive: 0,
            //     mesh: util.createRectangleYZ(-1.5, 3, 3),
            // },
            // {
            //     name: 'right',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 0, 0, 0.5],
            //     emissive: 0,
            //     mesh: util.createRectangleYZ(1.5, 3, 3, true),
            // },
            // {
            //     name: 'bottom',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 0, 0, 1],
            //     emissive: 0,
            //     mesh: util.createRectangleZX(3, -1.5, 3),
            // },
            // {
            //     name: 'top',
            //     anchor: [0, 0, 0],
            //     position: [0, 0, 0],
            //     rotation: [0, 0, 0],
            //     scale: [1, 1, 1],
            //     color: [1, 0, 0, 0.5],
            //     emissive: 0,
            //     mesh: util.createRectangleZX(3, 1.5, 3, true),
            // },
        ].sort((a, b) => Math.sign(1 - a.color[3]) - Math.sign(1 - b.color[3]))

        // 2.顶点缓冲区数据布局，一般在createRenderPipelineAsync创建渲染管线的时候作为参数使用
        this.vertexBufferLayout = [
            {
                arrayStride: (3 + 3 + 2 + 4 + 1 + 1) * 4, // * 4 这里的4是4字节的意思
                attributes: [
                    { shaderLocation: 0, offset: 0 * 4, format: 'float32x3' },  // 3 position
                    { shaderLocation: 1, offset: 3 * 4, format: 'float32x3' },  // 3 normal
                    { shaderLocation: 2, offset: 6 * 4, format: 'float32x2' },  // 2 uv
                    { shaderLocation: 3, offset: 8 * 4, format: 'float32x4' },  // 4 color
                    { shaderLocation: 4, offset: 12 * 4, format: 'float32' },   // 1 emissive
                    { shaderLocation: 5, offset: 13 * 4, format: 'float32' },   // 1 triangle index
                ]
            }
        ]

        // 3.根据模型实例创建对应的 [顶点缓冲区] 和 [索引缓冲区]
        this.instances.forEach(instance => {
            this.triangleIndex ??= -1
            const { color, emissive, mesh } = instance,
                { positions, uvs, normals, triangles } = mesh,
                vertice = triangles.map(indice => {
                    this.triangleIndex += 1
                    return indice.map(i => {
                        return [
                            ...positions[i],        // 3 position
                            ...normals[i],          // 3 normal
                            ...uvs[i],              // 2 uv
                            ...color,               // 4 color
                            emissive,               // 1 emissive
                            this.triangleIndex      // 1 triangle index
                        ]
                    }).flat()
                }).flat(),
                vertexData = new Float32Array(vertice),
                indice = triangles.map((indice, i) => {
                    return [
                        i * 3,
                        i * 3 + 1,
                        i * 3 + 2
                    ]
                }).flat(),
                indexData = new Uint32Array(indice)
            // 打印原始数据
            // console.log(`${instance.name} vertice:`, vertice)
            // console.log(`${instance.name} indice:`, indice)
            instance.vertexBuffer = this.createMappedBuffer({
                size: vertexData.byteLength,
                usage: GPUBufferUsage.VERTEX,
                source: vertexData,
                typedArray: Float32Array
            })
            instance.indexBuffer = this.createMappedBuffer({
                size: indexData.byteLength,
                usage: GPUBufferUsage.INDEX,
                source: indexData,
                typedArray: Uint32Array // ---- 由Unit16改为Unit32是因为数据量的关系，Unit16容量太小
            })
        })

        // 4.获取场景全部三角面数据，然后创建场景三角面缓冲区，在光线追踪渲染模型中会用到，作为绑定资源组
        this.triangles = this.instances.map((instance, instanceIndex) => {
            const { color, mesh } = instance,
                positions = mesh.positions
            return mesh.triangles.map(indice => {
                const [ia, ib, ic] = indice
                return {
                    a: positions[ia],
                    b: positions[ib],
                    c: positions[ic],
                    color,
                    instanceIndex
                }
            })
        }).flat()
        // console.log(this.triangles)
        const trianglestride = 20
        const triangleData = new Float32Array(this.triangles.length * trianglestride)
        this.triangles.forEach((e, i) => {
            const { a, b, c, color, instanceIndex } = e,
                offset = trianglestride * i
            triangleData.set([
                ...a, 1,                    // a position
                ...b, 1,                    // b position
                ...c, 1,                    // c position
                ...color,                   // color 
                instanceIndex               // instanceIndex
            ], offset)
        })
        this.triangleBuffer = this.createMappedBuffer({
            size: triangleData.byteLength,
            usage: GPUBufferUsage.STORAGE,
            source: triangleData,
            typedArray: Float32Array
        })
    }
    createMappedBuffer({
        size = 0,
        usage = GPUBufferUsage.VERTEX,
        source,
        typedArray = Float32Array
    } = {}) {
        const GPUbuffer = this.device.createBuffer({ size, usage, mappedAtCreation: true })
        new typedArray(GPUbuffer.getMappedRange()).set(source)
        GPUbuffer.unmap()
        return GPUbuffer
    }
    setCenter(center) {
        let out = new Array(3)
        if (util.isArray(center) || util.isTypedArray(center)) {
            if (center.some(e => Number.isNaN(e))) {
                //util.print('error', `参数 [center] 不是数字类型`, { color: 'yellowgreen' })
                console.error(`参数 [center] 不是数字类型 : `, center)
                return
            } else {
                out[0] = center[0]
                out[1] = center[1]
                out[2] = center[2]
            }
        }
        else if (util.isObject(center)) {
            this.setCenter(Object.values(center))
        }
        else {
            //util.print('error', `参数 [center] 需要是数组或者对象，这里是一个${typeof center}`, { color: 'red' })
            console.error(`参数 [center] 需要是数组或者对象，这里是一个${typeof center} : `, center)
            return
        }
        this.center = out
    }
    setEye(eye) {
        let out = new Array(3)
        if (util.isArray(eye) || util.isTypedArray(eye)) {
            if (eye.some(e => Number.isNaN(e))) {
                //util.print('error', `参数 [eye] 不是数字类型`, { color: 'yellowgreen' })
                console.error(`参数 [eye] 不是数字类型 : `, eye)
                return
            } else {
                out[0] = eye[0]
                out[1] = eye[1]
                out[2] = eye[2]
            }
        }
        else if (util.isObject(eye)) {
            this.setEye(Object.values(eye))
        }
        else {
            //util.print('error', `参数 [eye] 需要是数组或者对象，这里是一个${typeof eye}`, { color: 'red' })
            console.error(`参数 [eye] 需要是数组或者对象，这里是一个${typeof eye} : `, eye)
            return
        }
        this.eye = out
    }
    getCenter() {
        return this.center
    }
    getEye() {
        return this.eye
    }
    $(selector) {
        if (!selector) return
        if (selector.charAt(0) === '#' && selector.length > 1) { // tag
            let label = selector.slice(1)
            let data = this.instances.find(e => e.label == label)
            return data
        } else { // name
            let data = this.instances.filter(e => e.name === selector)
            return data
        }
    }
}